import React, { Component } from 'react';
import { Row, Col } from 'antd';
import SuggestionRate from '../card/CardTitelAndRate';

export default class Suggestion extends Component {
	render() {
		return (
			<div style={{ padding: '0px 5.6%', marginTop: '28px' }}>
				<Row>
					<Col span={8} style={{ textAlign: 'left' }}>
						{this.props.data.items.length > 4 ? <a className="seeMoreMobileView">مشاهده همه</a> : null}
					</Col>
					<Col span={16} style={{ textAlign: 'right' }}>
						<span className="titleSuggestionMobileView">مکان های مشابه</span>
					</Col>
				</Row>

				<Row style={{ overflow: 'auto', whiteSpace: 'nowrap', direction: 'rtl' }}>
					{this.props.data.items.length === 1 ? (
						<Row style={{ width: '200px', height: '100px' }}>
							{this.props.data.items.slice(0, 4).map((data) => (
								<Col span={15} style={{ width: '100%', padding: '10px' }}>
									<SuggestionRate data={data} />
								</Col>
							))}
						</Row>
					) : null}
					{this.props.data.items.length === 2 ? (
						<Row style={{ width: '400px', height: '100px' }}>
							{this.props.data.items.slice(0, 4).map((data) => (
								<Col span={15} style={{ width: '50%', padding: '10px' }}>
									<SuggestionRate data={data} />
								</Col>
							))}
						</Row>
					) : null}
					{this.props.data.items.length === 3 ? (
						<Row style={{ width: '600px', height: '100px' }}>
							{this.props.data.items.slice(0, 4).map((data) => (
								<Col span={15} style={{ width: '33.333333%', padding: '10px' }}>
									<SuggestionRate data={data} />
								</Col>
							))}
						</Row>
					) : null}
					{this.props.data.items.length > 4 ? (
						<Row style={{ width: '800px', height: '100px' }}>
							{this.props.data.items.slice(0, 4).map((data) => (
								<Col span={15} style={{ width: '25%', padding: '10px' }}>
									<SuggestionRate data={data} />
								</Col>
							))}
						</Row>
					) : null}
				</Row>
			</div>
		);
	}
}
