import React, { Component } from 'react';
import { Row, Col } from 'antd';
import 'antd/dist/antd.css';
import './../../App.css';
import 'antd/dist/antd.css';

// import ReactDOM from 'react-dom';
import 'react-responsive-carousel/lib/styles/carousel.min.css';
// import { Carousel } from 'react-responsive-carousel';

// bara in zade shode react bnbgalary

import ReactBnbGallery from 'react-bnb-gallery';

// bara in zade shode react bnbgalary

// import kebab1 from './../../assets/kebab1.jpg';
// import kebab2 from './../../assets/kebab2.jpg';

export default class Images extends Component {
	constructor(props) {
		super(props);
		this.searchAllEnabled = false;
		this.state = {
			modalVisible: false,
			galleryOpened: false,
			// ali : [],
			photoForReactBnbGallery: [],
			showPhotoBnbGallery: 0
		};
	}

	// bara in zade shode react bnbgalary

	toggleGallery = () => {
		this.setState((prevState) => ({
			galleryOpened: !prevState.galleryOpened
		}));
	};

	// bara in zade shode react bnbgalary
	PictureModal = (number) => {
		// let picture = {
		// 	photo: `/files/justro.png?mode=view&fcode=${number}`,
		// 	caption: 'Woman smoking a tobacco',
		// 	subcaption: 'Photo by Hannah Cauhepe on Unsplash',
		// 	thumbnail: 'https://source.unsplash.com/QdBHnkBdu4g/100x67'
		// };
	};
	// bara in zade shode react bnbgalary

	showModal = () => {
		this.setState({
			modalVisible: true
		});
	};
	handleOk = (e) => {
		this.setState({
			modalVisible: false
		});
	};
	handleCancel = (e) => {

		this.setState({
			modalVisible: false
		});
	};
	componentDidMount = () => {
		this.fillPhoto(this.props.data);
	};

	// fuction por kardan photo bara Image
	fillPhoto = (image) => {
		let counter = 0;

		if (image.length !== 0) {
			const tempImage = [];
			for (counter; counter < image.images.length; counter++) {
				let fillphoto = {
					photo: `/files/justro.png?mode=view&fcode=${image.images[counter]}`,
					caption: ' ',
					subcaption: ' ',
					thumbnail: `/files/justro.png?mode=view&fcode=${image.images[counter]}`
				};
				tempImage.push(fillphoto);
			}
			this.setState({
				photoForReactBnbGallery: tempImage
			});
		}
	};
	showPhotoToggleGallery = (e) => {
		this.setState({
			showPhotoBnbGallery: e
		});
		this.toggleGallery();
	};
	render() {
		return (
			<div>
				{this.props.data === undefined ? null : (
					<div>
						<Row className="restaurantImages">
							{this.props.data.images.length === 1 ? (
								<div className="ImageThingsTodoDetailOnePage">
									<Col
										onClick={this.showPhotoToggleGallery}
										span={12}
										className="ImageOneThingsTodoDetailOnePage "
										style={{
											background: `URL(/files/justro.png?mode=view&fcode=${this.props.data
												.images[0]})`
										}}
									/>
								</div>
							) : null}

							{this.props.data.images.length === 2 ? (
								<div className="ImageThingsTodoDetailTwoPage">
									<Col
										onClick={this.showPhotoToggleGallery}
										span={14}
										className="ImageTwoThingsTodoDetailOnePage"
										style={{
											background: `URL(/files/justro.png?mode=view&fcode=${this.props.data
												.images[0]})`
										}}
									/>

									<Col
										onClick={this.showPhotoToggleGallery}
										span={10}
										className="ImageTwoThingsTodoDetailOnePage"
										style={{
											background: `URL(/files/justro.png?mode=view&fcode=${this.props.data
												.images[1]})`,
											marginLeft: '1%'
										}}
									/>
								</div>
							) : null}
							{/* *********************************** length 3 ***************************** */}
							{this.props.data.images.length === 3 ? (
								<Row>
									<Col span={12} style={{ height: '320px' }}>
										<Row onClick={this.showPhotoToggleGallery} style={{ height: '160px' }}>
											<div
												className="PhotoInJustRo"
												style={{
													background: `URL(/files/justro.png?mode=view&fcode=${this.props.data
														.images[2]})`,
													borderBottom: '4px solid white'
												}}
											/>
										</Row>
										<Row onClick={this.showPhotoToggleGallery} style={{ height: '160px' }}>
											<div
												className="PhotoInJustRo"
												style={{
													background: `URL(/files/justro.png?mode=view&fcode=${this.props.data
														.images[1]})`,
													borderTop: '4px solid white'
												}}
											/>
										</Row>
									</Col>

									<Col onClick={this.showPhotoToggleGallery} span={12} style={{ height: '320px' }}>
										<div
											className="PhotoInJustRo"
											style={{
												background: `URL(/files/justro.png?mode=view&fcode=${this.props.data
													.images[0]})`,
												borderLeft: '8px solid white'
											}}
										/>
									</Col>
								</Row>
							) : null}

							{/* *********************************** length 4 ***************************** */}
							{this.props.data.images.length >= 4 ? (
								<Row>
									<Col span={12} style={{ height: '320px' }}>
										<Row style={{ height: '160px' }}>
											<div
												data-tag={1}
												onClick={this.showPhotoToggleGallery.bind(null, 1)}
												className="PhotoInJustRo "
												style={{
													background: `URL(/files/justro.png?mode=view&fcode=${this.props.data
														.images[1]})`,
													borderBottom: '4px solid white'
												}}
											/>
											{this.props.data.images.length > 4 ? (
												<a className="image-hero-textThings" onClick={this.toggleGallery}>
													({this.props.data.images.length}) مشاهده تصاویر{' '}
													<img
														height="18px"
														width="18px"
														src="data:image/png;base64,"
													/>
												</a>
											) : null}
										</Row>

										<Row style={{ height: '160px' }}>
											<Col span={12} style={{ height: '160px' }}>
												<div
													data-tag={2}
													onClick={this.showPhotoToggleGallery.bind(null, 2)}
													className="PhotoInJustRo "
													style={{
														background: `URL(/files/justro.png?mode=view&fcode=${this.props
															.data.images[2]})`,
														borderTop: '4px solid white',
														borderRight: '4px solid white'
													}}
												/>
											</Col>
											<Col span={12} style={{ height: '160px' }}>
												<div
													data-tag={3}
													onClick={this.showPhotoToggleGallery.bind(null, 3)}
													className="PhotoInJustRo "
													style={{
														background: `URL(/files/justro.png?mode=view&fcode=${this.props
															.data.images[3]})`,
														borderTop: '4px solid white',
														borderLeft: '4px solid white'
													}}
												/>
											</Col>
										</Row>
									</Col>

									<Col span={12} style={{ height: '320px' }}>
										<div
											data-tag={0}
											onClick={this.showPhotoToggleGallery.bind(null, 0)}
											className="PhotoInJustRo maskHeroThree"
											style={{
												background: `URL(/files/justro.png?mode=view&fcode=${this.props.data
													.images[0]})`,
												borderLeft: '8px solid white'
											}}
										/>
									</Col>
								</Row>
							) : null}
						</Row>

						{/*  bara in zade shode react bnbgalary */}

						<ReactBnbGallery
							thumbnail="پنهان کردن"
							activePhotoIndex={this.state.showPhotoBnbGallery}
							show={this.state.galleryOpened}
							photos={this.state.photoForReactBnbGallery}
							onClose={this.toggleGallery}
						/>
						{/* <ImageGallery items={this.state.photoForReactBnbGallery} /> */}

						{/*  bara in zade shode react bnbgalary */}
					</div>
				)}
			</div>
		);
	}
}
