import { PLACE_DETAIL } from './types';

export const placeDetail = (detail) => (dispatch) => {
	fetch(`/api/layerId/Point?uid=${detail}`).then((res) => res.json())
	.then((posts) => {
		let temp = {
			address: posts.address,
			city: posts.city,
			cityUid: posts.cityUid,
			description: posts.description,
			images: "images" in posts ? posts.images : [] ,
			nearbies: posts.nearbies,
			point: posts.point,
			province: posts.province,
			rate: posts.rate,
			similarPlaces: posts.similarPlaces,
			tags: posts.tags,
			telephoneNum: posts.telephoneNum === 'null' ? '' : posts.telephoneNum ,
			title: posts.title,
			type: posts.type,
			uid: posts.uid,
			website: posts.website === "null" ? '' : posts.website ,

		}
		if("images" in posts){
			console.log("field is not define")
		}
		if(posts.website === "null"){
			console.log("website is null")
		}
		if(posts.telephoneNum === 'null'){
			console.log("telephoneNum is null")
		}
		dispatch({
			type: PLACE_DETAIL,
			payload: temp
		});
	});
};