import React, { Component } from 'react';
import { Row } from 'antd';
import { Menu, Dropdown, Icon, Checkbox } from 'antd';
import Down from '../../../assets/IconDownGray.png';
import { constants } from 'fs';

var a = [];

export default class CategoryTags extends Component {
	constructor(props) {
		super(props);
		this.state = {
			disable: false,
			tags: [],
			tagsProps: []
		};
	}
	filter = (event) => {
		this.fineNameForChangeCheched(event.name);
		let temp = this.state.tags;
		// temp.name= event.name;
		temp.push(event.name);
		this.setState({
			disable: false,
			tags: temp
		});

		this.props.updataTags(temp);
	};
	fineNameForChangeCheched = (name) => {
		for (let i = 0; i < a.length; i++) {
			if (name === a[i].name) {
				a[i].checked = true;
			}
		}
	};
	showDropDown = () => {
		this.setState({
			disable: true,
			propstags: []
		});
	};
	componentDidMount = () => {
		for (let i = 0; i < this.props.tags.length; i++) {
			let t = {
				name: this.props.tags[i].name,
				checked: false
			};
			a.push(t);
		}
	};

	render() {
		return (
			<Dropdown
				overlayStyle={{ width: '80%' }}
				overlayClassName="categoryTags"
				overlay={
					<div style={{ width: '100%' }}>
						{this.props.tags.length > 0 ? (
							<div className="divIconDownFillterPlaceMobileView rtl">
								{a.map((data) => (
									<Row className="dropDrownCategoryTags">
										<Row style={{ margin: '2% 4%' }}>
											<Checkbox
												className="shabnam"
												name={data.name}
												onChange={this.filter.bind(null, data)}
												checked={data.checked}
											>
												{data.name}{' '}
											</Checkbox>
										</Row>
									</Row>
								))}
							</div>
						) : null}
					</div>
				}
				trigger={[ 'click' ]}
			>
				<Row className="divDropDownModalTagsPlaceListMobileView shabnam" onClick={this.showDropDown}>
					<img src={Down} className="IconDownFillterPlaceMobileView" />
					<span>دسته بندی :</span>
					<span>
						{' '}
						{this.props.selectedTags === undefined ||  this.props.selectedTags.length === 0 ? (
							'همه'
						) : (
							<span>{this.props.selectedTags.map((data,i) => <span> {data} </span>)}</span>
						)}{' '}
					</span>
				</Row>
			</Dropdown>
		);
	}
}
