import React, { Component } from 'react';
import { Row } from 'antd';
import Close from '../../assets/closeGreen.png';
import IconSearch from '../../assets/cursor.svg';

// redux code
import { connect } from 'react-redux';
import { searchCity } from '../../actions/search/searchCity';

class ModalSearchCity extends Component {
	constructor(props) {
		super(props);
		this.state = {
			city: '',
			visible: false
		};
	}

	

	closeModal = () => {
		let modal = document.getElementById('ModalSearchCity');
		modal.style.display = 'none';
	};
	searchCity = (e) => {
		if (e.target.value.length > 1) {
			this.setState({
				city: e.target.value,
				visible: true
			});
			this.props.searchCity(e.target.value);
		} else {
			this.setState({
				city: e.target.value,
				visible: false
			});
		}
	};
	goToCity = (city)=>{
		window.location.href = `./?cityName=${city.name}&layer_uid=${city.layer_uid}`
	}
	getLocation = () =>{
		if (navigator.geolocation) {
			navigator.geolocation.getCurrentPosition(this.showPosition);
		  } else { 
			alert('این مورد در مرورگر شما پشتیبانی نمی شود');
		  }
	}

	showPosition = (position) =>{
		window.location.href = `./?Latitude=${position.coords.latitude}&Longitude=${position.coords.longitude}`
	}
	render() {
		return (
			<div id="ModalSearchCity" className="modalSearchCityMobileView">
				<Row className="titleModalSearchCityMobileView">
					<img onClick={this.closeModal} width="5.6%" style={{ verticalAlign: 'bottom' }} src={Close} />
					<input
						value={this.state.city}
						onChange={this.searchCity}
						placeholder="کجا هستی؟"
						className="inputModalSearchCityMobileView"
					/>
				</Row>
				{this.state.visible === false ? (
					<Row style={{ margin: '8% 0px' }} onClick = {this.getLocation}>
						<Row className="ta-r">
							<span >اطراف من</span>
							<img
								src={IconSearch}
								style={{
									width: '8%',
									marginLeft: '3%'
								}}
							/>
						</Row>
					</Row>
				) : null}
				{this.state.visible === true ? (
					<Row>
						{this.props.city.slice(0, 6).map((data) => (
							<Row style={{ margin: '3% 0px' }} onClick={this.goToCity.bind(null, data)}>
								<Row data-tag={data} className="ta-r nameModalSearchCity">{data.name}</Row>
								<Row className="ta-r parentModalSearchCity">{data.parent} , ایران</Row>
							</Row>
						))}
					</Row>
				) : null}
			</div>
		);
	}
}
// redux code
const mapStateToProps = (state) => ({
	city: state.posts.city
});
// redux code

export default connect(mapStateToProps, { searchCity })(ModalSearchCity); // redux code
