import React, { Component } from 'react';
import { Input, Row, Modal } from 'antd';
import getLocation from '../Functions/GetLocation';
import axios from 'axios';
import image from '../assets/arrow-down-sign-to-navigate.svg';
import IconSearch from '../assets/cursor.svg';
import closeCity from '../assets/close-city.svg';
import iconDownGray from '../assets/IconDownGray.png';

var url_string = window.location.href;
var url = new URL(url_string);
let Latitude = url.searchParams.get('Latitude');
let Longitude = url.searchParams.get('Longitude');
var bartarinShahr = [
	{
		name: 'شهر تهران',
		layer_uid: 'b91a9358-ef69-4596-a136-59789370e914',
		parent: 'استان تهران'
	},
	{
		name: 'شهر مشهد',
		layer_uid: 'bca28afc-d28b-4f67-80b1-0fb6e5ad79fc',
		parent: 'استان خراسان رضوی'
	},

	{
		name: 'شهر شیراز',
		layer_uid: '63a10d89-c78d-460b-a12b-7c78c6158666',
		parent: 'استان فارس'
	},

	{
		name: 'شهر اصفهان',
		layer_uid: 'be011126-f9a1-47a6-a351-3ffcbc447d44',
		parent: 'استان اصفهان'
	},
	{
		name: 'شهر کیش',
		layer_uid: '8a6e43a9-b18d-4195-be4e-0752e93fc66d',
		parent: 'استان هرمزگان'
	}
];
export default class searchMainCity extends Component {
	constructor(props) {
		super(props);
		this.state = {
			items: [],
			selectedCity: '',
			visible: false,
			displayAtrafeMn: 'block'
		};

		this.getCityDetail = this.getCityDetail.bind(this);
	}

	getCityDetail = (name, id) => {
		axios
			.get(`/api/${id}/city?cityName=${name}&type=hotel `)
			.then((res) => {})
			.catch(function(error) {
				console.log(error);
			})
			.then(function() {});
	};

	searchInputChange = (e) => {
		let inputName = e.target.name;
		this.setState({
			[inputName]: e.target.value
		});

		if (e.target.value.length > 1) {
			axios
				.get('/api/city/city-names?q=' + e.target.value)
				.then((res) => {
					this.setState({
						items: res.data,
						displayAtrafeMn: 'none'
					});
				})
				.catch(function(error) {
					console.log(error);
				})
				.then(function() {});
		} else {
			this.setState({
				items: [],
				displayAtrafeMn: 'block'
			});
		}
	};

	searchCity = (i, x) => {
		this.setState({ ['selectedCity']: i.name });
		this.getCityDetail(i.name, i.layer_uid);
	};

	// modal ***************************************************
	handleCancel = (e) => {
		this.setState({
			visible: false
		});
	};

	handleOk = (e) => {
		this.setState({
			visible: false
		});
	};

	showModal = () => {
		this.setState({
			visible: true
		});
	};

	getLocation = () => {
		// if (navigator.geolocation) {
		// 	navigator.geolocation.getCurrentPosition(this.showPosition);
		// } else {
		// 	alert('این مورد در مرورگر شما پشتیبانی نمی شود');
		// }
	};
	showPosition = (position) => {
		window.location.href = `./?Latitude=${position.coords.latitude}&Longitude=${position.coords.longitude}`;
	};

	// *********************************************************

	render() {
		return (
			<div>
				{/* button koja miri**************************************************** */}
				<Row style={{ display: 'flex' }}>
					{window.location.pathname === '/' ? (
						<button onClick={this.showModal} className="ButtonKojamiri">
							<img src={image} style={{ width: '16px', height: '16px', marginRight: '5%' }} />

							<span style={{ marginRight: '3px' }}>
								{' '}
								<b>{Latitude === null ? this.props.text : 'اطراف من'}</b>{' '}
							</span>
						</button>
					) : (
						<button onClick={this.showModal} className="ButtonKojamiriNotRoot">
							<img src={iconDownGray} className="imageKojamiriNotRoot" />

							<span style={{ marginRight: '3px' }}>
								{' '}
								<b>{this.props.text}</b>{' '}
							</span>
						</button>
					)}

					{window.location.pathname === '/' ? (
						<a href={`./`}>
							{this.props.text === 'کجا میری ؟' && Latitude === null ? null : (
								<img src={closeCity} className="imageKojamir" />
							)}
						</a>
					) : null}

					{/* {this.props.text === 'کجا میری ؟' ? null : (
						<a href={`./`} className="buttonCancleKojaMiri">
							<img src={closeCity} />
						</a>
					)} */}
				</Row>

				{/* ************************************************************ */}
				<Modal
					visible={this.state.visible}
					onOk={this.handleOk}
					onCancel={this.handleCancel}
					width={'35%'}
					className="ModalSearchMainCity"
				>
					<div className="InputModalSearchMainCity">
						<Input
							style={{
								background: 'white',
								borderBottom: '1px solid #e0e0e0'
							}}
							onKeyUp={this.searchInputChange.bind(this)}
							placeholder="جستجو ..."
							className="MainCitySearchInput"
						/>
					</div>

					<div>
						<div
							style={{
								display: this.state.displayAtrafeMn,
								padding: '10px 0px'
							}}
							className="searchAllResult"
						>
							<img
								src={IconSearch}
								style={{
									width: '20px',
									height: '20px',
									float: 'right',
									display: this.state.displayAtrafeMn
								}}
							/>

							<div
								onClick={this.getLocation}
								style={{ marginBottom: '10px' }}
								className="searchAllResultContent"
							>
								برترین شهرها
							</div>
							{bartarinShahr.map((item, index) => (
								<div onClick={this.searchCity.bind(index, item)} className="searchAllResult">
									<a href={`../?cityName=${item.name}&layer_uid=${item.layer_uid}`}>
										<div className="searchAllResultContent">
											<b>{item.name}</b>
										</div>

										<div className="searchAllResultContent searchAllResultContentSub">
											{item.parent}
											، ایران
										</div>
									</a>
								</div>
							))}
						</div>
						{this.state.items.map((item, index) => (
							<div onClick={this.searchCity.bind(index, item)} className="searchAllResult">
								<a href={`../?cityName=${item.name}&layer_uid=${item.layer_uid}`}>
									<div className="searchAllResultContent">
										<b>{item.name}</b>
									</div>

									<div className="searchAllResultContent searchAllResultContentSub">
										{item.parent}
										، ایران
									</div>
								</a>
							</div>
						))}

						{/* <div className="searchAllResult searchAllResultContent seeAllsearchAllResult">
							<a href={`./search`}>{`مشاهده تمامی پیشنهادها`} </a>
						</div> */}
					</div>
				</Modal>
			</div>
		);
	}
}
