import React, { Component } from 'react';
import { Row, Col } from 'antd';
import Rating from 'react-rating';
import DefaultRes from '../../assets/defultResturant.jpeg';
import DefaultThin from '../../assets/DefaultThingsToDo.jpeg';
export default class Places extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		return (
			<a
				href={
					this.props.data.type === 'city' ? (
						`./?cityName=${this.props.data.title}&layer_uid=${this.props.data.uid}`
					) : (
						`./${this.props.data.type}?uid=${this.props.data.uid}&city=${this.props.data.city}`
					)
				}
				target="blank"
			>
				<Row 
				className = "cardResultSearchPage"
				
				style={{ padding : "25px 30px 25px 30px" }}>
					<Col
						span={19}
						style={{ height: '150px', textAlign: 'right', paddingRight: '20px', paddingTop: '15px' }}
					>
						<Row className="TextInJustro shabnam rtl" style={{ fontSize: '16px', color: '#00a680' }}>
							{this.props.data.title}
						</Row>
						<Row
							className="TextInJustro shabnam rtl"
							style={{ fontSize: '12px', color: '#9e9e9e', marginBottom: '12px' }}
						>
							{this.props.data.province}
						</Row>

						<Row>
							<Col className="gutter-row flr" span={12}>
								<Rating 
								className="rating"
								type="custom" initialRating={this.props.data.rate / 2} readonly />
							</Col>
						</Row>
						<Row
							className="TextInJustro shabnam rtl"
							style={{ fontSize: '13px', color: '#333333', overflow: 'hidden', height: '36px' }}
						>
							{this.props.data.description}
						</Row>
					</Col>

					{'images' in this.props.data ? (
						<Col span={5}>
							<div>
								{this.props.data.type === 'things-to-do' || this.props.data.type === 'city' ? (
									<div
										style={{
											background: `URL(/files/justro.png?mode=view&fcode=${this
												.props.data.images[0]} )`,
											height: '150px'
										}}
										className="PhotoInJustRoCard"
									/>
								) : (
									<div
										style={{
											background: `URL(${this.props.data.images[0]})`,
											height: '150px'
										}}
										className="PhotoInJustRoCard"
									/>
								)}
							</div>
						</Col>
					) : (
						<Col span={5}>
							{this.props.data.type === 'things-to-do' || this.props.data.type === 'city' ? (
								<div
									style={{
										background: `URL(${DefaultThin})`,
										height: '150px'
									}}
									className="PhotoInJustRoCard"
								/>
							) : (
								<div
									style={{
										background: `URL(${DefaultRes})`,
										height: '150px'
									}}
									className="PhotoInJustRoCard"
								/>
							)}
						</Col>
					)}
				</Row>
			</a>
		);
	}
}
