import React, { Component } from "react";
import Header from "./Header";
import Suggestion from "./suggestions";
import "antd/dist/antd.css";
import "./../App.css";
import CitySuggestion from "./citySuggestion";
import FeaturesSection from "./FeaturesSection";
// import { func } from "prop-types";
import SearchByCity from "./SearchByCity";

export default class home extends Component {
  constructor() {
    super();
    this.searchAllEnabled = false;
    this.state = {
      passengers: {
        roomsCount: 1,
        adult: 2,
        childrenNo: 0,
        rooms: [
          {
            adult: 2,
            childrenNo: 0,
            children: []
          }
        ]
      }
    };
  }

  handleOutsideClick(e) {
    try {
      if (document.getElementsByClassName("mainPassenger")[0] !== "undefined") {
        if (
          document
            .getElementsByClassName("mainPassenger")[0]
            .contains(e.target) ||
          document
            .getElementsByClassName("searchBarContainer")[0]
            .contains(e.target)
        ) {
        } else {
          if (e.target.nodeName !== "LI") {
            document.getElementsByClassName("mainPassenger")[0].style.display =
              "none";
          }
        }
      }
    } catch (err) {}
  }

  // updateHomeTraveller = travellers => {
  //   this.setState({ ["passengers"]: travellers });
  // };

  render() {
    let arr = [
      [{ name: "ali" }, { name: "ali2" }, { name: "ali3" }],
      [{ name: "ali4" }, { name: "ali5" }, { name: "ali6" }],
      [{ name: "ali7" }, { name: "ali8" }, { name: "ali9" }]
    ];
    let suggestion=[]
    return (
      <div onClick={this.handleOutsideClick.bind(this)}>
        <Header updateHomeTraveller={this.updateHomeTraveller.bind(this)} />
        {/* <HeaderFlight/> */}
        <FeaturesSection />
        {suggestion.map(suggestion => (
          <Suggestion data={suggestion} />
        ))}
        <CitySuggestion />

        <SearchByCity />
        <div class="g-signin2" data-onsuccess="onSignIn" />
      </div>
    );
  }
}
