import React, { Component } from 'react';
import { Pagination, Tabs, Modal, Input, Menu, Dropdown, Icon, Radio } from 'antd';
import img from '../../assets/profile.png';
import Card from './CardProfile';
import Toolbar from '../ToolbarProfile.js';
import ModalEdit from '../ModalEditProfile.js';
// redux code
import { connect } from 'react-redux';
import { myReviews } from '../../actions/postActions';
import axios from 'axios';

const TabPane = Tabs.TabPane;

class CardProfiletwo extends Component {
	state = {
		current: 1,
		commentThings : [],
		comment : [ [] , [] ],
		visible: false,
		user: "NOTUSER"
	};
	// componentDidMount = () => {
	// 	this.props.myReviews();
	// 	this.props.checkLogin();
	// };


	componentDidMount = () => {
		this.checkLoginForStep();
		this.myReview();

		if (this.props.myreviews !== undefined) {
			for (let i = 0; i < this.props.myreviews.items.length; i++) {
				if(this.props.myreviews.items[i].place.type === "restaurant"){
					let temp = this.state.commentResturant;
					temp.push(this.props.myreviews.items[i])
					this.setState({
						commentResturant: temp
					})

				}else if(this.props.myreviews.items[i].place.type === "things-to-do"){
					let temp = this.state.commentThings;
					temp.push(this.props.myreviews.items[i])
					this.setState({
						commentThings: temp
					})

				}
			}
		}
	};

	checkLoginForStep = () => {
		axios
			.get(`/api/cu/v1/profile`)
			.then((res) => {
				this.setState({
					user: res.data
				})
			
			})
			.catch(function(error) {
				// this.setState({ existLogin: 'NOTHING' });
				console.log(error);
			})
			.then(function() {});
	};

	myReview = () => {
		axios
			.get(`/api/review/my-reviews-web`)
			.then((res) => {
				this.setState({
					commentThings: res.data
				})
				
				console.log("-------------------------->>>>>", res.data.items)
				for(let i = 0 ; i<res.data.items.length ; i++ ){
					if(res.data.items[i].place.type === "restaurant"){
						let temp = this.state.comment[0];
						temp.push(res.data.items[i])

					}else{
						let temp = this.state.comment[1];
						temp.push(res.data.items[i])

					}
					console.log("............", res.data.items[i].place.type )

				}
			
			
			})
			.catch(function(error) {
				// this.setState({ existLogin: 'NOTHING' });
				console.log(error);
			})
			.then(function() {});
	};



	handleCancel = (e) => {
		console.log(e);
		this.setState({
			visible: false
		});
	};

	ShowModal = () => {
		this.setState({
			visible: true
		});
	};

	onChange = (page) => {
		console.log(page);
		this.setState({
			current: page
		});
	};

	callback = (key) => {
		console.log(key);
	};
	render() {
		return (
			<div>
				{this.state.user !== "NOTUSER" ? (
					<div>

						<div className="ProfileDivRe">
							<Toolbar data={this.state.user} ShowModal={this.ShowModal} />
						</div>
						<div style={{ height: '130px' }} />
						<div style={{ height: '100px' }}>
							<div className="TabsInPro">
								<Tabs defaultActiveKey="1" onChange={this.callback} style={{ width: '100%' }}>
									<TabPane tab="مکان های دیدنی" key="1" />
									<TabPane tab="رستوران" key="2" />
								</Tabs>
							</div>
						</div>
						

						{this.state.commentThings.items === undefined  ? (
							<div style={{ marginLeft: '22%', marginRight: '22%' }}>
								<div>loading ..... </div>
							</div>
						) : (
							<div>
								<div style={{ marginLeft: '22%', marginRight: '22%' }}>
									{this.state.commentThings.items.map((data) => <Card data={data} />)}
								</div>
								{/* <div className="PaginationCostomCss">
									<Pagination
										current={this.state.currentPagination}
										onChange={this.onChangePagination}
										total={this.props.myreviews.totalCount}
										showSizeChanger
									/>
								</div> */}
							</div>
						)}

						<ModalEdit
							data={this.state.user}
							handleCancel={this.handleCancel}
							visible={this.state.visible}
							change={this.update}
							reloadPage={this.checkLoginForStep}
						/>
					</div>
				) : (
					<div style = {{height: '80vh', padding: '11% 0px 0px 0px'}}>
							<div class="spinner">
								<div class="dot1" />
								<div class="dot2" />
							</div>
						</div>
				)}
			</div>
		);
	}
}
// redux code
const mapStateToProps = (state) => ({
	myreviews: state.posts.myreviews,
	login: state.posts.login
});
// redux code

export default connect(mapStateToProps, { myReviews })(CardProfiletwo); // redux code
