import React, { Component } from 'react';
import Close from '../../assets/closeGreen.png';
import { Row } from 'antd';
import IconSearch from '../../assets/search.png';
import IconLocation from '../../assets/location.png';
import SearchResult from '../search/SearchResult';

// redux code
import { connect } from 'react-redux';
import { searchPlace } from '../../actions/Searchplace';
import { searchCity } from '../../actions/search/searchCity';

class ModalSearch extends Component {
	constructor(props) {
		super(props);
		this.state = {
			search: {
				type: '',
				name: [ 'name=', '', '', '' ],
				city: '&collection=city',
				thingsToDo: '&collection=things-to-do',
				resturant: '&collection=restaurant'
			},
			city: '',
			disable: false
		};
	}
	componentDidMount = () => {
		let jus = document.getElementById('SaerchInjustroRow');
		jus.style.borderBottomColor = '#00a680';
	};
	handelSearchInJustro = (event) => {
		if (event.target.value.length > 1) {
			if (this.state.search.name[2] === '') {
				let temp = this.state.search;
				temp.type = 'free-mode';
				temp.name[1] = event.target.value;
				this.props.searchPlace(temp);
				this.setState({
					search: temp
				});
			} else {
				let temp = this.state.search;
				temp.name[1] = event.target.value;
				this.props.searchPlace(temp);
				this.setState({
					search: temp
				});
			}
		} else {
			let temp = this.state.search;
			temp.name[1] = event.target.value;
			this.setState({
				search: temp
			});
		}
	};
	SearchCity = (event) => {
		if (event.target.value.length > 1) {
			this.props.searchCity(event.target.value);
			this.setState({
				city: event.target.value,
				disable: true
			});
		} else {
			this.setState({
				city: event.target.value,
				disable: false
			});
		}
	};
	closeModal = () => {
		let modal = document.getElementById('divSearchNavbar');
		modal.style.display = 'none';
	};
	goToCity = (city) => {
		let temp = this.state.search;
		temp.name[2] = '&layerUid=';
		temp.name[3] = city.layer_uid;
		temp.type = 'city';
		this.setState({
			search: temp,
			city: city.name,
			disable: false
		});
	};
	borderButtonJustro = () => {
		let jus = document.getElementById('SaerchInjustroRow');
		jus.style.borderBottomColor = '#00a680';
		let cit = document.getElementById('SaerchCityRow');
		cit.style.borderBottomColor = '#7e7e7e';
	};
	borderButtonCity = () => {
		let jus = document.getElementById('SaerchInjustroRow');
		jus.style.borderBottomColor = '#7e7e7e';
		let cit = document.getElementById('SaerchCityRow');
		cit.style.borderBottomColor = '#00a680';
	};
	render() {
		return (
			<div id="divSearchNavbar" className="modalSearchMobileView">
				<img
					onClick={this.closeModal}
					className='closeButtonSearchMobileView'
					style={{ verticalAlign: 'bottom', marginLeft: '3%', width : "4.9%" }}
					src={Close}
				/>
				<Row id="SaerchInjustroRow" className="RowModalSearchMobileView">
					<input
						onClick={this.borderButtonJustro}
						onKeyUp={this.handelSearchInJustro}
						placeholder="جستجو در جاسترو"
						className="inputSearchInJustroMobileView"
					/>
					<img style={{ width: '4.9%' }} src={IconSearch} />
				</Row>
				<Row id="SaerchCityRow" className="RowModalSearchMobileView">
					<input
						onClick={this.borderButtonCity}
						value={this.state.city}
						onChange={this.SearchCity}
						placeholder="انتخاب مقصد"
						className="inputSearchInJustroMobileView"
					/>
					<img style={{ width: '4.5%' }} src={IconLocation} />
				</Row>
				{this.state.disable === true ? (
					<Row style={{ marginTop: '5%' }}>
						{this.props.search.city.slice(0, 6).map((data) => (
							<Row style={{ margin: '3% 0px' }} onClick={this.goToCity.bind(null, data)}>
								<Row data-tag={data} className="ta-r nameModalSearchCity">
									{data.name}
								</Row>
								<Row className="ta-r parentModalSearchCity">{data.parent} , ایران</Row>
							</Row>
						))}
					</Row>
				) : (
					<SearchResult data={this.props.search.searchPlace} justro={this.state.search.name[1]} />
				)}
			</div>
		);
	}
}
// redux code
const mapStateToProps = (state) => ({
	search: state.posts
});
// redux code

export default connect(mapStateToProps, { searchPlace, searchCity })(ModalSearch); // redux code
