import React, { Component } from 'react';
import image from '../../assets/arrow-down-sign-to-navigate.svg';

export default class SearchMainCityMobileView extends Component {
	constructor(props) {
		super(props);
		this.state = {
			showModal: 'none'
		};
	}
	showModal = () => {
		let modal = document.getElementById('ModalSearchCity');
		modal.style.display = 'block';
	};
	onClose = () => () => {
		this.setState({
			showModal: 'none'
		});
	};

	render() {
		return (
			<div>
				{window.location.pathname === '/' ||
				window.location.pathname === '/ResturantSearch' ||
				window.location.pathname === '/Attraction' ? (
					<button
						id="buttonSearchCityMobileView"
						className="ButtonKojamiriMobileView"
						onClick={this.showModal}
					>
						<img src={image} style={{ width: '16px', height: '16px' }} />

						<span style={{ marginRight: '3px' }}>
							{' '}
							<b>{this.props.text}</b>{' '}
						</span>
					</button>
				) : (
					<button
						id="buttonSearchCityMobileView"
						className="ButtonKojamiriPlacelistMobileView"
						onClick={this.showModal}
					>
						<img src={image} style={{ width: '16px', height: '16px' }} />

						<span style={{ marginRight: '3px' }}>
							{' '}
							<b>{this.props.text}</b>{' '}
						</span>
					</button>
				)}
			</div>
		);
	}
}
