import React, { Component } from 'react';
// import { color } from 'style-value-types';
import { Row, Col } from 'antd';
import CardTextRate from '../card/CardTextRate';

export default class SimilarNearBy extends Component {
	render() {
		return (
			<div>
				{'items' in this.props.data ? (
					<div className="gutter-example">
						<Row className="headerSection">

							<Col className="flr sectionTitle ">{this.props.data.name}</Col>
							{this.props.data.items.length > 4 ? (
								<Col className=" fll seeAll ">
									{this.props.data.type === 'things-to-do' ? (
										<a
											href={`../ThingsTodoList?
											uid=${this.props.uidAndCity.cityUid}
											&city=${this.props.uidAndCity.city}
											&visitPage=${this.props.data.visitPage}
											&filter=${JSON.stringify(this.props.uidAndCity.similarPlaces.filter)}
											`}
										>
											مشاهده همه
										</a>
									) : (
										<a
											href={`../RestaurantList?uid=${this.props.uidAndCity.cityUid}&city=${this
												.props.uidAndCity.city}&visitPage=${this.props.data
												.visitPage}&filter=${JSON.stringify(
												this.props.uidAndCity.similarPlaces.filter
											)}`}
										>
											مشاهده همه
										</a>
									)}
								</Col>
							) : null}
						</Row>

						<Row gutter={16}>
							{this.props.data.items.splice(0, 4).map((item) => {
								return <CardTextRate data={item} />;
							})}
						</Row>
					</div>
				) : null}
			</div>
		);
	}
}
