export const ThingsToDoTags = (Uid) => (dispatch) => {
	fetch(`/api/things-to-do/tags-count?${Uid[0]}${Uid[1]}`)
		.then((res) => res.json())
		.then((tags) => {
			let array = [];
			for (let i = 0; i < tags.length; i++) {
				if (tags[i].count > 0) {
					let tag = {
						name: tags[i].name,
						count: tags[i].count,
						checked: false
					};

					array.push(tag);
				}
			}

			dispatch({
				type: FETCH_TAGS,
				payload: array
			});
		})
		.catch(function(error) {
			// this.setState({ existLogin: 'NOTHING' });
			console.log(error);
		});
};

