export const myReviews = () => (dispatch) => {
	fetch(`/api/review/my-reviews-web`).then((res) => res.json()).then((reviews) => {
		console.log('//////////////////////////////////////', reviews);
		let myreviews = {
			items: reviews.items,
			totalCount: reviews.totalCount
		};
		dispatch({
			type: MY_REVIEWS,
			payload: myreviews
		}).catch(function(error) {
			// this.setState({ existLogin: 'NOTHING' });
			console.log(error);
		});
	});
};

export const commentOtherUser = (Uid) => (dispatch) => {
	fetch(`/api/review/user_web?userUid=${Uid}`).then((res) => res.json()).then((tags) => {
		let send = {
			restaurant: [],
			thingToDo: [],
			totalCount: 0
		};
		send.totalCount = tags.totalCount;
		for (let i = 0; i < tags.items.length; i++) {
			if (tags.items[i].place.type === 'things-to-do') {
				send.thingToDo.push(tags.items[i]);
			} else if (tags.items[i].place.type === 'restaurant') {
				send.restaurant.push(tags.items[i]);
			}
		}
		dispatch({
			type: FETCH_TAGS,
			payload: send
		}).catch(function(error) {
			// this.setState({ existLogin: 'NOTHING' });
			console.log(error);
		});
	});
};
