export const searchCity = (city) => (dispatch) => {
	fetch(`/api/city/city-names?q=${city}`)
		.then((res) => res.json())
		.then((tags) => {
			return dispatch({
				type: FETCH_TAGS,
				payload: tags
			});
		})
		.catch(function(error) {
			// this.setState({ existLogin: 'NOTHING' });
			console.log(error);
		});
};

export const searchCityThingToDo = (city, section) => (dispatch) => {
	dispatch({
		type: START_FETCH
	});
	fetch(`/api/filter/free-mode?name=${city}&rate=DSC&collection=city&collection=${section}&start=0&len=10`)
		.then((res) => res.json())
		.then((tags) => {
			console.log(tags);
			let temp =
				section === 'things-to-do'
					? [ { city: 0, 'things-to-do': 0 }, [ { items: [] }, { items: [] } ] ]
					: [ { city: 0, restaurant: 0 }, [ { items: [] }, { items: [] } ] ];
			if ('hashCode' in tags) {
				return dispatch({
					type: FETCH_TAGS,
					payload: temp
				});
			} else {
				return dispatch({
					type: FETCH_TAGS,
					payload: tags
				});
			}
			console.log(tags);
		})
		.catch(function(error) {
			// this.setState({ existLogin: 'NOTHING' });
			console.log(error);
		});
};

