// post ************************************************************************************

export const createPost = (postData, start) => (dispatch) => {
	dispatch({
		type: FETCH_POSTS,
		payload: []
	});

	fetch(`/api/new-config/main/seeMore?start=${start}&length=20`, {
		method: 'POST',
		headers: {
			'content-type': 'application/json'
		},
		body: JSON.stringify(postData)
	})
		.then((res) => res.json())
		.then((post) =>
			dispatch({
				type: FETCH_POSTS,
				payload: post
			})
		)
		.catch(function(error) {
			// this.setState({ existLogin: 'NOTHING' });
			console.log(error);
		});
};

export const updataProfile = (postData) => (dispatch) => {
	console.log('updata profile ****************************', postData);
	dispatch({ type: START_FETCH });
	fetch(`/api/cu/v1/profile`, {
		method: 'POST',
		headers: {
			'content-type': 'application/json'
		},
		body: JSON.stringify(postData)
	})
		.then((res) => res.json())
		.then((updataProfileUser) =>
			dispatch({
				type: UPDATA_PROFILE,
				payload: updataProfileUser
			})
		)
		.catch(function(error) {
			// this.setState({ existLogin: 'NOTHING' });
			console.log(error);
		});
};

