import React, { Component } from 'react';
import Header from './ThingsToDoHeader';
import FeaturesSection from '../FeaturesSection';
import Suggestions from '../suggestions';
import { Skeleton, Col, Row } from 'antd';

// redux code
import { connect } from 'react-redux';
import { fetchPostsAttraction } from '../../actions/postActions';

let mainConfig = [];

class ThingsToDoMain extends Component {
	componentWillMount() {
		this.props.fetchPostsAttraction();
	}

	render() {
		let arr = mainConfig;
		return (
			<div>
				<Header />
				<FeaturesSection />
				{this.props.posts.length > 0 ? (
					<div>{this.props.posts.map((suggestion) => <Suggestions data={suggestion} />)}</div>
				) : (
					<Row style={{ padding: '0 12.5% 4% 12.5%' }} gutter={16}>
						{[ 1, 2, 3, 4 ].map((item) => {
							return (
								<Col className="gutter-row main-front-view modifySkeletonWhereYouGo" span={6}>
									<Skeleton className="SkeletonCardRow" active avatar paragraph={{ rows: 3 }} />
								</Col>
							);
						})}
						{[ 1, 2, 3, 4 ].map((item) => {
							return (
								<Col className="gutter-row main-front-view modifySkeletonWhereYouGo" span={6}>
									<Skeleton className="SkeletonCardRow" active avatar paragraph={{ rows: 3 }} />
								</Col>
							);
						})}
					</Row>
				)}

				{/* <CitySuggestion />s */}
			</div>
		);
	}
}

// redux code
const mapStateToProps = (state) => ({
	posts: state.posts.items
});
// redux code

export default connect(mapStateToProps, { fetchPostsAttraction })(ThingsToDoMain); // redux code
