import React, { Component } from 'react';
import { Row, Col } from 'antd';
import DefThing from '../card/DefaultThingsToDo';
import DefRes from '../card/DefaultResturant';
import Photo from '../card/Photo';
import PhotoRe from '../card/ResturantPhoto';
import Rating from 'react-rating';
import { Link } from 'react-router-dom';

export default class CardSearchResult extends Component {
	render() {
		return (
			<a
				href={
					this.props.data.type === 'city' ? (
						`${`/?cityName=${this.props.data.title}&layer_uid=${this.props.data.uid}`}`
					) : (
						`${`/${this.props.data.type}?uid=${this.props.data.uid}`}`
					)
				}
			>
				<Row style={{ padding: '0.5vh 0px', marginBottom: '4%' }}>
					<Col span={18} style={{ paddingRight: '2%' }}>
						<Row className="titleCardtextRateMobileView">{this.props.data.title}</Row>
						<Row style={{ textAlign: 'right' }}>
							<Rating
								className="ratingMobileView"
								type="custom"
								initialRating={this.props.data.rate}
								readonly
							/>
						</Row>
						<Row className="CiAndProCardtextRateMobileView">
							{this.props.data.type === 'city' ? (
								<span>{this.props.data.province} , </span>
							) : (
								<span>{this.props.data.city} , </span>
							)}
							{this.props.data.type === 'city' ? (
								<span>ایران</span>
							) : (
								<span>{this.props.data.province}</span>
							)}
						</Row>
					</Col>

					<Col span={6}>
						{'images' in this.props.data ? (
							<div>
								{this.props.data.images.length > 0 ? (
									<div className="imageCardSearchResult">
										{this.props.data.type !== 'restaurant' ? (
											<Photo photo={this.props.data.images[0]} />
										) : (
											<PhotoRe photo={this.props.data.images[0]} />
										)}
									</div>
								) : (
									<div className="imageCardSearchResult">
										{this.props.data.type === 'restaurant' ? <DefRes /> : <DefThing />}
									</div>
								)}
							</div>
						) : (
							<div className="imageCardSearchResult">
								{this.props.data.type === 'restaurant' ? <DefRes /> : <DefThing />}
							</div>
						)}
					</Col>
				</Row>
			</a>
		);
	}
}
