import React, { Component } from 'react';
import { Link } from 'react-router-dom';


export default class ButtonMap extends Component {
	render() {
		return (
			<a href={`./Map?uid=${this.props.uid}&type=${this.props.type}`}
				to={{
					pathname: '/Map',
					search: `?uid=${this.props.uid}&type=${this.props.type}`
				}}
			>
				<button className="seeInMapMobileView">نقشه</button>
			</a>
		);
	}
}
