import React, { Component } from 'react';
import { Map, InfoWindow, Marker, GoogleApiWrapper } from 'google-maps-react';

import hotelMap from '../../assets/hotelMap.png';
import ttdMap from '../../assets/ttdMap.png';
import restaurantMap from '../../assets/restaurantMap.png';
import { Row, Col } from 'antd';
// import AddToFavorite from '../AddToFavorite';
import Rating from 'react-rating';
import defaultThin from '../../assets/DefaultThingsToDo.jpeg';
import defaultRes from '../../assets/defultResturant.jpeg';

export class MiniMap extends Component {
	constructor(props) {
		super(props);
		this.state = {
			showingInfoWindow: false,
			activeMarker: {},
			selectedPlace: {},
			locationData: []
		};
	}

	onMarkerClick = (props, marker, e) =>
		this.setState({
			selectedPlace: props,
			activeMarker: marker,
			showingInfoWindow: true
		});

	onMapClicked = (props) => {
		if (this.state.showingInfoWindow) {
			this.setState({
				showingInfoWindow: false,
				activeMarker: null
			});
		}
	};
	componentDidMount = () => {
		this.fillMap(this.props.location);
	};
	fillMap = (nearby) => {
		let array = [];

		if (nearby.loaded == true) {
			if ('nearbies' in nearby.items) {
				// if("Hotel" in nearby.items.nearbies){
				//   for(let CounterNearby = 0 ; CounterNearby < nearby.items.nearbies.Hotel.length ; CounterNearby++){
				//     let loc = {
				//       rate: nearby.items.nearbies.Hotel[CounterNearby].rate ,
				//       image: nearby.items.nearbies.Hotel[CounterNearby].images[0],
				//       name: nearby.items.nearbies.Hotel[CounterNearby].title,
				//       lat: nearby.items.nearbies.Hotel[CounterNearby].point.coordinates[1],
				//       lng: nearby.items.nearbies.Hotel[CounterNearby].point.coordinates[0],
				//       type: nearby.items.nearbies.Hotel[CounterNearby].type
				//     }
				//     console.log(nearby.items.nearbies.Hotel[CounterNearby].type)
				//     array.push(loc);
				//   }
				// }

				if ('Things-To-Do' in nearby.items.nearbies) {
					for (
						let CounterNearby = 0;
						CounterNearby < nearby.items.nearbies[`Things-To-Do`].length;
						CounterNearby++
					) {
						let loc = {
							subTitle: nearby.items.nearbies[`Things-To-Do`][CounterNearby].tags,
							rate: nearby.items.nearbies[`Things-To-Do`][CounterNearby].rate,
							image:
								'images' in nearby.items.nearbies[`Things-To-Do`][CounterNearby]
									? '/files/justro.png?mode=view&fcode=' +
										nearby.items.nearbies[`Things-To-Do`][CounterNearby].images[0]
									: null,
							name: nearby.items.nearbies[`Things-To-Do`][CounterNearby].title,
							lat: nearby.items.nearbies[`Things-To-Do`][CounterNearby].point.coordinates[1],
							lng: nearby.items.nearbies[`Things-To-Do`][CounterNearby].point.coordinates[0],
							type: nearby.items.nearbies[`Things-To-Do`][CounterNearby].type,
							uid: nearby.items.nearbies[`Things-To-Do`][CounterNearby].uid,
							city: nearby.items.nearbies[`Things-To-Do`][CounterNearby].city
						};
						array.push(loc);
					}
				}

				if ('Restaurant' in nearby.items.nearbies) {
					for (
						let CounterNearby = 0;
						CounterNearby < nearby.items.nearbies.Restaurant.length;
						CounterNearby++
					) {
						let loc = {
							subTitle: nearby.items.nearbies.Restaurant[CounterNearby].price,
							rate: nearby.items.nearbies.Restaurant[CounterNearby].rate,
							image: nearby.items.nearbies.Restaurant[CounterNearby].images[0],
							name: nearby.items.nearbies.Restaurant[CounterNearby].title,
							lat: nearby.items.nearbies.Restaurant[CounterNearby].point.coordinates[1],
							lng: nearby.items.nearbies.Restaurant[CounterNearby].point.coordinates[0],
							type: nearby.items.nearbies.Restaurant[CounterNearby].type,
							uid: nearby.items.nearbies.Restaurant[CounterNearby].uid,
							city: nearby.items.nearbies.Restaurant[CounterNearby].city
						};
						array.push(loc);
					}
				}
			}
			this.setState({
				locationData: array
			});
		}
	};

	render() {
		const style = {
			width: '100%',
			height: '100%',
			position: 'releative'
		};
		const hotelIcon = {
			url: hotelMap,
			scaledSize: new this.props.google.maps.Size(31, 31)
		};
		const ttdIcon = {
			url: ttdMap,
			scaledSize: new this.props.google.maps.Size(31, 31)
		};
		const restIcon = {
			url: restaurantMap,
			scaledSize: new this.props.google.maps.Size(31, 31)
		};
		return (
			<Map
				google={this.props.google}
				style={style}
				initialCenter={{
					lat: this.props.location.items.point.coordinates[1],
					lng: this.props.location.items.point.coordinates[0]
				}}
				zoom={15}
				onClick={this.onMapClicked}
			>
				<Marker
					position={{
						lat: this.props.location.items.point.coordinates[1],
						lng: this.props.location.items.point.coordinates[0]
					}}
				/>

				{this.state.locationData.map((data) => (
					<Marker
						onClick={this.onMarkerClick}
						title={data.name}
						name={data.name}
						image={data.image}
						rate={data.rate}
						type={data.type}
						uid={data.uid}
						city={data.city}
						subTitle={data.subTitle}
						position={{
							lat: data.lat,
							lng: data.lng
						}}
						icon={data.type === 'hotel' ? hotelIcon : data.type === 'restaurant' ? restIcon : ttdIcon}
					/>
				))}

				<InfoWindow marker={this.state.activeMarker} visible={this.state.showingInfoWindow}>
					<div>
						{/* <h1>{this.state.selectedPlace.name}</h1> */}

						<div className="fullMapCard">
							<a
								target="_blank"
								href={`./${this.state.selectedPlace.type}?uid=${this.state.selectedPlace
									.uid}&city=${this.state.selectedPlace.city}`}
							>
								<Row>
									<Col span={14}>
										<div style={{ width: '191px', height: '100px', paddingRight: '10px' }}>
											<Row 
											className = "TextFullMapRow">
											
												<span 
												className="TextFullMap shabnam">
													{this.state.selectedPlace.name}
												</span>
											</Row>
											<Row>
												<div className="CardPicLikeTeRaRating">
													<Rating
														className="resultRating rtl rating"
														type="custom"
														initialRating={this.state.selectedPlace.rate}
														readonly
													/>
													{/* <span
													className=" main-front-comment flr rtl CardPicLikeTeRabazkhord"
													style={{ paddingTop: '5px' }}
												>
													7650 بازخورد
												</span> */}
												</div>
											</Row>
											{this.state.selectedPlace.type === 'restaurant' ? (
												<Row>
													{this.state.selectedPlace.subTitle === 1 ? (
														<span className="subStar">$</span>
													) : this.state.selectedPlace.subTitle === 2 ? (
														<span className="subStar">$$</span>
													) : this.state.selectedPlace.subTitle === 3 ? (
														<span className="subStar">$$$</span>
													) : this.state.selectedPlace.subTitle === 4 ? (
														<span className="subStar">$$$$</span>
													) : (
														<span className="subStar">$$$$$</span>
													)}
												</Row>
											) : this.state.selectedPlace.type === 'things-to-do' ? (
												<Row>
													<span className="subStar">
														{this.state.selectedPlace.subTitle[0]}
													</span>
												</Row>
											) : null}
										</div>
									</Col>
									{this.state.selectedPlace.image !== undefined ? (
										<Col span={10}>
											{/* <AddToFavorite className="addToFavMargin restaurantNearby" /> */}
											<div
												className="PictureFullMap"
												style={{
													background: `URL(${this.state.selectedPlace.image})`,
													width: '128px',
													height: '100px'
												}}
											/>
										</Col>
									) : (
										<Col span={10}>
											{this.state.selectedPlace.type === 'restaurant' ? (
												<div
													className="PictureFullMap"
													style={{
														background: `URL(${defaultRes})`,
														width: '128px',
														height: '100px'
													}}
												/>
											) : (
												<div
													className="PictureFullMap"
													style={{
														background: `URL(${defaultThin})`,
														width: '128px',
														height: '100px'
													}}
												/>
											)}
										</Col>
									)}
								</Row>
							</a>
						</div>
					</div>
				</InfoWindow>
			</Map>
		);
	}
}

export default GoogleApiWrapper({
	apiKey: 'AIzaSyA2i7HL7TQ-q2RvhNb_x4c35UvAol90VXA'
})(MiniMap);
