import { TAGS_PLACELIST } from './types';
import axios from 'axios';

export const tagsPlaceList = (uid) => (dispatch) => {
	if (uid !== null) {
		axios
			.get(`/api/${uid.path}/tags-count?layerUid=${uid.uid}`)
			.then(function(res) {
				dispatch({
					type: TAGS_PLACELIST,
					payload: res.data
				});
			})
			.catch(function(error) {
				console.log(error);
			});
	}
};
