import React, { Component } from 'react';
import { Checkbox, Upload, Modal, Input, Menu, Dropdown, Icon, Radio } from 'antd';

// redux import
import { connect } from 'react-redux';
import { updataProfile } from '../actions/postActions';
import profileDefault from '../assets/profileDefault.jpeg';

const { TextArea } = Input; //text Area Edit profile

// meno dropdown
const menu = (
	<Menu>
		<Menu.Item key="0">
			<a>مذهبی</a>
		</Menu.Item>
		<Menu.Item key="1">
			<a>number one</a>
		</Menu.Item>
		<Menu.Divider />
		<Menu.Item key="3">3rd menu item</Menu.Item>
	</Menu>
);

const RadioGroup = Radio.Group; //   Radio button man or woman in Edit Profile

class ModalEditProfile extends Component {
	constructor(props)
	{
		super(props);
		this.state = {
			value: 1,
			user: {
				age: '',
				avatarHash: '',
				biography: '',
				email: '',
				gender: '',
				locality: '',
				name: '',
				phoneNo: '',
				reviewCount: 0
			},
			sex: true,
			imageUrl:"/files/justro.png?mode=view&fcode="+props.data.avatarHash
		};
		
	}
	
	getBase64=(img, callback)=> {
		const reader = new FileReader();
		reader.addEventListener('load', () => callback(reader.result));
		reader.readAsDataURL(img);
	  }
	avatarUploader = (info) => {
		if (info.file.status === 'uploading') {
			console.log('uploading : ---------------->>> ');
		}
		if (info.file.status === 'done') {
			let temp = this.state.user;
			temp.avatarHash = info.file.response[0].hash;
			this.setState({
				user: temp
			});

			this.getBase64(info.file.originFileObj, imageUrl => this.setState({
				imageUrl,
				loading: false,
			  }));



			console.log('ok hast : ---------------->>> ');
		}
	};

	componentWillMount = () => {
		let person = this.state.user;
		person.name = this.props.data.name;
		person.age = this.props.data.age;
		person.avatarHash = this.props.data.avatarHash;
		person.gender = this.props.data.gender;
		person.locality = this.props.data.locality;
		person.phoneNo = this.props.data.phoneNo;
		person.reviewCount = this.props.data.reviewCount;
		person.biography = this.props.data.biography;
		person.email = this.props.data.email;
		if (this.props.data.gender === 'female') {
			this.setState({
				sex: false
			});
		} else {
			this.setState({
				sex: true
			});
		}
		this.setState({
			user: person
		});
	};

	handleOk = (e) => {
		console.log(e);
		this.setState({
			visible: false
		});
	};

	handleCancel = (e) => {
		this.props.handleCancel();
	};

	handelOnChange = (e) => {
		let temp = this.state.user;

		switch (e.target.name) {
			case 'name':
				temp.name = e.target.value;
				break;
			case 'locality':
				temp.locality = e.target.value;
				break;
			case 'email':
				temp.email = e.target.value;
				break;
			case 'biography':
				temp.biography = e.target.value;
				break;
			// default:
			// code block
		}

		this.setState({
			user: temp
		});
	};

	onChangeRadioButtonEditProfile = (e) => {
		console.log('radio checked', e.target.value);
		this.setState({
			value: e.target.value
		});
	};
	saveChangeData = () => {
		
		console.log('11111111111111111111111111111111', this.state.user);
		this.props.updataProfile(this.state.user);
		this.props.handleCancel();
		// this.props.change();
		this.props.reloadPage()
	};
	setSex = (e) => {
		if (e.target.checked === true) {
			if (e.target.name !== 'male') {
				let temp = this.state.user;
				temp.gender = 'female';
				this.setState({
					user: temp,
					sex: false
				});
			} else {
				let temp = this.state.user;
				temp.gender = 'male';
				this.setState({
					user: temp,
					sex: true
				});
			}
		} else {
			if (e.target.name !== 'male') {
				let temp = this.state.user;
				temp.gender = 'female';

				this.setState({
					user: temp,
					sex: true
				});
			} else {
				let temp = this.state.user;
				temp.gender = 'male';
				this.setState({
					user: temp,
					sex: false
				});
			}
		}
	};
	cancelChangeData = () => {
		this.props.handleCancel();
	};



	render() {
		return (
			<div>
				<Modal
					title="Basic Modal"
					visible={this.props.visible}
					onOk={this.handleOk}
					onCancel={this.handleCancel}
					width="540px"
					className="EditProfile"
				>
					{this.state.user.avatarHash === '' || this.state.user.avatarHash === null ? (
						<div
							className="PictureProfileTwo"
							style={{
								backgroundImage: `URL(${profileDefault})`
							}}
						>
								<Upload
									name="avatar"
									listType="picture-card"
									className="editProfileAvatarText"
									showUploadList={false}
									action="/api/files/upload"
									onChange={this.avatarUploader}
								>
									<span className="uploadTextEditProfile shabnam">بارگیری تصویر</span>
								</Upload>
						</div>
					) : (
						<div
							className="PictureProfileTwo " 
							style={{
								backgroundImage:`URL(${this.state.imageUrl}`
							}}
							
						> 
						<div className="PictureProfileTwo" style={{width:"100%",height:"100%",backgroundColor:"rgba(0, 0, 0, 0.2)"}}></div>
							<Upload
								name="avatar"
								listType="picture-card"
								className="editProfileAvatarText"
								showUploadList={false}
								action="/api/files/upload"
								onChange={this.avatarUploader}
							>
								<span className="uploadTextEditProfile shabnam">بارگیری تصویر</span>
							</Upload>
						</div>
					)}

					<div style={{ padding: '28px 75px', marginTop: '10px' }}>
						<div className="TextEditProfileTwo shabnam">نام</div>
						<input
							name="name"
							onChange={this.handelOnChange}
							value={this.state.user.name}
							className="EditProInput"
						/>

						<div className="TextEditProfileTwo shabnam">شهر</div>
						<input
							name="locality"
							onChange={this.handelOnChange}
							value={this.state.user.locality}
							className="EditProInput"
						/>

						{/* <a className="ant-dropdown-link" href="#">
							<Dropdown overlay={menu} trigger={[ 'click' ]}>
								<div className="EditProDropDown">
									{' '}
									<Icon type="down" />{' '}
								</div>
							</Dropdown>
						</a> */}

						<div className="TextEditProfileTwo shabnam">ایمیل</div>
						<input
							name="email"
							onChange={this.handelOnChange}
							value={this.state.user.email}
							className="EditProInput"
						/>

						<div className="TextEditProfileTwo shabnam">جنسیت</div>

						{/* baraye radio button edit profile */}
						{/* <div className="RadioButtonEditProfile" style={{ textAlign: 'right' }}>
							<RadioGroup onChange={this.onChangeRadioButtonEditProfile} value={this.state.value}>
								<Radio value={2}>زن</Radio>
								<Radio value={1}>مرد</Radio>
							</RadioGroup>
						</div> */}
						<div style={{ textAlign: 'right', direction: 'ltr' }}>
							<Checkbox name="male" onChange={this.setSex} checked={this.state.sex} className="shabnam">
								مرد{' '}
							</Checkbox>
							<Checkbox
								name="female"
								onChange={this.setSex}
								checked={!this.state.sex}
								className="shabnam"
							>
								زن{' '}
							</Checkbox>
						</div>

						<div className="TextEditProfileTwo shabnam">بیوگرافی</div>
						<div className="biographyEditProfile shabnam">
							<TextArea
								name="biography"
								onChange={this.handelOnChange}
								value={this.state.user.biography}
								rows={4}
							/>
						</div>
					</div>

					<button onClick={this.saveChangeData} className="EditProButtonSave shabnam" style={{cursor:"pointer"}}>
						ذخیره
					</button>
					<button onClick={this.cancelChangeData} className="EditProButtonCancel shabnam">
						انصراف
					</button>
				</Modal>
			</div>
		);
	}
}

const mapStateToProps = (state) => ({
	newPost: state.posts.item
});

export default connect(mapStateToProps, { updataProfile })(ModalEditProfile); // redux code
