import React, { Component } from 'react';
import { Row, Modal } from 'antd';
import CategoryTags from './CategoryTags';
import Rating from './Rating';

// redux code
import { connect } from 'react-redux';
import { tagsPlaceList } from '../../../actions/tagsPlaceList';

var url_string = window.location.href;
var url = new URL(url_string);
let layer_uid = url.searchParams.get('uid');
let city = url.searchParams.get('city');

class ModalTags extends Component {
	constructor(props) {
		super(props);
		this.state = {
			tags: '',
			selectedTags: [],
			selectedRate: 5
		};
	}

	componentDidMount = () => {
		this.props.tagsPlaceList(layer_uid);
		if (this.props.tagAndRate !== undefined) {
			console.log('fffffffffffffffffffffffffff', typeof this.props.tagAndRate.tags);
			if (typeof this.props.tagAndRate.tags === 'string') {
				let o = [];
				o.push(this.props.tagAndRate.tags)
				this.setState({
					selectedTags: o ,
					selectedRate: this.props.tagAndRate.rate
				});
			} else {
				this.setState({
					selectedTags: this.props.tagAndRate.tags,
					selectedRate: this.props.tagAndRate.rate
				});
			}
		}
	};
	handleCancel = () => {
		this.props.handelVisible();
	};
	reset = () => {
		this.setState({
			selectedTags: [],
			selectedRate: 5
		});
	};
	updataTags = (tags) => {
		this.setState({
			selectedTags: tags
		});
	};
	updateRate = (rate) => {
		this.setState({
			selectedRate: rate
		});
	};
	sendTags = () => {
		this.props.update(this.state);
		console.log('send tags');
	};

	render() {
		return (
			<div id="ModalTagsPlaceList" className="modalTagsPlaceListMobileView">
				<Modal
					closable={false}
					wrapClassName="modalTagsMobileView"
					style={{ top: 0 }}
					bodyStyle={{ margin: '0 !important' }}
					visible={this.props.visible}
					onOk={this.handleOk}
					onCancel={this.handleCancel}
				>
					<Row className="divModalTagsPlaceListMobileView">
						<button className="buttonModalTagsPlaceListMobileView" onClick={this.handleCancel}>
							بازگشت
						</button>
						<span className="fillterModalTagsPlaceListMobileView">فیلترها</span>
					</Row>
					<Row style={{ padding: '20% 9% 0 9%' }}>
						<CategoryTags
							updataTags={this.updataTags}
							selectedTags={this.state.selectedTags}
							tags={this.props.tags}
						/>
						<Rating updateRate={this.updateRate} selectedRate={this.state.selectedRate} />
						<Row style={{ borderBottom: '1px solid #7e7e7e', margin: '0 8%', paddingBottom: '22%' }}>
							<button
								onClick={this.reset}
								className="buttonFuctionModalTagsPlaceListMobileView bazneshaniModalTagsPlaceListMobileView shabnam"
							>
								بازنشانی
							</button>
							<button
								onClick={this.sendTags}
								className="buttonFuctionModalTagsPlaceListMobileView emalKardanModalTagsPlaceListMobileView shabnam"
							>
								اعمال کردن
							</button>
						</Row>
					</Row>
				</Modal>
			</div>
		);
	}
}
// redux code
const mapStateToProps = (state) => ({
	tags: state.posts.tagsPlaceList
});
// redux code

export default connect(mapStateToProps, { tagsPlaceList })(ModalTags); // redux code
