import {  CHECK_LOGIN,START_FETCH,FETCH_POSTS, NEW_POST, FETCH_TAGS, FETCH_LIST, SEARCH_CITY, UPDATA_PROFILE, MY_REVIEWS, WRITE_REVIEW } from '../actions/types';

/////////////////////// mobile viwe //////////////////////////////////////////
import { ROOT_SUGGESTION, RESTURANT_SUGGESTION, CITY_SEARCH, PLACE_LIST, SEARCH_PLACE, PLACE_DETAIL, TAGS_PLACELIST, TOP_CITY } from '../actions/types';

const initialState = {
    items : [],
    city : [],
    list : [],
    tagsPlaceList : [],
    placeDetail : [],
    searchPlace : [],
    topCity : [],
    loaded : false,
    visibleLoginPageThree: false,
            updataProfileUser:'NOTHING'
}

export default function(state = initialState, action){
    switch(action.type){
        case CHECK_LOGIN:
        return {
            ...state,
            loaded:false,
            updataProfileUser:action.payload,
            visibleLoginPageThree: action.payload.name===null || action.payload.name==="" ?true : false,

        }
        case START_FETCH:
        return {
            ...state,
            loaded:false
        }
        case CITY_SEARCH : 
        return { 
            ...state,
            city: action.payload,
            loaded:true
        };
        case TOP_CITY : 
        return { 
            ...state,
            topCity: action.payload,
            loaded:true
        };
        case TAGS_PLACELIST : 
        return { 
            ...state,
            tagsPlaceList: action.payload,
            loaded:true
        };
        case PLACE_DETAIL : 
        return { 
            ...state,
            placeDetail: action.payload,
            loaded:true
        };
        case RESTURANT_SUGGESTION : 
        return { 
            ...state,
            items: action.payload,
            loaded:true
        };
        case ROOT_SUGGESTION : 
        return { 
            ...state,
            items: action.payload,
            loaded:true
        };
        case PLACE_LIST : 
        return { 
            ...state,
            list: action.payload,
            loaded:true
        };
        case SEARCH_PLACE : 
        return { 
            ...state,
            searchPlace: action.payload,
            loaded:true
        };

     
//////////////////////////////////////////////// up mobile viwe    ///////////////////////////////
        case FETCH_POSTS : 
        return { 
            ...state,
            items: action.payload,
            loaded:true
        };
        case WRITE_REVIEW : 
        return { 
            ...state,
            reviewPlace: action.payload,
            loaded:true
        };
        case MY_REVIEWS : 
        return { 
            ...state,
            myreviews: action.payload,
            loaded:true
        };
        case UPDATA_PROFILE : 
        return { 
            ...state,
            updataProfileUser: action.payload,
            loaded:true
        };
        case SEARCH_CITY : 
        return { 
            ...state,
            search: action.payload,
            loaded:true
        };

        case FETCH_LIST : 
        return { 
            ...state,
            list: action.payload,
            loaded:true
        };

        case FETCH_TAGS : 
        return { 
            ...state,
            tags: action.payload,
            loaded:true
        };
        case NEW_POST:
        return { 
            ...state,
            items: action.payload,
            loaded:true
        };

        default : 
        return state;
    }
}


  