import React, { Component } from 'react';
import { Modal } from 'antd';
import PartOne from './Login/PartOne';
import PartTwo from './Login/PartTwo';

export default class ModalLogin extends Component {
	state = {
		ModalText: 'Content of the modal',
		visible: false,
		confirmLoading: false,
		partOne: {
			visible: true ,
			phoneNumber: ''
		},
		partTwo: false
	};
	handleCancel = () => {
		this.setState({
			visible: false
		});
	};
	endOfPartOne = (one) => {
		let temp = this.state.partOne;
		temp.visible = false;
		temp.phoneNumber = one;
		this.setState({
			partOne: temp
		});
		this.partTwo();
	};
	partTwo = () => {
		this.setState({
			partTwo: true
		});
	};
	showModal = () => {
		this.setState({
			visible: true
		});
	};
	handleOk = () => {
		this.setState({
			ModalText: 'The modal will be closed after two seconds',
			confirmLoading: true
		});
		setTimeout(() => {
			this.setState({
				visible: false,
				confirmLoading: false
			});
		}, 2000);
	};
	render() {
		return (
			<div>
				{/* <button className="buttonDrawerNavbarMobileView loginDrawerNavbarMobileView">عضویت</button> */}
				<button onClick={this.showModal} className="buttonDrawerNavbarMobileView openDrawerNavbarMobileView">
					ورود
				</button>

				<Modal
					closable={false}
					wrapClassName="modalLoginMobileView"
					style={{ top: 0 }}
					bodyStyle={{ margin: '0 !important' }}
					visible={this.state.visible}
					onOk={this.handleOk}
					confirmLoading={this.state.confirmLoading}
					onCancel={this.handleCancel}
				>
					<div className="tac fSize24 color-jusrtu shabnam margin-top9">ورود به جاسترو</div>
					{this.state.partOne.visible === true ? <PartOne endOfPartOne={this.endOfPartOne} /> : null}
					{this.state.partTwo === true ? <PartTwo phoneN={this.state.partOne.phoneNumber} /> : null}
				</Modal>
			</div>
		);
	}
}
