import { CITY_SEARCH } from '../types';

export const searchCity = (city) => (dispatch) => {
	fetch(`/api/city/city-names?q=${city}`)
		.then((res) => res.json())
		.then((city) => {
			dispatch({
				type: CITY_SEARCH,
				payload: city
			});
		})
		.catch((error) => {
			console.log(error);
		});
};
