import React, { Component } from 'react';
import justroIcon from '../../assets/justro-logo@3x.png';
import menu from '../../assets/menu.png';
import ModalSearchCity from './ModalSearchCity';
import SearchMainCity from '../search/SearchMainCityMobileView';
import ModalSearch from '../search/ModalSearch';
import ModalTags from '../placeList/ModalTags/ModalTags';
import Drawer from './DrawerNavBar';
import menuW from '../../assets/menuWhite.png';
import Download from '../downLoad/download';

import { Icon, Col } from 'antd';

var url_string = window.location.href;
var url = new URL(url_string);
let city = url.searchParams.get('city');

export default class MobileMainNavbar extends Component {
	constructor(props) {
		super(props);
		this.state = {
			sidebarOpen: false
		};
	}
	showModalSearch = () => {
		let modal = document.getElementById('divSearchNavbar');
		modal.style.display = 'block';
	};
	onSetSidebarOpen = () => {
		this.setState({ sidebarOpen: !this.state.sidebarOpen });
	};
	render() {
		setTimeout(function() {
			try {
				if (window.location.pathname === '/ThingsTodoList' || window.location.pathname === '/RestaurantList') {
					document.getElementById('mainNavBarMobileView').classList.add('colorHandlerMobileView');
				}
			} catch (err) {}
		}, 1);
		return (
			<div>
				
				{window.location.pathname === '/things-to-do' ||
				window.location.pathname === '/restaurant' ||
				window.location.pathname === '/Map' ||
				window.location.pathname === '/AllPlaces' ? null : (
					<div>
						<ModalSearchCity />
						<ModalSearch />
						<ModalTags />
						<Download />

						<Drawer onSetSidebarOpen={this.onSetSidebarOpen} open={this.state.sidebarOpen} />

						<div style={{ height: '9vh', padding: '3.6% 5.6%' }} id="mainNavBarMobileView">
							<Col span={3}>
								{window.location.pathname === '/' ||
								window.location.pathname === '/ResturantSearch' ||
								window.location.pathname === '/Attraction' ? (
									<Icon
										onClick={this.showModalSearch}
										type="search"
										style={{
											fontSize: '1.75rem',
											color: '#757575'
										}}
									/>
								) : (
									<Icon
										onClick={this.showModalSearch}
										type="search"
										style={{
											color: 'white',
											fontSize: '1.75rem'
										}}
									/>
								)}
							</Col>
							<a href="/">
								<Col span={18} style={{ textAlign: 'center' }}>
									{window.location.pathname === '/' ||
									window.location.pathname === '/ResturantSearch' ||
									window.location.pathname === '/Attraction' ? (
										<span className="justroHeader">جاسترو</span>
									) : (
										<span style={{ color: 'white', fontSize: '20px' }}>جاسترو</span>
									)}

									{window.location.pathname === '/' ||
									window.location.pathname === '/ResturantSearch' ||
									window.location.pathname === '/Attraction' ? (
										<img src={justroIcon} style={{ width: '15%' }} />
									) : (
										<SearchMainCity text={city} />
									)}
								</Col>
							</a>

							<Col span={3} style={{ textAlign: 'right' }}>
								{window.location.pathname === '/' ||
								window.location.pathname === '/ResturantSearch' ||
								window.location.pathname === '/Attraction' ? (
									<img onClick={this.onSetSidebarOpen} src={menu} style={{ width: '80%' }} />
								) : (
									<img onClick={this.onSetSidebarOpen} src={menuW} style={{ width: '80%' }} />
								)}
							</Col>
						</div>
					</div>
				)}
			</div>
		);
	}
}
