import React, { Component } from 'react';
import JustroIcon from '../../assets/justro-logo@3x.png';
import { Icon } from 'antd';
import { isAndroid , CustomView } from 'react-device-detect';
var currentTime = '';

export default class download extends Component {
	checkAppExist = () => {
		currentTime = Date.now();

		var getAppVersion = navigator.appVersion;
		if (getAppVersion.indexOf('Android') !== -1 || getAppVersion.indexOf('android') !== -1) {
			setTimeout(this.callStores, 1000);
			window.location = 'justro://checkappexist/checkAppExist/';
		}
	};
	callStores = () => {
		if (currentTime - Date.now() < 3000) {
			window.location = 'market://details?id=com.JustRO.android';
		}
	};
	close = () => {
		let v = document.getElementById('downLoadApp');
		v.style.display = 'none';
	};
	render() {
		return (
			<CustomView condition={ isAndroid  }>
				<div id="downLoadApp" className="downLoadApp alCenter">
					<button onClick={this.checkAppExist} className="color-jusrtu buttonDownLoadApp bgWhite borNone">
						دانلود برنامه
					</button>
					<span 
                    style = {{margin: "0px 5vw 0px 7vw", fontSize: "14px"}}
                    className="colorWhite">کارایی بهتر در اپلیکیشن</span>

					<img className="imageDownLoadApp" src={JustroIcon} />
					<Icon onClick={this.close} style={{ color: 'white', margin: '0px 0px 0vw 3vw' }} type="close" />
				</div>
			</CustomView>
		);
	}
}
