import React, { Component } from 'react';
import ButtonSearch from './search/SearchKojamiri';
import bg from '../assets/mobileViewPhoto/backgroundRootMobileView.png';

export default class HeaderWhereYouGo extends Component {
	render() {
		return (
			<div>
				<header
					style={{
						background: `${this.props.image === undefined
							? `URL(${bg})`
							: `${'name' in this.props.image
									? `URL(${bg})`
									: `URL(${`${'images' in this.props.image.cityDetail[0]
											? `/files/justro.png?mode=view&fcode=${this.props
													.image.cityDetail[0].images[0]}`
											: `${bg}`}  `})`}`} `
					}}
					className="headerPhotoMobileView backgroundMainMobileView"
				/>
				<div className="WhereYouGoButtonForRootPageMobileVeiw  ">
					<ButtonSearch data={{ destination: this.props.destination, scale: 'scale(0.9)' }} />
				</div>
			</div>
		);
	}
}
