import React, { Component } from 'react';
import { Row } from 'antd';
// import Rating from 'react-rating';
// import AddToFavorite from './AddToFavorite';
import CardLikeRate from './card/CardLikeRate';

export default class CategorySuggestion extends Component {
	constructor(props) {
		super(props);
		this.state = {
			checked: false
		};
	}

	onClick = (evnet, props) => {
		this.setState({ checked: !this.state.checked });
	};

	render() {
		return (
			<div className="mb10" style={{ marginRight: '2.5%' }}>
				{this.props.data.name === undefined || this.props.data.items.length === 0 ? null : (
					<div>
						<div className="result-front-view-header-title">{this.props.data.name}</div>
						<div className="gutter-example" style={{ marginTop: '-3%' }}>
							<div className="front-view-header-title" />

							<Row gutter={16}>
								{this.props.data.items.slice(0, 4).map((data) => <CardLikeRate data={data} />)}
							</Row>
						</div>
					</div>
				)}
			</div>
		);
	}
}
