import React, { Component } from 'react';
import { Row, Col } from 'antd';
// import AddToFavorite from '../AddToFavorite';
import Rating from 'react-rating';
import DefaultThings from '../../assets/DefaultThingsToDo.jpeg';
import DefaultRes from '../../assets/defultResturant.jpeg';

export default class CardMap extends Component {
	render() {
		return (
			<Row>
				<a
					target="_blank"
					href={`./${this.props.selectedPlace.type}?uid=${this.props.selectedPlace.uid}&city=${this.props
						.selectedPlace.city}`}
				>
					<Col span={14}>
						<div style={{ width: '191px', height: '100px', paddingRight: '10px' }}>
							<Row className="TextFullMap shabnam">
								<span>{this.props.selectedPlace.name}</span>
							</Row>
							<Row>
								<div className="CardPicLikeTeRaRating">
									<Rating
										className="resultRating rtl rating"
										type="custom"
										initialRating={this.props.selectedPlace.rate}
										readonly
									/>
									{/* <span
										className=" main-front-comment flr rtl CardPicLikeTeRabazkhord"
										style={{ paddingTop: '5px' }}
									>
										7650 بازخورد
									</span> */}
								</div>
							</Row>
							{'tags' in this.props.selectedPlace ? (
								<div>
									{this.props.selectedPlace.tags.length > 0 ? (
										<Row>
											{this.props.selectedPlace.tags
												.slice(0, 3)
												.map((data) => <span className="subStar shabnam">{data}</span>)}
										</Row>
									) : null}
								</div>
							) : null}
						</div>
					</Col>

					<Col span={10}>
						{this.props.selectedPlace.image !== undefined ? (
							<div>
								{this.props.selectedPlace.image.length > 0 ? (
									<div>
										{this.props.selectedPlace.image[0].charAt(0) === 'h' &&
										this.props.selectedPlace.image[0].charAt(1) === 't' &&
										this.props.selectedPlace.image[0].charAt(2) === 't' &&
										this.props.selectedPlace.image[0].charAt(3) === 'p' ? (
											<div
												className="PictureFullMap"
												style={{
													background: `URL(/files/justro.png?mode=view&fcode=${this.props.selectedPlace.image[0]})`,
													width: '128px',
													height: '100px'
												}}
											/>
										) : (
											<div
												className="PictureFullMap"
												style={{
													background: `URL(/files/justro.png?mode=view&fcode=${this
														.props.selectedPlace.image[0]})`,
													width: '128px',
													height: '100px'
												}}
											/>
										)}
									</div>
								) : (
									<div>
										{this.props.data.type === 'things-to-do' ? (
											<div
												className="mask grad PictureFullMap"
												style={{
													background: `URL(${DefaultThings})`,
													width: '128px',
													height: '100px',
													borderRadius: '5px',
													backgroundSize: 'cover'
												}}
											/>
										) : (
											<div
												className="mask grad PictureFullMap"
												style={{
													background: `URL(${DefaultRes})`,
													width: '128px',
													height: '100px',
													borderRadius: '5px',
													backgroundSize: 'cover'
												}}
											/>
										)}
									</div>
								)}
							</div>
						) : (
							<div>
								{this.props.selectedPlace.type === 'things-to-do' ? (
									<div
										className="mask grad PictureFullMap"
										style={{
											background: `URL(${DefaultThings})`,
											width: '128px',
											height: '100px',
											borderRadius: '5px',
											backgroundSize: 'cover'
										}}
									/>
								) : (
									<div
										className="mask grad PictureFullMap"
										style={{
											background: `URL(${DefaultRes})`,
											width: '128px',
											height: '100px',
											borderRadius: '5px',
											backgroundSize: 'cover'
										}}
									/>
								)}
							</div>
						)}
					</Col>
				</a>
			</Row>
		);
	}
}
