import React, { Component } from 'react';
import Suggestion from '../Suggestion';
import CardList from '../CardList';
import { Row } from 'antd';
import Filter from '../Filter';
import Bmap from '../ButtonMap';

// redux code
import { connect } from 'react-redux';
import { placeSuggestion } from '../../../actions/placeSuggestion';
import { placeList } from '../../../actions/placeList';
import { Link } from 'react-router-dom';

var url_string = window.location.href;
var url = new URL(url_string);
let layer_uid = url.searchParams.get('uid');
let city = url.searchParams.get('city');
let visitPage = url.searchParams.get('visitPage');

class ResturantList extends Component {
	constructor(props) {
		super(props);
		this.state = {
			place: {
				type: 'restaurant',
				uid: [ '?cityUid=', layer_uid ]
			},
			placeList: {
				type: 'restaurant',
				layerUid: layer_uid,
				tags: this.forUrl('tag'),
				rate: this.forUrl('rate'),
				start: 0
			},
			tagAndRate: {
				tags: this.mapFilteringFromURL(String(url)).tag,
				rate: this.mapFilteringFromURL(String(url)).rate
			},
			start: 0
		};
	}
	componentDidMount = () => {
		this.props.placeSuggestion(this.state.place);
		this.props.placeList(this.state.placeList);
	};
	forUrl = (tags) => {
		if (tags === 'tag') {
			let t = this.mapFilteringFromURL(String(url)).tag;
			console.log('ooooooooooooooooo', t);
			let y = [];
			if (t === undefined) {
				return y;
			} else {
				for (let i = 0; i < t.length; i++) {
					y += `&tag=${t[i]}`;
				}
				return y;
			}
		} else if (tags === 'rate') {
			let r = this.mapFilteringFromURL(String(url)).rate;
			if (r === undefined) {
				return '';
			} else {
				let c = `&rate=${r}`;
				return c;
			}
		}
	};

	more = () => {
		if (this.state.start < this.props.suggestion.list.totalCount - 10) {
			let temp = this.state.placeList;
			temp.start = temp.start + 10;
			this.setState({
				placeList: temp
			});
			this.props.placeList(temp);
		}
	};
	updateTagsAndRate = (tags) => {
		for (let i = 0; i < tags.selectedTags.length; i++) {
			let temp = this.state.placeList;
			temp.tags += `&tag=${tags.selectedTags[i]}`;
			this.setState({
				placeList: temp
			});
		}
		let tempRate = this.state.placeList;
		tempRate.rate = `&rate=${tags.selectedRate}`;
		this.setState({
			placeList: tempRate
		});

		this.windowLocation();
	};
	windowLocation = () => {
		window.location.href = `../RestaurantList?uid=${layer_uid}&city=${city}${this.state.placeList.tags}${this.state
			.placeList.rate}&visitPage=0`;
	};

	mapFilteringFromURL = (urlValue) => {
		var subStringUrl = urlValue.substring(urlValue.indexOf('tag'), urlValue.length);
		subStringUrl = decodeURIComponent(subStringUrl);
		let resultObject = this.convertStringQueryToJsonObject(subStringUrl);
		delete resultObject.filteringCount;
		return resultObject;
	};
	convertStringQueryToJsonObject = (queryParamString) => {
		var result = {};
		var urlVariables = queryParamString.split('&');
		for (var i = 0; i < urlVariables.length; i++) {
			var parameterName = urlVariables[i].split('=');
			if (result[parameterName[0]] !== undefined) {
				let currentValue = result[parameterName[0]];

				if (currentValue instanceof Array) {
					currentValue.push(parameterName[1]);
					result[parameterName[0]] = currentValue;
				} else {
					let arrayObject = [];
					arrayObject.push(currentValue);
					arrayObject.push(parameterName[1]);
					result[parameterName[0]] = arrayObject;
				}
			} else {
				result[parameterName[0]] = parameterName[1];
			}
		}
		return result;
	};

	render() {
		return (
			<div style={{ padding: '5%' }}>
				{console.log('this.state.tagAndRate', this.state.tagAndRate)}
				<Filter tagAndRate={this.state.tagAndRate} updateTagsAndRate={this.updateTagsAndRate} />
				<div>
					{visitPage == 0 ? null : (
						<div>{this.props.suggestion.items.map((data) => <Suggestion data={data} />)}</div>
					)}
				</div>

				{'items' in this.props.suggestion.list ? (
					<div>
						<Row className="titleThingsToDoListMobileView">رستوران‌های {city}</Row>
						{this.props.suggestion.list.items.map((data) => <CardList data={data} />)}
						{this.props.suggestion.list.totalCount > 10 ? (
							<button className="buttonThingsToDoListMobileView" onClick={this.more}>
								... بیشتر
							</button>
						) : null}
					</div>
				) : null}

				<Bmap uid={layer_uid} type={'restaurant'} />
			</div>
		);
	}
}
// redux code
const mapStateToProps = (state) => ({
	suggestion: state.posts
});
// redux code

export default connect(mapStateToProps, { placeSuggestion, placeList })(ResturantList); // redux code
