	sendPhoneNumberToServer = () => {
		this.setState({
			valueButtonOne: '',
			timer:59
		});
		if (this.state.stepOneLogin.phoneNumber.length === 11) {
			fetch('/api/cu/v1/phone_verification', {
				method: 'POST',
				headers: {
					'content-type': 'application/json'
				},
				body: JSON.stringify(this.state.stepOneLogin)
			}).then((res) => {
				if (res.status === 204) {
					clearInterval(this.state.start);
					this.counter()
					this.setState({
						visibleLoginPageOne: false,
						visibleLoginPageTwo: true
					});
				} else {
					this.setState({
						valueButtonOne: 'ارسال کد'
					});
					notification.open({
						className : "notificatinMainNavBar",
						message: 'شماره مورد نظر اشتباه وارد شده است',
						description: 'لطفا مجددا شماره را وارد کنید',
						icon: <img 
						style = {{marginRight: "-70%"}}
						src = {justroIcon} 
						width = "17%" />
					});
				}
			});
		}
	};
	counter = () => {
		this.setState({start:setInterval(()=>{
            this.setState({timer:this.state.timer-1});
        },1000)});
		
	};

	sendOtpTokenToServer = () => {
		console.log('11111111111111111');

		const formBody = Object.keys(this.state.stepTwoLogin)
			.map((key) => encodeURIComponent(key) + '=' + encodeURIComponent(this.state.stepTwoLogin[key]))
			.join('&');

		fetch('/j_spring_otptoken_security_check', {
			method: 'POST',
			body: formBody,
			headers: { 'Content-Type': 'application/x-www-form-urlencoded' }
		}).then((res) => {
			if (res.status !== 401 && res.status === 200) {
				console.log('200 gereftim');
				this.props.checkLoginForStep();
				// this.checkLogin();
				this.setState({
					valueButtonTwo: 'ادامه',
					visibleLoginPageTwo: false,
									visibleSearchNavbar: false
				});
				// this.props.checkLogin();
			} else {
				notification.open({
					message: 'کد وارد شده اشتباه است',
					description: 'لطفا کد را مجددا وارد کنید',
					icon: <Icon type="smile" style={{ color: '#108ee9' }} />
				});
				console.log('401 gereftim');
			}
		});
	};

