import React, { Component } from 'react';
import IconSearch from '../../assets/search.png';
import { Row, Col } from 'antd';

import { connect } from 'react-redux';
import { fetchPostsSearch, searchCity, SearchWithCity, TopPlaceSearch } from '../../actions/postActions';

class InputSearchPlace extends Component {
	constructor(props) {
		super(props);
		this.state = {
			place : {
				name: '',
				uid: [ '', '' ]
			},
			disableDropDown: false
		};
	}

	componentDidMount = () => {
		var url_string = window.location.href;
		var url = new URL(url_string);

		let cityQuery = this.state.place ;
		cityQuery.name = url.searchParams.get('Search');
		cityQuery.uid[0] =  '&cityUid=';
		cityQuery.uid[1] =  url.searchParams.get('uid');

		this.setState({
			place : cityQuery
		});
	};


	onChange = (event) => {
		if (event.target.value.length >= 1 && event.target.value.length != 0) {
			let place = this.state.place;
			place.name = event.target.value;
			place.uid = this.props.city.uid;
			this.props.TopPlaceSearch(place);
			this.props.insertText(event.target.value);

			this.setState({
				place: place,
				disableDropDown: true
			});
		} else {
			let place = this.state.place;
			place.name = event.target.value;
			place.uid = this.props.city.uid;
			this.setState({
				place: place,
				disableDropDown: false
			});
		}
	};
	selectPlace = (place) => {
		this.props.insertText(place.title);
		let placeS = this.state.place;
		placeS.name = place.title;
		this.setState({
			place: placeS,
			disableDropDown: false
		});
	};

	render() {
		return (
			<div>
				<div className="divSearchPlaceDetails">
					<input
						id="inputSearchPlace"
						autoComplete="off"
						onChange={this.onChange}
						value={this.state.place.name}
						className="inputSearchAllCity"
						placeholder="جست و جو در جاسترو"
					/>
					<img src={IconSearch} style={{ marginLeft: '10px', width: '14px' }} />
				</div>
				{this.props.disableModals === true ? (
					<div>
					{this.state.disableDropDown === false ? null : (
							<div>
								{this.props.list === undefined ? null : (
									<Row className="divDropDownSearchCity">
										{'city' in this.props.list ? (
											<Row
												onClick={this.selectPlace.bind(null, this.props.list.city)}
												className="dropDownSearchCity"
												style={{ marginBottom: '10px' }}
											>
												<Row data-tag={this.props.list.city}>
													<b>{this.props.list.city.title}</b>
												</Row>
												<Row style={{ fontSize: '12px', paddingRight: '10px' }}>
													{this.props.list.city.province}, ایران
												</Row>
											</Row>
										) : null}

										{'restaurant' in this.props.list ? (
											<Row
												onClick={this.selectPlace.bind(null, this.props.list.restaurant)}
												className="dropDownSearchCity"
												style={{ marginBottom: '10px' }}
											>
												<Row data-tag={this.props.list.restaurant}>
													<b>{this.props.list.restaurant.title}</b>
												</Row>
												<Row style={{ fontSize: '12px', paddingRight: '10px' }}>
													{this.props.list.restaurant.province}, ایران
												</Row>
											</Row>
										) : null}

										{'things-to-do' in this.props.list ? (
											<Row
												onClick={this.selectPlace.bind(null, this.props.list['things-to-do'])}
												className="dropDownSearchCity"
												style={{ marginBottom: '10px' }}
											>
												<Row data-tag={this.props.list['things-to-do']}>
													<b>{this.props.list['things-to-do'].title}</b>
												</Row>
												<Row style={{ fontSize: '12px', paddingRight: '10px' }}>
													{this.props.list['things-to-do'].province}, ایران
												</Row>
											</Row>
										) : null}
									</Row>
								)}
							</div>
						)}

					</div>) : null}
						
					
				
			</div>
		);
	}
}
// redux code
const mapStateToProps = (state) => ({
	list: state.posts.list
});
// redux code

export default connect(mapStateToProps, { TopPlaceSearch })(InputSearchPlace); // redux code
