import React, { Component } from 'react';
import { Col, Row, Alert } from 'antd';
import Down from '../../assets/IconDownGray.png';
import ModalTag from './ModalTags/ModalTags';

// redux code
import { connect } from 'react-redux';
import { tagsPlaceList } from '../../actions/tagsPlaceList';

var url_string = window.location.href;
var url = new URL(url_string);
let pathName = url.pathname === '/ThingsTodoList' ? 'things-to-do' : 'restaurant';
let layer_uid = url.searchParams.get('uid');

var tag = {
	path: pathName,
	uid: layer_uid
};

class Filter extends Component {
	constructor(props) {
		super(props);
		this.state = {
			visible: false
		};
	}
	showModalTags = () => {
		if (this.props.tags.length > 0) {
			this.setState({
				visible: true
			});
		}
	};
	handelVisible = () => {
		this.setState({
			visible: false
		});
	};

	componentDidMount = () => {
		this.props.tagsPlaceList(tag);
	};
	update = (up) => {
		this.props.updateTagsAndRate(up);
	};
	render() {
		console.log('state : ', this.state.visible);
		return (
			<Row>
				<div className="filterPlaceListMobileView">
					<Col
						style={{ borderRight: '1px solid #e0e0e0' }}
						span={12}
						className="faildFilterPlaceListMobileView "
					>
						<span style={{ color: '#e0e0e0' }}>( {this.props.tags.length} ) </span>
						همه
					</Col>
					<Col
						onClick={this.showModalTags}
						style={{ color: '#00a680' }}
						span={12}
						className="faildFilterPlaceListMobileView "
					>
						<img style={{ width: '12%', marginRight: '12%' }} src={Down} />
						فیلتر
					</Col>
				</div>
				<ModalTag
					tagAndRate={this.props.tagAndRate}
					update={this.update}
					visible={this.state.visible}
					handelVisible={this.handelVisible}
				/>
			</Row>
		);
	}
}
// redux code
const mapStateToProps = (state) => ({
	tags: state.posts.tagsPlaceList
});
// redux code

export default connect(mapStateToProps, { tagsPlaceList })(Filter); // redux code
