import { SEARCH_PLACE } from './types';
import axios from 'axios';

export const searchPlace = (Search) => (dispatch) => {
	console.log('search place ::::::::::::::::++++++>>>>>', Search);
	fetch(
		`/api/filter/${Search.type}?${Search.name[0]}${Search.name[1]}${Search.name[2]}${Search
			.name[3]}&collection=city&collection=things-to-do&collection=restaurant&start=0&len=10&rate=DSC`
	)
		.then((res) => {
			return res.json()
		})
		.then((posts) => {
			if ('hashCode' in posts) {
				let temp = [ { city: 0, ['things-to-do']: 0, restaurant: 0 }, [{totalCount: 0, items: [], type: "city"}] ];
				dispatch({
					type: SEARCH_PLACE,
					payload: temp
				});
			} else {
				dispatch({
					type: SEARCH_PLACE,	
					payload: posts
				});
			}
		});
};
