import React, { Component } from 'react';
import { Tabs, Row, Pagination } from 'antd';
import CardResult from '../card/CardSearchResult';

const TabPane = Tabs.TabPane;

export default class SearchResult extends Component {
	constructor(props) {
		super(props);
		this.state = {
			current: 1
		};
	}
	callback = (e) => {
		this.setState({
			defaultActiveKey: e
		});
	};
	itemRender = (current, type, originalElement) => {
		if (type === 'prev') {
			return <a>Previous</a>;
		}
		if (type === 'next') {
			return <a>Next</a>;
		}
		return originalElement;
	};
	handelPagination = (e) => {
		this.setState({
			current: e
		});
	};

	render() {
		return (
			<div>
				{this.props.data.length > 0 ? (
					<div>
						<div className="SearchResultMoadlMobileView">
							<Tabs defaultActiveKey="1" onChange={this.callback}>
								<TabPane tab="شهر" key="4" />
								<TabPane tab="رستوران" key="3" />
								<TabPane tab="مکان دیدنی" key="2" />
								<TabPane tab="همه" key="1" />
							</Tabs>
						</div>
						<Row style={{ textAlign: 'right', direction: 'rtl' }}>
							{' '}
							همه ی نتایج مطابق با " {this.props.justro}"
						</Row>
						{this.props.data[1].length === 1 ? (
							<div>{this.props.data[1][0].items.slice(0, 3).map((data) => <CardResult data={data} />)}</div>
						) : (
							<div>
								{this.props.data[1].map((data) => (
									<div>{data.items.slice(0, 1).map((items) => <CardResult data={items} />)}</div>
								))}
							</div>
						)}

						<div className="paginationSearchResultMobileView">
							<Pagination
							onShowSizeChange
								onChange={this.handelPagination}
								current={this.state.current}
								total={
									this.props.data[1].length === 1 ? (
										this.props.data[1].totalCount
									) : (
										(this.props.data[0].city +
											this.props.data[0]['things-to-do'] +
											this.props.data[0].restaurant) /
										3
									)
								}
								itemRender={this.itemRender}
							/>
						</div>
					</div>
				) : null}
			</div>
		);
	}
}
