import React, { Component } from 'react';
import { Col, Row, Checkbox, Breadcrumb, Pagination } from 'antd';
// import CategorySuggestion from "../CategorySuggestion";
import minimappic from '../../assets/minimap.png';
import Rating from 'react-rating';
import CategoryThingsToDoList from './CategoryThingsToDoList';
import SpectacularPlaceResult from '../SpectacularPlace/Result';

import Card from '../card/CardTagsPlaceList';

// redux code
import { connect } from 'react-redux';
import { fetchPostsThingToDoList, ThingsToDoTags, listForThingsToDoList } from '../../actions/postActions';

// gereftan query param *****************************
var url_string = window.location.href;
var url = new URL(url_string);
var uid = url.searchParams.get('uid');
var city = url.searchParams.get('city');
var visitPage = url.searchParams.get('visitPage');

class ThingsTodoList extends Component {
	constructor(props) {
		super(props);
		this.state = {
			error: null,
			isLoaded: false,
			items: [],
			currentPagination: 1,
			viewTwo: false,

			List: {
				start: 0,
				uid: [ '', '' ],
				rate: [ '', '', '', '', '' ],
				tag: '',
				SortByNameOrRate: '&rateSort=DSC'
			},
			lastStart: 0,
			checked: false,
			borderBottonRate: '3px solid #00a680',
			borderBottonAlfa: '',
			textColorRate: 'black !important',
			textColotAlfa: '',
			tags: [ '', '' ]
		};
	}

	componentWillMount() {
		if (uid === 'NoUid') {
			let temp = this.state.List;
			temp.uid[0] = 'cityName=';
			temp.uid[1] = city;

			let tagTemp = this.state.tags;
			tagTemp[0] = 'cityName=';
			tagTemp[1] = city;

			this.setState({
				List: temp,
				tags: tagTemp
			});
			this.props.ThingsToDoTags(tagTemp);
			this.props.listForThingsToDoList(temp);
		} else {
			let temp = this.state.List;
			temp.uid[0] = 'layerUid=';
			temp.uid[1] = url.searchParams.get('uid');

			let tagTemp = this.state.tags;
			tagTemp[0] = 'layerUid=';
			tagTemp[1] = url.searchParams.get('uid');

			this.setState({
				List: temp,
				tags: tagTemp
			});
			this.props.fetchPostsThingToDoList(uid);
			this.props.ThingsToDoTags(tagTemp);
			this.props.listForThingsToDoList(temp);
		}
	}

	fixTagsAzCardTag = (array) => {
		let tagQueries = '';
		for (let i = 0; i < array.length; i++) {
			tagQueries += '&tag=' + array[i].name + '&';
		}
		var lastChar = tagQueries[tagQueries.length - 1];
		if (lastChar === '&') {
			tagQueries = tagQueries.substring(0, tagQueries.length - 1);
		}
		let temp = this.state.List;
		temp.tag = tagQueries;

		this.setState({
			List: temp
		});

		this.props.listForThingsToDoList(this.state.List);
	};

	onChangePagination = (page) => {
		let temp = this.state.List;
		temp.start = (page - 1) * 10;

		this.setState({
			currentPagination: page,
			viewTwo: true,
			List: temp
		});

		this.props.listForThingsToDoList(this.state.List);
		window.scrollTo(0, 0);
	};

	sortByRate = (e) => {
		if (e.target.checked) {
			let temp = this.state.List;

			temp.rate[e.target.name - 1] = '&rate=' + e.target.name;
			this.setState({
				List: temp
			});
			this.props.listForThingsToDoList(temp);
		} else {
			let temp = this.state.List;
			temp.rate[e.target.name - 1] = '';
			this.setState({
				List: temp
			});
			this.props.listForThingsToDoList(temp);
		}
	};

	isAnyRateSelected = () => {
		let tempQueries = this.state.List.rate;
		if (tempQueries !== '') {
			return false;
		} else {
			return true;
		}
	};

	addRateQuery = (rate) => {
		let rateQueries = this.state.List;
		if (rate === 0) {
			this.setStateWithNewRateString('');
		} else {
			this.setStateWithNewRateString(`&rate=${rate}`);
		}
	};

	removeSelectedRate = (name) => {
		let tempQueries = this.state.List.rate;
		let query = 'rate=' + name;
		let index = tempQueries.indexOf(query);

		let firstStringPart = tempQueries.slice(0, index);
		let secondStringPart = tempQueries.slice(index, tempQueries.length);
		let lastCharacter = firstStringPart.charAt[firstStringPart.length - 1];
		if (firstStringPart === '&' || lastCharacter === '&') {
			firstStringPart = firstStringPart.slice(0, firstStringPart.length - 1);
		}
		secondStringPart = secondStringPart.slice(query.length, secondStringPart.length);
		this.setStateWithNewRateString(firstStringPart + secondStringPart);
	};

	setStateWithNewRateString = (newRateQueryString) => {
		let rateQueries = this.state.List;
		rateQueries.rate = newRateQueryString;
		this.setState({
			List: rateQueries
		});
		this.props.listForThingsToDoList(rateQueries);
	};

	sortByNameForList = () => {
		let temp = this.state.List;
		temp.SortByNameOrRate = '&nameSort=DSC';
		this.props.listForThingsToDoList(temp);
		this.setState({
			borderBottonRate: '',
			borderBottonAlfa: '3px solid #00a680',
			textColorRate: '',
			textColotAlfa: 'black !important'
		});
	};

	sortByRateForList = () => {
		let temp = this.state.List;
		temp.SortByNameOrRate = '&rateSort=DSC';
		this.props.listForThingsToDoList(temp);
		this.setState({
			borderBottonRate: '3px solid #00a680',
			borderBottonAlfa: '',
			textColorRate: 'black !important',
			textColotAlfa: ''
		});
	};

	// *******************

	render() {
		return (
			<div style={{ height: 'auto' }}>
				<div>
					<div
						style={{
							margin: '15px 17.5% 0px 12.5%',
							textAlign: 'right'
						}}
					>
						<Breadcrumb separator=">">
							<Breadcrumb.Item>
								<a href={`/?cityName=${city}&layer_uid=${uid}`}>{city}</a>
							</Breadcrumb.Item>
							<Breadcrumb.Item style={{ fontFamily: 'BYekan' }}>مکان های دیدنی {city}</Breadcrumb.Item>
						</Breadcrumb>
					</div>
					<div className="ThingsToDolistTitle shabnam">
						<span className="result-front-view-header-title ThingsToDolistTitle">
							مکان‌های دیدنی {city}
						</span>
					</div>
					<Col span={24} className="SpectacularPlaceBorder marginLayout">
						<Col span={18} className="resaultSpectacularPlace">
							{this.state.viewTwo == false && (visitPage === null || visitPage === 1) ? (
								<div>
									{this.props.posts.items.map((suggestion) => (
										<div>
											<CategoryThingsToDoList data={suggestion} />
										</div>
									))}
								</div>
							) : null}

							{/* moratabsazi ********************************************************** */}
							<Row style={{ height: 'auto' }}>
								{this.state.viewTwo == false && (visitPage === null || visitPage === 1) ? (
									<Col className="sortControl" span={24}>
										<div className="gutter-box  sortTextDiv rtl ta-r flr">
											<span style={{ paddingRight: '20px' }} className="sortText">
												{' '}
												مرتب سازی :{' '}
											</span>
										</div>

										<div
											style={{ borderBottom: this.state.borderBottonRate }}
											className="gutter-box  sortTextDiv rtl ta-r flr selectedSortPlaceList"
											onClick={this.sortByRateForList}
										>
											<span style={{ color: this.state.textColorRate }} className=" sortType">
												امتیاز
											</span>
										</div>

										<div
											style={{ borderBottom: this.state.borderBottonAlfa }}
											className="gutter-box  sortTextDiv rtl ta-r flr selectedSortPlaceList"
											onClick={this.sortByNameForList}
										>
											<span style={{ color: this.state.textColotAlfa }} className="sortType">
												{' '}
												الفبا
											</span>
										</div>
									</Col>
								) : null}

								{/* ********************************************************************** */}

								{this.props.list !== undefined ? (
									<div>
										{'items' in this.props.list ? (
											<Col span={24}>
												{this.props.list.items.length == 0 ? null : (
													<div>
														{this.props.list.items.slice(0, 10).map((item, index) => (
															<Row className="shadowHover">
																<SpectacularPlaceResult data={item} />

																{index + 1 !== this.state.items.length ? (
																	<Col
																		className="fullSeperatorLineResult"
																		span={24}
																	/>
																) : null}
															</Row>
														))}
													</div>
												)}
											</Col>
										) : null}
									</div>
								) : null}

								{/* ********************************************************************** */}
							</Row>
						</Col>
						<Col span={6}>
							<Col span={24} className="padding-15 ">
								<a href={`./Map?uid=${uid}&type=things-to-do&city=${city}`}>
									<img height="140px" width="100%" src={minimappic} style={{ borderRadius: '5px' }} />
								</a>
							</Col>
							{/* ***************************************************************************** */}

							{this.props.tags === undefined ? null : (
								<Card updateFillterTag={this.fixTagsAzCardTag} tags={this.props.tags} />
							)}

							{/* **************************************************** */}
							<Col className="seperatorLine" span={24} />
							<Col span={24} className="padding-15 ta-r shabnam">
								<Row className="filterMainTitle"> بر حسب امتیاز </Row>
								<Row className="ratingRow">
									<Checkbox
										className="shabnam fullWidth minHeight25"
										name="All"
										onChange={this.sortByRate}
									>
										{' '}
										<span className="fullWidth"> همه </span>{' '}
									</Checkbox>

									<Checkbox className="shabnam" name="5" onChange={this.sortByRate}>
										{' '}
										<Rating
											className="marginRating rating"
											type="custom"
											initialRating={5}
											readonly
										/>{' '}
									</Checkbox>
									<Checkbox className="shabnam" name="4" onChange={this.sortByRate}>
										{' '}
										<Rating
											className="marginRating rating"
											type="custom"
											initialRating={4}
											readonly
										/>{' '}
									</Checkbox>
									<Checkbox className="shabnam" name="3" onChange={this.sortByRate}>
										{' '}
										<Rating
											className="marginRating rating"
											type="custom"
											initialRating={3}
											readonly
										/>{' '}
									</Checkbox>
									<Checkbox className="shabnam" name="2" onChange={this.sortByRate}>
										{' '}
										<Rating
											className="marginRating rating"
											type="custom"
											initialRating={2}
											readonly
										/>{' '}
									</Checkbox>
									<Checkbox className="shabnam" name="1" onChange={this.sortByRate}>
										{' '}
										<Rating
											className="marginRating rating"
											type="custom"
											initialRating={1}
											readonly
										/>{' '}
									</Checkbox>
								</Row>
							</Col>

							<Col className="seperatorLine" span={24} />
							{/* <Col span={24} className="padding-15 ta-r shabnam">
							<Row className="filterMainTitle"> آب و هوای مشهد </Row>
						</Col> */}
						</Col>

						{this.props.list !== undefined ? (
							<Col span={24}>
								{/* Pagination ***************************************** */}
								{this.props.list.totalCount > 10 ? (
									<div className="PaginationCostomCss">
										<Pagination
											current={this.state.currentPagination}
											onChange={this.onChangePagination}
											total={parseInt(this.props.list.totalCount)}
											showSizeChanger
										/>
									</div>
								) : null}

								{/* **************************************************  */}
							</Col>
						) : null}

						<Col span={6} />
					</Col>
				</div>
			</div>
		);
	}
}
// redux code
const mapStateToProps = (state) => ({
	posts: state.posts,
	tags: state.posts.tags,
	list: state.posts.list
});
// redux code

export default connect(mapStateToProps, { fetchPostsThingToDoList, ThingsToDoTags, listForThingsToDoList })(
	ThingsTodoList
); // redux code
