import React, { Component } from 'react';
import Header from '../Attraction/Header';
import Suggestion from '../4Suggestion/Suggestion';
import Category from '../Attraction/CategoryThin';

// redux code
import { connect } from 'react-redux';
import { placeSuggestion } from '../../actions/placeSuggestion';


class thingsToDoSearch extends Component {
  constructor(props){
		super(props);
		this.state = {
      place : {
        type : "things-to-do",
        uid : ['','']
      }
		}
	}
	componentDidMount = () => {
		this.props.placeSuggestion(this.state.place);
	};
  render() {
    return (
      <div style = {{marginBottom: "50px"}}>
      <Header />
      <Category />
      {this.props.suggestion.items.map((data) => <Suggestion data={data} />)}

      </div>
    )
  }
}
// redux code
const mapStateToProps = (state) => ({
	suggestion: state.posts
});
// redux code

export default connect(mapStateToProps, { placeSuggestion })(thingsToDoSearch); // redux code
