import React, { Component } from 'react';
import { Col, Row, Checkbox } from 'antd';
import IconDown from '../../assets/IconDown.png';

var url_string = window.location.href;
var url = new URL(url_string);
var filter = JSON.parse(url.searchParams.get('filter'));

let arrayForUrlTags = [];

export default class CardTagsPlaceList extends Component {
	constructor(props) {
		super(props);
		this.state = {
			SelectTags: [], // array az tag haye select shode
			tags: [], // tag haee ke az props miyad to in save mishe
			MoreTag: false, // bara moshahede bishtar va tag 4 ta 10
			ali: this.mapFilteringFromURL(String(url))
		};
	}
	mapFilteringFromURL = (urlValue) => {
		var subStringUrl = urlValue.substring(urlValue.indexOf('filtering'), urlValue.length);
		subStringUrl = decodeURIComponent(subStringUrl);

		let resultObject = this.convertStringQueryToJsonObject(subStringUrl);
		delete resultObject.filteringCount;
		this.convertStringValuesToArray(resultObject);
		return resultObject;
	};
	convertStringQueryToJsonObject = (queryParamString) => {
		var result = {};
		var urlVariables = queryParamString.split('&');
		for (var i = 0; i < urlVariables.length; i++) {
			var parameterName = urlVariables[i].split('=');
			result[parameterName[0]] = parameterName[1];
		}
		return result;
	};
	convertStringValuesToArray = (object) => {
		for (var obj in object) {
			object[obj] = object[obj].split(',');
		}
	};

	componentDidMount = () => {
		this.FillSelectedTag(this.state.ali);
		this.FillStateTags(this.props.tags);
		this.fillTagsFromTopPage(this.props);
	};
	// componentWillUpdate = () =>{
	// 	this.fillTagsFromTopPage(this.props)
	// }
	componentDidUpdate = () => {
		this.fillTagsFromTopPage(this.props);
	};
	fillTagsFromTopPage = (e) => {
		console.log('Card tags ::::::::', e.setTagsFromPage);
		// if(e.setTagsFromPage.name !== undefined){
		// 	let temp = this.state.tags;
		// for(let i=0 ; i < temp.length ; i++){
		// 	if(temp[i].name === e.setTagsFromPage.name){
		// 		console.log('==============================',temp[i])
		// 		temp[i].checked = true;

		// 	}

		// }
		// console.log("Card tags ::::::::", e.setTagsFromPage)

		// }
	};

	FillSelectedTag = (e) => {
		if (e !== null) {
			if (e['new-tags'] !== undefined) {
				for (let i = 0; i < e['new-tags'].length; i++) {
					let tags = {
						checked: true,
						count: 1,
						name: e['new-tags'][i]
					};
					arrayForUrlTags.push(tags);
				}
				this.setState({
					SelectTags: arrayForUrlTags
				});
			}
			this.props.updateFillterTag(arrayForUrlTags);
		}
		let SetToTags = this.state.tags;
		for (let i = 0; i < SetToTags.length; i++) {
			let counter = 0;
		}
	};

	FillStateTags = (e) => {
		if (arrayForUrlTags.length !== 0) {
			let temp = [];
			for (let j = 0; j < arrayForUrlTags.length; j++) {
				for (let i = 0; i < e.length; i++) {
					if (arrayForUrlTags[j].name === e[i].name) {
						arrayForUrlTags[j].count = e[i].count;
						arrayForUrlTags[j].checked = true;
					}
				}
				temp.push(arrayForUrlTags[j]);
			}

			for (let i = 0; i < e.length; i++) {
				for (let j = 0; j < arrayForUrlTags.length; j++) {
					if (arrayForUrlTags[j].name !== e[i].name) {
						temp.push(e[i]);
					}
				}
			}
			this.setState({
				tags: temp
			});
		} else {
			this.setState({
				tags: e
			});
		}
	};

	clearAllSelectedTags = () => {
		// bara pak kardan hame be kar mire
		let temp = this.state.tags;
		for (let counterOne = 0; counterOne < this.state.SelectTags.length; counterOne++) {
			temp[this.getIndexForItemWithName(this.state.SelectTags[counterOne].name)].checked = false;
		}
		let clearAll = this.state.SelectTags;
		clearAll = [];

		this.setState({
			tags: temp,
			SelectTags: clearAll
		});

		this.props.updateFillterTag(clearAll);
	};

	clearSelectedTag = (Event) => {
		let temp = this.state.tags;
		temp[this.getIndexForItemWithName(Event)].checked = false;

		let tempSelected = this.state.SelectTags;

		tempSelected[this.getIndexForItemWithNameInSelectedTags(Event)].checked = false;
		tempSelected.splice(this.getIndexForItemWithNameInSelectedTags(Event), 1);

		this.setState({
			tags: temp,
			SelectTags: tempSelected
		});
		this.props.updateFillterTag(tempSelected);
	};

	//TODO: fix method name
	SelectTags = (Event) => {
		// tag haye select mishe ba in handel mishe

		if (Event.target.checked == true) {
			let selected = this.state.tags;
			selected[this.getIndexForItemWithName(Event.target.name)].checked = !selected[
				this.getIndexForItemWithName(Event.target.name)
			].checked;

			let TagHayeSelectShode = this.state.SelectTags;
			TagHayeSelectShode = TagHayeSelectShode.concat(selected[this.getIndexForItemWithName(Event.target.name)]);

			this.setState({
				tags: selected,
				SelectTags: TagHayeSelectShode
			});

			this.props.updateFillterTag(TagHayeSelectShode);
		} else if (Event.target.checked == false) {
			let selected = this.state.tags;
			selected[this.getIndexForItemWithName(Event.target.name)].checked = !selected[
				this.getIndexForItemWithName(Event.target.name)
			].checked;
			this.clearSelectedTag(Event.target.name);
			this.setState({
				tags: selected
			});
		}
	};

	//TODO:fix name for this method
	MoreTags = () => {
		// bara bishtar va tag haye 4 ta 10 hast
		this.setState({
			MoreTag: true
		});
	};
	lesTags = () => {
		this.setState({
			MoreTag: false
		});
	};

	getIndexForItemWithName = (name) => {
		for (let i = 0; i < this.state.tags.length; i++) {
			if (this.state.tags[i].name === name) {
				return i;
			}
		}
	};

	getIndexForItemWithNameInSelectedTags = (name) => {
		for (let i = 0; i < this.state.SelectTags.length; i++) {
			if (this.state.SelectTags[i].name === name) {
				return i;
			}
		}
	};

	render() {
		return (
			<div>
				{this.state.tags.length > 0 ? (
					<div>
						{/* tag haye select shode ****************** */}
						{this.state.SelectTags.length > 0 ? (
							<div>
								<Col className="seperatorLine" span={24} />

								<Col span={24} className="padding-15 rtl">
									<Row className="rtl ta-r shabnam  ">
										<Row style={{ marginBottom: '10px' }}>
											<span style={{ fontSize: '10px', float: 'right' }}>انتخاب های شما</span>
											<span
												onClick={this.clearAllSelectedTags}
												style={{ fontSize: '10px', float: 'left', cursor: 'pointer' }}
											>
												پاک کردن همه
											</span>
										</Row>
										{this.state.SelectTags.map((tag) => (
											<Row>
												<i
													data-tag={tag.name}
													onClick={this.clearSelectedTag.bind(null, tag.name)}
													class="fas fa-times"
													style={{ color: '#e6692f', fontSize: '12px' }}
												/>

												<label className="pr-2">{tag.name}</label>
											</Row>
										))}
									</Row>
								</Col>
							</div>
						) : null}
						<Col className="seperatorLine" span={24} />
						{/* tag haye samt rast ************************************** */}
						<Col span={24} className="padding-15 ta-r shabnam">
							<Row className="filterMainTitle"> دسته بندی نقاط دیدنی</Row>
							<Row className="rtl ta-r shabnam">
								{/* in badra in zade shode ke 4 ta tag aval ro neshon bede */}
								{this.state.tags.slice(0, 4).map((tag) => (
									<div>
										<Checkbox
											className="shabnam"
											name={tag.name}
											onChange={this.SelectTags}
											checked={tag.checked}
										>
											{tag.name}{' '}
										</Checkbox>
										<span className="filterNumbers">({tag.count})</span>
									</div>
								))}
								{/* MoreTag bara ine ke bishtar hazf shavad va tag haye 4 ta 10 neshon bede */}
								{this.state.MoreTag == false ? (
									<Row className="shabnam" onClick={this.MoreTags}>
										<a>
											بیشتر <img src={IconDown} className="PlaceDetailMore" />
										</a>
									</Row>
								) : null}
								{/* baghi tag ha az 4 ta 10 ro neshon mide */}
								{this.state.MoreTag == true ? (
									<div>
										{this.state.tags.slice(4, 10).map((tag) => (
											<div>
												<Checkbox
													className="shabnam"
													name={tag.name}
													onChange={this.SelectTags}
													checked={tag.checked}
												>
													{tag.name}{' '}
												</Checkbox>
												<span className="filterNumbers">({tag.count})</span>
											</div>
										))}
									</div>
								) : null}
								{this.state.MoreTag == true ? (
									<Row className="shabnam" onClick={this.lesTags}>
										<a>
											کمتر{' '}
											<img
												style={{ transform: 'rotate(180deg)', marginBottom: '1px' }}
												src={IconDown}
												className="PlaceDetailMore"
											/>
										</a>
									</Row>
								) : null}
							</Row>
						</Col>
					</div>
				) : (
					<p>loading</p> // ino badan bayad loading ya skeleton bezanim
				)}
			</div>
		);
	}
}
