import React, { Component } from "react";
import "antd/dist/antd.css";
import { Icon } from "antd";
import SearchMainCity from "./searchMainCity";


export default class WhereYouGoButton extends Component {
  constructor() {
    super();
    this.state = {
      isVisible: true,
    };

    this.showMainCitySearch = this.showMainCitySearch.bind(this);
  }
  Cancle = () =>{
   window.location.href="../"
  }
  showMainCitySearch = e => {
    this.setState({ 
      isVisible: true ,
      visibleModal : true
    });
  };
  componentDidMount() {
  
  }

  render() {
    return (
      <div
        className="WhereYouGoBar "
        style={{ transform: this.props.data.scale, zIndex: "15" }}>
         <SearchMainCity  text = {this.props.data.destination}/>
      </div>
    );
  }
}
