import React, { Component } from 'react';
import AddReview from '../AddReview';
import ProgressViewStats from '../ProgressViewStats';

export default class ReviewStats extends Component {
	constructor(props) {
		super(props);
		this.state = {
			noReview: false,
			
		};
	}

	render() {
		return (
			<div>
				{'items' in this.props.comment ? (
					<div>
						{this.props.comment.items.length > 0 ? (
							<ProgressViewStats data={this.props.comment.overallRate} />
						) : (
							<AddReview />
						)}
					</div>
				) : (
					<AddReview />
				)}
			</div>
		);
	}
}
