import React, { Component } from 'react';
import Header from './HeaderResturantSearch';
import Suggestion from '../4Suggestion/Suggestion';
import Category from '../ResturantSearch/CategoryRes';

// redux code
import { connect } from 'react-redux';
import { placeSuggestion } from '../../actions/placeSuggestion';

class RootPageResturantPage extends Component {
	constructor(props) {
		super(props);
		this.state = {
			place: {
				type: 'restaurant',
				uid: [ '', '' ]
			}
		};
	}
	componentDidMount = () => {
		this.props.placeSuggestion(this.state.place);
	};
	render() {
		return (
			<div style={{ marginBottom: '50px' }}>
				<Header />
				<Category />
				{this.props.suggestion.items.map((data) => <Suggestion data={data} />)}
			</div>
		);
	}
}

// redux code
const mapStateToProps = (state) => ({
	suggestion: state.posts
});
// redux code

export default connect(mapStateToProps, { placeSuggestion })(RootPageResturantPage); // redux code
