import React, { Component } from 'react';
import { Row, Col } from 'antd';

import Card from '../card/CardNearby';

export default class NearbyRestaurant extends Component {
	constructor(props) {
		super(props);
	}
	render() {
		return (
			<Row className=" ">
				{this.props.data == undefined ? null : (
					<div>
						<Row className="headerSection">
							<Col className="flr sectionTitle "> رستوران‌های اطراف </Col>
							{this.props.data.length > 8 ? (
								<Col className=" fll seeAll ">
									<a
										href={`../RestaurantList?uid=${this.props.cityAndUid.cityUid}&city=${this.props
											.cityAndUid.city}&visitPage=0&filter=${JSON.stringify(this.props.cityAndUid.similarPlaces.filter)}`}
									>
										مشاهده همه
									</a>
								</Col>
							) : null}

							<Col span={24} />

							{this.props.data.slice(0, 6).map((items) => <Card data={items} />)}
						</Row>
					</div>
				)}
			</Row>
		);
	}
}
