import React, { Component } from 'react';
import { Modal } from 'antd';
import Rating from 'react-rating';
import { Row, Dropdown, Icon } from 'antd';

export default class AddComm extends Component {
	state = {
		ModalText: 'Content of the modal',
		visible: false,
		confirmLoading: false,
		text: ''
	};

	showModal = () => {
		this.setState({
			visible: true
		});
	};
	handleOk = () => {
		this.setState({
			ModalText: 'The modal will be closed after two seconds',
			confirmLoading: true
		});
		setTimeout(() => {
			this.setState({
				visible: false,
				confirmLoading: false
			});
		}, 2000);
	};

	handleCancel = () => {
		this.setState({
			visible: false
		});
	};
	commentText = (e) => {
	if(this.state.text.length < 700){
		this.setState({
			text: e.target.value
		});
	}
	};

	render() {
		const { visible, confirmLoading, ModalText } = this.state;
		return (
			<div>
				<div style={{ margin: '4% 0' }} className="juCenter">
					<button onClick={this.showModal} className="addCommentPlaceDetailMobileView">
						ثبت بازخورد
					</button>
					<Modal
						closable={false}
						wrapClassName="modalAddCommentMobileView"
						style={{ top: 0 }}
						bodyStyle={{ margin: '0 !important' }}
						visible={visible}
						onOk={this.handleOk}
						confirmLoading={confirmLoading}
						onCancel={this.handleCancel}
					>
						<div className="color-jusrtu tac fSize19 shabnam bold">افزودن بازخورد</div>
						<div className="c757575 tac fSize15 shabnam bold youRateAddCommentMobileView">امتیاز شما</div>
						<Row style={{ textAlign: 'center', marginTop: '3%' }}>
							<Rating className="ratingAddMobileView" type="custom" initialRating={2} readonly />
						</Row>
						<div className="shabnam">
							<div className="tar shabnam bold mTxtAddCommentMobileView">نوع مراجعه</div>
							<Dropdown overlay={<div>salam</div>} trigger={[ 'click' ]}>
								<div className="rtl">
									<div className="dropDownAddComent">
										<span className="flr">انتخاب کنید</span>
										<Icon type="down" />
									</div>
								</div>
							</Dropdown>

							<div className="tar shabnam bold mTxtAddCommentMobileView">عنوان بازخورد</div>
							<div className="rtl">
								<div className="dropDownAddComent">
									<input className="borNone" />
								</div>
							</div>

							<div className="tar shabnam bold mTxtAddCommentMobileView">شرح بازخورد</div>
							<div>
								<textarea
									onChange={this.commentText}
									className="rtl textAreaAddCommentMobileView"
									rows="4"
								/>
							</div>
							<div>{this.state.text.length}</div>
							<div className="juCenter margin17">
								<div>
									<button className="disBlock buttonAddCommentMobileView fSize18 borNone saveAddCommentMobileView">
										ثبت بازخورد
									</button>
									<button
										onClick={this.handleCancel}
										className="disBlock buttonAddCommentMobileView fSize18 borNone cancelAddCommentMobileView"
									>
										انصراف
									</button>
								</div>
							</div>
						</div>
					</Modal>
				</div>
			</div>
		);
	}
}
