import React, { Component } from 'react';
import { Row } from 'antd';
import Rating from 'react-rating';
import IconLocation from '../../assets/location.png';
import IconPhoneNumber from '../../assets/phone2.png';

export default class Description extends Component {
	render() {
		return (
			<div style={{ padding: '3%' }}>
				<Row className="titleDescriptionPlaceDetailMobileView rtl">{this.props.description.title}</Row>
				<Row style={{ textAlign: 'right' }}>
					<Rating className="ratingMobileView" type="custom" initialRating={this.props.description.rate/2} readonly />
				</Row>
				{this.props.description.address === '' ? null : (
					<Row className="tar overFlowHidden">
						<span>{this.props.description.address}</span>
						<img src={IconLocation} style={{ width: '5%', marginLeft: '2%' }} />
					</Row>
				)}
				{this.props.description.telephoneNum === '' ? null : (
					<Row className="tar">
						<span>{this.props.description.telephoneNum}</span>
						<img src={IconPhoneNumber} style={{ width: '5%', marginLeft: '2%' }} />
					</Row>
				)}
			</div>
		);
	}
}
