import React, { Component } from 'react';
import justroIcon from '../../../assets/justro-logo.png';
import { notification } from 'antd';

export default class PartOne extends Component {
	constructor(props) {
		super(props);
		this.state = {
			buttonSent: 'ارسال کد',
			backgroundCol: '#cdcdcd',
			send: {
				phoneNumber: ''
			}
		};
	}
	send = () => {
		if (this.state.send.phoneNumber.length === 11) {
			this.setState({
				buttonSent: 'در حال ارسال'
			});
			this.sendToServer();
			// this.props.endOfPartOne(this.state.send.phoneNumber);
		}
	};
	number = (e) => {
		let temp = this.state.send;
		if (e.target.value.length === 11) {
			temp.phoneNumber = e.target.value;
			this.setState({
				phoneNumber: temp,
				backgroundCol: '#00a680'
			});
		} else {
			temp.phoneNumber = e.target.value;
			this.setState({
				phoneNumber: temp,
				backgroundCol: '#cdcdcd'
			});
		}
	};
	sendToServer = () => {
		fetch('/api/cu/v1/phone_verification', {
			method: 'POST',
			headers: {
				'content-type': 'application/json'
			},
			body: JSON.stringify(this.state.send)
		}).then((res) => {
			if (res.status === 204) {
				this.props.endOfPartOne(this.state.send.phoneNumber);
				this.setState({
					buttonSent: 'ادامه'
				});
			} else {
				notification.open({
					className: 'notificatinMainNavBar',
					message: 'شماره مورد نظر اشتباه وارد شده است',
					description: 'لطفا مجددا شماره را وارد کنید',
					icon: <img style={{ marginRight: '-70%' }} src={justroIcon} width="17%" />
				});
			}
		});
	};

	render() {
		return (
			<div>
				<div>
					<div className="tac fSize16  margin-top12">
						<span className="color616161">برای </span>
						<span className="colorff8a65">ورود </span>
						<span className="color616161">شماره تلفن خود را وارد کنید</span>
					</div>
				</div>
				<div className="tac fSize16 mTop30">شماره تلفن همراه</div>
				<div />
				<input type="number" onChange={this.number} className="inputPartOneLoginMobileView" />

				<div className="juCenter">
					<button
						className="borNone colorWhite fSize19 shabnam sendButtonPartOneLoginMobileView"
						onClick={this.send}
						style={{ backgroundColor: this.state.backgroundCol }}
					>
						{this.state.buttonSent}
					</button>
				</div>
			</div>
		);
	}
}
