import React, { Component } from 'react';
import DefualtRes from './DefaultResturant';
import DefualtThin from './DefaultThingsToDo';
import Photo from './Photo';
import PhotoRes from './ResturantPhoto';

export default class CardSuggestion extends Component {
	render() {
		return (
			<a href={`/${this.props.data.type}?uid=${this.props.data.uid}`}>
				<div style={{ height: '100%', borderRadius: '5px' }}>
					{'images' in this.props.data ? (
						<div style={{ height: '100%' }}>
							{this.props.data.images === undefined ||
							this.props.data.images.length === undefined ||
							this.props.data.images.length === 0 ? (
								<div style={{ height: '100%' }}>
									{this.props.data.type === 'things-to-do' ? (
										<div className="maskHeroMobileView" style={{ height: '100%' }}>
											<DefualtThin />
										</div>
									) : (
										<div className="maskHeroMobileView" style={{ height: '100%' }}>
											<DefualtRes />
										</div>
									)}
									<div className="cityNameSuggestionMobileView shabnam">{this.props.data.title}</div>
								</div>
							) : (
								<div style={{ height: '100%' }}>
									{this.props.data.type === 'things-to-do' || this.props.data.type === 'hotel' ? (
										<div style={{ height: '100%' }} className="maskHeroMobileView">
											<Photo photo={this.props.data.images[0]} />
										</div>
									) : (
										<div className="maskHeroMobileView" style={{ height: '100%' }}>
											<PhotoRes photo={this.props.data.images[0]} />
										</div>
									)}
									<div className="cityNameSuggestionMobileView shabnam ">{this.props.data.title}</div>
								</div>
							)}
						</div>
					) : (
						<div style={{ height: '100%' }}>
							{this.props.data.type === 'things-to-do' ? (
								<div className="maskHeroMobileView" style={{ height: '100%' }}>
									<DefualtThin />
								</div>
							) : (
								<div className="maskHeroMobileView" style={{ height: '100%' }}>
									<DefualtRes />
								</div>
							)}
							<div className="cityNameSuggestionMobileView shabnam ">{this.props.data.title}</div>
						</div>
					)}
				</div>
			</a>
		);
	}
}
