import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import 'antd/dist/antd.css';
import './App.css';
import './index.css';
import { BrowserRouter, Route, Switch } from 'react-router-dom';

import { Provider } from 'react-redux'; //this code is for redux
import store from './store'; //this code is for redux

import MainNavbar from './Components/mainNavbar';
import home from './Components/home';
import Flight from './Components/Flight/Flight';
import TopCategorizedList from './Components/TopCategorizedList';
import RestaurantDetail from './Components/Restaurant/ResturantDetail';
import WhereYouGo from './Components/WhereYouGo';
import ThingsTodo from './Components/ThingsToDo/ThingsToDoMain';
import RestaurantList from './Components/SpectacularPlace/SpectacularPlacePage';
import ThingsTodoDetail from './Components/ThingsToDo/ThingsToDo';
import FooterMain from './Components/Footer';
import Allplaces from './Components/Allpalces';
import Search from './Components/search/Search';
import ThingsTodoList from './Components/ThingsToDo/ThingsTodoList';
import Map from './Components/Maps/FullMap';
import profile from './Components/Profile';
import AddComment from './Components/card/AddComment';
import CardProfiletwo from './Components/card/CardProfiletwo';
import resSearch from './Components/Restaurant/resturantSearch/ResturantSearch';
import Campain from './Components/campain/campain';
import error404Web from './assets/error/404errorWeb.png';

// mobile view import
import MediaQuery from 'react-responsive';
import NavbarMobileView from './mobileComponents/navbar/MobileMainNavbar';
import FooterMobileView from './mobileComponents/footer/FooterMain';
import WhereYouGoMobileView from './mobileComponents/WhereYouGo';
import ResSearchMV from './mobileComponents/ResturantSearch/RootPageResturantPage';
import AttarctionMV from './mobileComponents/Attraction/thingsToDoSearch';
import thingsToDoList from './mobileComponents/placeList/thingsToDoList/thingsToDoList';
import resturantListMobileView from './mobileComponents/placeList/ResturantList/ResturantList';
import thingToDoMobileView from './mobileComponents/placeDetails/thingsToDo/thingsToDo';
import resturantMobileView from './mobileComponents/placeDetails/resturant/Resturant';
import AllplacesMobileView from './mobileComponents/allPlaces/Allplaces';
import CampainMobil from './mobileComponents/campain/campain';
import GoogleTagManager from './GoogleTagManager';
import MapPage from './mobileComponents/MapPage/ModalMap';

import campainMobil from "./Components/campain/compation-mobile"
import campain from "./Components/campain/compation"

const errorWeb = () => {
	return <img style={{ width: '67%', marginLeft: '16%' }} src={error404Web} />;
};
const error = () => {
	return <h1>error</h1>;
};

class App extends Component {
	constructor(props) {
		super(props);
		this.state = {
			content: 'content'
		};
	}
	render() {
		return (
			<Provider store={store}>
				<div className="App">
					{/* <GoogleTagManager gtmId="GTM-TZ6WBR6" /> */}
					<BrowserRouter>
						<div>
							<MediaQuery query="(max-device-width: 1224px)">
								<div>
									{window.location.pathname !== '/justro98' && window.location.pathname !== '/justro98/campain' ? (
										<NavbarMobileView />
									) : null}

									<Switch>
										<Route path="/"component={WhereYouGoMobileView} exact />
										<Route path="/justro98/campain" component={campainMobil} exact/>
										<Route path="/ResturantSearch" component={ResSearchMV} exact />
										<Route path="/Attraction" component={AttarctionMV} exact />
										<Route path="/ThingsTodoList" component={thingsToDoList} exact />
										<Route path="/RestaurantList" component={resturantListMobileView} exact />
										<Route path="/things-to-do" component={thingToDoMobileView} exact />
										<Route path="/restaurant" component={resturantMobileView} exact />
										<Route path="/AllPlaces" component={AllplacesMobileView} exact />
										<Route path="/Map" component={MapPage} exact />
										<Route path="/justro98" component={CampainMobil} exact />
										<Route component={error} />
									</Switch>
									{window.location.pathname !== '/justro98' && window.location.pathname !== '/justro98/campain'  ? <FooterMobileView /> : null}
								</div>
							</MediaQuery>
							<MediaQuery query="(min-device-width: 1224px)">
								<div>
									{window.location.pathname !== '/justro98' && window.location.pathname !== '/justro98/campain' ? <MainNavbar /> : null}
									<Switch>
										<Route path="/justro98/campain" component={campain} exact/>
										<Route path="/hotels" component={home} exact />
										{/* <Route path="/home" component={App} exact /> in error midahad chon to khode app.js hast */}
										<Route path="/flight" component={Flight} exact />
										<Route path="/Top" component={TopCategorizedList} exact />
										<Route path="/restaurant" component={RestaurantDetail} exact />
										<Route path="/" component={WhereYouGo} exact />
										<Route path="/Attraction" component={ThingsTodo} exact />
										<Route path="/resturantSearch" component={resSearch} exact />
										<Route path="/RestaurantList" component={RestaurantList} exact />
										<Route path="/ThingsTodoList" component={ThingsTodoList} exact />
										<Route path="/things-to-do" component={ThingsTodoDetail} exact /> 
										<Route path="/Allplaces" component={Allplaces} exact />
										<Route path="/Search" component={Search} exact />
										<Route path="/Map" component={Map} exact />
										<Route path="/profile" component={profile} exact />
										<Route path="/profilePage" component={CardProfiletwo} exact />
										<Route path="/AddComment" component={AddComment} exact />
										<Route path="/justro98" component={Campain} exact />
										<Route component={errorWeb} />
									</Switch>
									{window.location.pathname !== '/justro98'  && window.location.pathname !== '/justro98/campain' ? <FooterMain /> : null}
								</div>
							</MediaQuery>
						</div>
					</BrowserRouter>
				</div>
			</Provider>
		);
	}
}

export default App;
