import React, { Component } from "react";
import "pretty-checkbox/src/pretty-checkbox.scss";

export default class AddToFavorite extends Component {
  constructor(props) {
    super(props);
    this.state = {
      like: this.props.like
    };
  }

  render() {
    return (
      <div>
        {this.state.like ? (
          <div class="pretty p-icon p-toggle p-round p-pulse">
            <input type="checkbox" />

            <div class="state p-off">
              <i class="fas fa-heart like" />
            </div>

            <div class="state p-on p-info-o">
              <i class="far fa-heart unlike" />
            </div>
          </div>
        ) : (
          <div class="pretty p-icon p-toggle p-round p-pulse">
            <input type="checkbox" />

            <div class="state p-on">
              <i class="fas fa-heart like" />
            </div>

            <div class="state p-off p-info-o">
              <i class="far fa-heart unlike" />
            </div>
          </div>
        )}
      </div>
    );
  }
}
