import { ROOT_SUGGESTION } from './types';
import axios from 'axios';

export const suggestionRoot = (req) => (dispatch) => {
	axios.get(`/api/new-config/main/mainPage?${req[0]}${req[1]}${req[2]}${req[3]}`)
	.then(function(res) {
		dispatch({
			type: ROOT_SUGGESTION,
			payload: res.data
		});
	});
};
