import React, { Component } from "react";
import Button from "antd/lib/button";
import "antd/lib/button/style/css";
import { Row, Col, Input, Form, Icon } from "antd";
// import markerImg from "../assets/pin@3x.png";
import calImg from "../assets/calendar@3x.png";
import { DatePicker } from "antd";
import { LocaleProvider } from "antd";
import fa_IR from "antd/lib/locale-provider/fa_IR";
import "moment/locale/fa";
import moment from "antd-jalali-moment";
import Passengers from "./passengers";
import markerImg from "../assets/pin@3x.png";
import { func } from "prop-types";

moment.locale("fa");

const { RangePicker } = DatePicker;
let today = new Date();

function disabledDate(current) {
  // Can not select days before today and today
  return current && current < moment().add(-1, "days");
}

export default class SearchSection extends Component {
  constructor(props) {
    super(props);
    this.hotel = "deafult text ...";

    this.state = {
      city: "",
      checkin: "",
      checkout: "",
      startValue: moment(),
      endValue: null,
      searchAllEnabled: false,
      showPassenger: true,
      passengers: {
        roomsCount: 1,
        adult: 2,
        childrenNo: 0,
        rooms: [
          {
            adult: 2,
            childrenNo: 0,
            children: []
          }
        ]
      }
    };
    this.handleChange = this.handleChange.bind(this);
    this.onChangeDate = this.onChangeDate.bind(this);
    this.onChangeTraveller = this.onChangeTraveller.bind(this);
  }

  componentDidMount() {
    this.props.updateTraveller(this.state.passengers);
  }

  onChangeTraveller = travellers => {
    this.setState({ ["passengers"]: travellers });
    this.props.updateTraveller(travellers);
  };

  handleChange = e => {
    let inputName = e.target.name;
    this.setState({ [inputName]: e.target.value });
  };
  onChangeDate(date, dateString) {
    this.state.startValue = dateString[0];
    this.state.endValue = dateString[1];
  }

  passengerHandleClick(e) {
    e.preventDefault();
    document.getElementsByClassName("mainPassenger")[0].style.display = "block";
    document.getElementById("passengersCom").style.display = "block";
    alert(document.getElementsByClassName("mainPassenger")[0].style.display);
  }

  showPassenger() {
    if (
      document.getElementsByClassName("mainPassenger")[0].style.display ===
      "block"
    ) {
      // this.setState({['showPassenger']:false});
      document.getElementsByClassName("mainPassenger")[0].style.display =
        "none";
    } else {
      document.getElementsByClassName("mainPassenger")[0].style.display =
        "block";
      // this.setState({['showPassenger']:true});
    }

    // if(this.state.showPassenger===false){

    //   this.setState({['showPassenger']:true})
    // }
    // else{

    //   this.setState({['showPassenger']:false})
    // }
  }

  render() {
    const dateFormat = "YYYY/MM/DD";
    return (
      <div className="searchBarContainer">
        <div className="gutter-example searchBar">
          <Row gutter={24}>
            <Col
              className="gutter-row sepBorder searchElementContainer "
              span={4}
            >
              <div className=" searchElement borderRadiusLeft">
                <Button
                  className=" minHeight70 searchButton borderRadiusLeft"
                  type="primary"
                >
                  جستجو
                </Button>
              </div>
            </Col>
            <Col
              onClick={this.showPassenger.bind(this)}
              className="gutter-row sepBorder searchElementContainer"
              span={6}
            >
              <div className="gutter-box searchElement passengersTopView">
                {this.state.passengers.roomsCount}
                &nbsp; اتاق &nbsp;
                {this.state.passengers.adult + this.state.passengers.childrenNo}
                &nbsp; مهمان &nbsp;
              </div>
            </Col>

            <Col
              className="gutter-row sepBorder mt-1 searchElementContainer"
              span={7}
              style={"margin-top:1%"}
            >
              <div className="gutter-box searchElement pt-0">
                <LocaleProvider locale={fa_IR}>
                  {/* <img className="markerIcon" src={calImg} /> */}
                  <RangePicker
                    onChange={this.onChangeDate}
                    disabledDate={disabledDate}
                    defaultValue={[this.state.startValue, moment()]}
                    format={dateFormat}
                    style={"font-family : 'BYekan'"}
                    ranges={{
                      فردا: [
                        moment(new Date().setHours(24)),
                        moment(new Date().setHours(48))
                      ],
                      "آخر  این هفته ": [
                        moment(new Date(moment().endOf("week")).setHours(-48)),
                        moment().endOf("week")
                      ],
                      "این ماه": [moment(), moment().endOf("month")]
                    }}
                    dateRender={current => {
                      const style = {};
                      if (current.day() === 5 && current.isAfter(moment())) {
                        style.border = "1px solid #00a680";
                        style.borderRadius = "50%";
                        style.color = "#000";
                        style.fontWeight = "400";
                        style.paddingTop = "2px";
                      }
                      return (
                        <div className="ant-calendar-date" style={style}>
                          {current.date()}
                        </div>
                      );
                    }}
                  />
                </LocaleProvider>
              </div>
            </Col>
            <Col
              className="gutter-row sepBorde searchElementContainerr pdr-2"
              span={7}
            >
              <div className=" gutter-box searchElement pt-0">
                <Input
                  className="borderRadiusRight minHeight70 searchInput"
                  suffix={
                    <Icon
                      type="environment"
                      style={{ color: "rgba(0,0,0,.25)" }}
                    />
                  }
                  name="city"
                  placeholder=" مقصد مورد نظر شما "
                  value={this.state.city}
                  onChange={this.handleChange}
                />
              </div>
            </Col>
          </Row>
        </div>

        {/* { this.state.showPassenger ? <Passengers visibility={true}/> :null} */}
        <Passengers update={this.onChangeTraveller.bind(this)} />
      </div>
    );
  }
}
