import React, { Component } from 'react';
import { Row, Col, Breadcrumb } from 'antd';
import 'antd/dist/antd.css';
import './../../App.css';
import 'antd/dist/antd.css';

import BasicDetails from './BasicDetails';
import Images from './Images';
import ReviewStats from './ReviewStats';

import MiniMap from '../Maps/MiniMap';
import NearbyRestaurant from '../Nearby/Nearby';

// import Suggestion from '../suggestions';
// import BestNearby from './BestNearby';
// import NearbyHotels from '../Nearby/NearbyHotels';

import ThingsToDoInfo from './ThingsToDoInfo';
import SmallMap from '../Maps/smallMap';
import Reviews from '../Review/Reviews';
import DetailDescription from './DetailDescription';
// import TripSuggestion from './TripSuggestion';
// import Ways from './ways';
// import Weather from './Weather';
import axios from 'axios';
import NearbyThin from '../Nearby/NearbyThings';
import Similar from '../card/SimilarNearBy';
import defaultThings from '../../assets/DefaultThingsToDo.jpeg';

// redux code
import { connect } from 'react-redux';
import { fetchPostsThingsTodoDetail } from '../../actions/postActions';

let counter = 0;
var URLparam = '';

class ThingsToDo extends Component {
	constructor() {
		super();
		this.state = {
			comment: [],
			city: '',
			nearbyResForMap: [],
			nearbyThinForMap: []
		};
		this.searchAllEnabled = false;
	}

	// componentWillMount() {
	//   this.props.fetchPostsThingsTodoDetail();
	// }

	componentWillMount() {
		var url_string = window.location.href;
		var url = new URL(url_string);
		URLparam = url.searchParams.get('uid');

		if (URLparam !== null && counter < 1) {
			this.props.fetchPostsThingsTodoDetail(URLparam);
		} else {
			this.props.fetchPostsThingsTodoDetail(this.state.city);
		}
	}

	// ********************************************************************************************************************
	componentDidMount() {
		axios
			.get(`/api/review/gis-vector-object/${URLparam}?start=0&len=50`)
			.then((res) => {
				this.setState({ comment: res.data });
			})
			.catch(function(error) {
				console.log(error);
			})
			.then(function() {});
	}

	render() {
		return (
			<div className="marginLayout" style={{ marginBottom: '50px' }}>
				{'uid' in this.props.posts.items ? (
					<div>
						<div className="BreadcrumbThingsToDo">
							<Breadcrumb separator=">">
								<Breadcrumb.Item>
									<span className="shabnam">{this.props.posts.items.province}</span>
								</Breadcrumb.Item>
								<Breadcrumb.Item>
									<a
										href={`./?cityName=${this.props.posts.items.city}&layer_uid=${this.props.posts
											.items.cityUid}`}
									>
										{this.props.posts.items.city}
									</a>
								</Breadcrumb.Item>
								<Breadcrumb.Item>
									<span className="shabnam">مکان های دیدنی {this.props.posts.items.city}</span>
								</Breadcrumb.Item>
							</Breadcrumb>
						</div>

						<BasicDetails data={this.props.posts.items} />

						{'images' in this.props.posts.items ? <Images data={this.props.posts.items} /> : null}

						<Row>
							<Row className="tar mat-2" style={{ fontSize: '20px' }}>
								<b> بررسی اجمالی</b>
							</Row>
							<Col span={6}>
								<SmallMap data={this.props.posts.items.point} />
								{/* <Weather /> */}
							</Col>
							<Col span={10}>
								<ThingsToDoInfo data={this.props.posts.items} />
							</Col>
							<Col span={8}>
								<ReviewStats comment={this.state.comment} />
							</Col>
						</Row>

						{/* <Row className="headerSection" style={{ marginTop: '30px', marginBottom: '20px' }} span={24}>
							<BestNearby data={this.props.posts.items} />
						</Row> */}

						{'similarPlaces' in this.props.posts.items ? (
							<Row
								className="headerSection"
								style={{ marginTop: '30px', marginBottom: '20px' }}
								span={24}
							>
								<Similar
									data={this.props.posts.items.similarPlaces}
									uidAndCity={this.props.posts.items}
								/>
							</Row>
						) : null}

						{/* ********************************************************************************** */}
						{this.props.posts.items.description.length > 399 ? (
							<Row>
								<DetailDescription data={this.props.posts.items} />
							</Row>
						) : null}

						{/* ********************************************************************************** */}
						<Row>{/* <TripSuggestion /> */}</Row>
						{/* ********************************************************************************** */}

						{'nearbies' in this.props.posts.items ? (
							<Row>
								{'Things-To-Do' in this.props.posts.items.nearbies ||
								'Restaurant' in this.props.posts.items.nearbies ? (
									<Row style={{ marginTop: '22px', marginBottom: '22px', height: '40vh' }}>
										<Row>
											<span className=" sectionTitle flr MiniMapTitle ">نقاط اطراف</span>
											<a
												className="seeAll "
												href={`./Map?lng=${this.props.posts.items.point
													.coordinates[0]}&lat=${this.props.posts.items.point
													.coordinates[1]}&type=${this.props.posts.items.type}&city=${this
													.props.posts.items.city}`}
											>
												<span
													style={{
														width: '10px',
														backgroundColor: '#00a680',
														padding: '10px',
														borderRadius: '5px',
														color: 'white'
													}}
												>
													مشاهده در نقشه
												</span>
											</a>
										</Row>
										<MiniMap location={this.props.posts} />
									</Row>
								) : null}
							</Row>
						) : null}

						{/* ********************************************************************************** */}
						{'nearbies' in this.props.posts.items ? (
							<div>
								{'Restaurant' in this.props.posts.items.nearbies ? (
									<Row className="headerSection">
										<Col>
											<NearbyRestaurant
												data={this.props.posts.items.nearbies.Restaurant}
												cityAndUid={this.props.posts.items}
											/>
										</Col>
									</Row>
								) : null}
							</div>
						) : null}

						{/* ********************************************************************************** */}

						{'nearbies' in this.props.posts.items ? (
							<div>
								{'Things-To-Do' in this.props.posts.items.nearbies ? (
									<Row className="headerSection">
										<Col>
											<NearbyThin
												data={this.props.posts.items.nearbies['Things-To-Do']}
												cityAndUid={this.props.posts.items}
											/>
										</Col>
									</Row>
								) : null}
							</div>
						) : null}

						{/* ********************************************************************************** */}

						{/* <Row className="headerSection">
							<NearbyHotels data={hotels} />
						</Row> */}

						{/* comment*************************************************************************** */}
						<Reviews data={this.state.comment} />
						{/* ********************************************************************************** */}
					</div>
				) : (
					<div style={{ height: '80vh', padding: '11% 0px 0px 0px' }}>
						<div className="spinner">
							<div className="dot1" />
							<div className="dot2" />
						</div>
					</div>
				)}
			</div>
		);
	}
}

{
	/* <Row>
<Ways />
</Row> */
}

// redux code
const mapStateToProps = (state) => ({
	posts: state.posts
});
// redux code

export default connect(mapStateToProps, { fetchPostsThingsTodoDetail })(ThingsToDo); // redux code
