import React, { Component } from 'react';
import { Pagination, Tabs, Skeleton } from 'antd';
import img from '../assets/profile.png';
import Card from './card/CardProfile';
import profileDefault from '../assets/profileDefault.jpeg';

// redux code
import { connect } from 'react-redux';
import { otherProfile, commentOtherUser } from '../actions/postActions';

var url_string = window.location.href;
var url = new URL(url_string);
let URLparam = url.searchParams.get('Useruid');

const TabPane = Tabs.TabPane;

class Profile extends Component {
	state = {
		current: 1,
		currentPagination: 1,
		tab: 2,
		start: 0,
		lenght: 10
	};

	// onchange Pagination
	onChangePagination = (page) => {
		this.setState({
			currentPagination: page,
			start: (page - 1) * 10,
			lenght: page * 10
		});
	};

	// *****************************
	componentDidMount() {
		this.props.otherProfile(URLparam);
		this.props.commentOtherUser(URLparam);
	}

	onChange = (page) => {
		this.setState({
			current: page
		});
	};

	callback = (key) => {
		this.setState({
			tab: key,
			start: 0,
			lenght: 10
		});
	};
	render() {
		return (
			<div>
				{this.props.posts.loaded == true ? (
					<div>
						<div style={{ height: '190px' }}>
							<img src={img} style={{ height: '190px', width: '100%', position: 'absolute' }} />

							{this.props.posts.items.avatarHash !== null ? (
								<div>
									<div
										className="pictureOtherProfile"
										style={{
											backgroundImage: `URL(/files/justro.png?mode=view&fcode=${this.props.posts
												.items.avatarHash})`
										}}
									/>
									<div className="nameOtherUserPage shabnam">{this.props.posts.items.name}</div>
								</div>
							) : (
								<div>
									<div
										className="pictureOtherProfile"
										style={{ backgroundImage: `URL(${profileDefault})` }}
									/>
									<div className="nameOtherUserPage shabnam">{this.props.posts.items.name}</div>
								</div>
							)}
						</div>

						<div style={{ height: 'auto' }}>
							<div className="TabsInPro">
								<Tabs defaultActiveKey="2" onChange={this.callback} style={{ width: '100%' }}>
									{/* <TabPane tab="هتل و سوئیت" key="1" /> */}
									<TabPane tab="مکان های دیدنی" key="1" />
									<TabPane tab="رستوران" key="2" />
								</Tabs>
							</div>
						</div>

						{this.props.posts.tags !== undefined && this.state.tab == 2 ? (
							<div style={{ marginLeft: '22%', marginRight: '22%' }}>
								{this.props.posts.tags.restaurant
									.slice(this.state.start, this.state.lenght)
									.map((data) => <Card data={data} />)}
							</div>
						) : null}
						{this.props.posts.tags !== undefined ? null : (
							<div style={{ margin: '0 22% 10% 22%' }}>
								{[ 1, 2, 3, 4 ].map((data) => (
									<div className="modifiSkeletonProfile">
										<Skeleton active avatar paragraph={{ rows: 4 }} />
									</div>
								))}
							</div>
						)}

						{/* modifiSkeletonSearch */}

						{this.props.posts.tags !== undefined && this.state.tab == 1 ? (
							<div style={{ marginLeft: '22%', marginRight: '22%' }}>
								{this.props.posts.tags.thingToDo
									.slice(this.state.start, this.state.lenght)
									.map((data) => <Card data={data} />)}
							</div>
						) : null}

						{this.props.posts.tags !== undefined ? (
							<div>
								{'totalCount' in this.props.posts.tags ? (
									<div className="PaginationCostomCss">
										<Pagination
											current={this.state.currentPagination}
											onChange={this.onChangePagination}
											total={
												this.state.tab == 1 ? (
													this.props.posts.tags.thingToDo.length
												) : (
													this.props.posts.tags.restaurant.length
												)
											}
											showSizeChanger
										/>
									</div>
								) : (
									<div>{console.log('---------  totalCount not exist ---------')}</div>
								)}
							</div>
						) : null}
					</div>
				) : (
					<div style = {{height: '80vh', padding: '11% 0px 0px 0px'}}>
							<div class="spinner">
								<div class="dot1" />
								<div class="dot2" />
							</div>
						</div>
				)}
			</div>
		);
	}
}
// redux code
const mapStateToProps = (state) => ({
	posts: state.posts
});
// redux code

export default connect(mapStateToProps, { otherProfile, commentOtherUser })(Profile); // redux code
