import React, { Component } from 'react';
import { Row, Col, Icon, Button } from 'antd';
import IconLocal from '../assets/location.png';
import IconSearch from '../assets/search.png';
// redux code
import { connect } from 'react-redux';
import {  TopPlaceSearch } from '../actions/postActions';
import {searchCity} from "../actions/search/searchCity"

// const Search = Input.Search;

class SearchAll extends Component {
	constructor(props) {
		super(props);
		this.state = {
			searchData: '',
			data: '',
			listLength: 7,
			place: {
				cityName: '',
				name: '',
				uid: [ '', '' ]
			},
			hideCityResult: false,
			hidePlaceResult: false
		};
	}

	componentDidMount = () => {
		document.getElementById('SearchInJustroNavSearch').focus();
		let o = document.getElementById('divSearchNavPlace');
		o.style.border = '1px solid #00a680';
	};

	handelSearch = (event) => {
		let o = document.getElementById('divSearchNavBarMaghsad');
		let a = document.getElementById('divSearchNavPlace');

		if (event.target.value.length > 1) {
			if (event.target.name === 'SearchCity') {
				this.props.searchCity(event.target.value);
				a.style.border = '1px solid #e0e0e0';
				o.style.border = '1px solid #00a680';
			} else if (event.target.name === 'SearchPlace') {
				let temp = this.state.place;
				temp.name = event.target.value;
				this.props.TopPlaceSearch(temp);
				a.style.border = '1px solid #00a680';
				o.style.border = '1px solid #e0e0e0';
			}
			if (event.target.name === 'SearchCity') {
				this.setState({
					hideCityResult: true
				});
			}
			if (event.target.name === 'SearchPlace') {
				this.setState({
					hidePlaceResult: true
				});
			}
		} else {
			if (event.target.name === 'SearchCity') {
				let temp = this.state.place;
				temp.cityName = '';
				temp.uid = [ '', '' ];
				this.setState({
					hideCityResult: false,
					place: temp
				});
				a.style.border = '1px solid #e0e0e0';
				o.style.border = '1px solid #00a680';
			}
			if (event.target.name === 'SearchPlace') {
				let temp = this.state.place;
				temp.name = '';

				this.setState({
					hidePlaceResult: false,
					place: temp
				});
				a.style.border = '1px solid #00a680';
				o.style.border = '1px solid #e0e0e0';
			}
		}
	};
	setUid = (event) => {
		let temp = this.state.place;

		temp.cityName = event.name;
		temp.uid[0] = '&cityUid=';
		temp.uid[1] = event.layer_uid;
		this.setState({
			place: temp,
			hideCityResult: false
		});

		let input = document.getElementById('valueInputSearchNavCity');
		input.value = event.name;
		document.getElementById('SearchInJustroNavSearch').focus();

		let o = document.getElementById('divSearchNavBarMaghsad');
		o.style.border = '1px solid #e0e0e0';

		let a = document.getElementById('divSearchNavPlace');
		a.style.border = '1px solid #00a680';
	};
	setPlace = (event) => {
		let temp = this.state.place;
		temp.name = event.title;
		this.setState({
			place: temp
		});
		let input = document.getElementById('SearchInJustroNavSearch');
		input.value = event.title;
	};

	goToSearchPage = () => {
		if (this.state.place.name !== '' || this.state.place.cityName !== '') {
			window.location.href = `./search?CityName=${this.state.place.cityName}&uid=${this.state.place
				.uid[1]}&Search=${this.state.place.name}`;
		}
	};
	borderSelectCity = () => {
		let o = document.getElementById('divSearchNavBarMaghsad');
		let a = document.getElementById('divSearchNavPlace');
		a.style.border = '1px solid #e0e0e0';
		o.style.border = '1px solid #00a680';
	};
	borderSelectPlace = () =>{
		let o = document.getElementById('divSearchNavBarMaghsad');
		let a = document.getElementById('divSearchNavPlace');
		a.style.border = '1px solid #00a680';
		o.style.border = '1px solid #e0e0e0';
	}

	render() {
		return (
			<div>
				<div className="mainSearchAllView searchAllPadding mb10">
					<Icon
						type="close"
						style={{
							color: '#00a680',
							fontSize: '23px',
							float: 'right',
							marginTop: '1%',
							fontWeight: 'bold'
						}}
					/>
					<div className="gutter-example">
						{/* *********************************************** */}
						<Row>
							<Col
								className="gutter-row searchAllFronViewSearchButton searchAllInput"
								span={1}
								offset={2}
							>
								<div className="gutter-box">
									<Button className="loginButton" onClick={this.goToSearchPage}>
										جستجو
									</Button>
								</div>
							</Col>
							<Col
								className="gutter-row searchAllFronViewDestinaion mr-2 searchAllInput"
								span={1}
								offset={1}
							>
								<div id="divSearchNavBarMaghsad" className="inputSearchNavBarMaghsad">
									<img src={IconLocal} style={{ width: '6%' }} />
									<input
										id="valueInputSearchNavCity"
										autoComplete="off"
										placeholder="مقصد مورد نظر شما"
										className="inputSearchNavCity"
										name="SearchCity"
										onChange={this.handelSearch}
										onClick={this.borderSelectCity}
									/>
								</div>

								{this.state.hideCityResult === true ? (
									<Row>
										{this.props.tags !== undefined ? (
											<Row className="handelModal" style={{ marginTop: '20px' }}>
												{'hashCode' in this.props.tags ? null : (
													<Row className="handelModal">
														{this.props.tags.slice(0, 5).map((data) => (
															<Row
																onClick={this.setUid.bind(null, data)}
																className="dropDownSearchCity handelModal"
															>
																<Col
																	span={22}
																	className="shabnam handelModal"
																	style={{
																		textAlign: 'right',
																		paddingRight: '10px',
																		marginBottom: '13px'
																	}}
																>
																	<Row
																		data-tag={data}
																		style={{ fontSize: '14px', direction: 'rtl' }}
																		className="handelModal"
																	>
																		{data.name}
																	</Row>
																	<Row
																		style={{ fontSize: '12px', color: '#757575' }}
																		className="handelModal"
																	>
																		{data.parent} , ایران
																	</Row>
																</Col>

																<Col span={2}>
																	<img
																		src={IconLocal}
																		style={{ width: '55%', marginTop: '10px' }}
																		className="handelModal"
																	/>
																</Col>
															</Row>
														))}
													</Row>
												)}
											</Row>
										) : null}
									</Row>
								) : null}
							</Col>
							{/* *********************************************************** */}
							<Col
								style={{ marginTop: '3.8%' }}
								className="gutter-row searchAllFronView searchAllInput"
								span={12}
							>
								<div id="divSearchNavPlace" className="inpurSearchNavPlace">
									<img src={IconSearch} style={{ width: '3.5%' }} />
									<input
										autoComplete="off"
										id="SearchInJustroNavSearch"
										className="inputSearchplaceNav"
										placeholder="جستجو در جاسترو"
										name="SearchPlace"
										onChange={this.handelSearch}
										onClick={this.borderSelectPlace}
									/>
								</div>

								{this.state.hidePlaceResult === true ? (
									<Row>
										{this.props.list !== undefined ? (
											<Row className="handelModal" style={{ marginTop: '20px' }}>
												{'city' in this.props.list ? (
													<Row onClick={this.setPlace.bind(null, this.props.list.city)}>
														<a
															href={`/?cityName=${this.props.list.city
																.title}&layer_uid=${this.props.list.city.uid}`}
														>
															<Col
																span={22}
																className="shabnam handelModal"
																style={{
																	textAlign: 'right',
																	paddingRight: '10px',
																	marginBottom: '13px'
																}}
															>
																<Row
																	data-tag={this.props.list.city}
																	style={{ fontSize: '14px', direction: "rtl" }}
																	className="handelModal"
																>
																	{this.props.list.city.title}
																</Row>
																<Row
																	style={{ fontSize: '12px', color: '#757575' }}
																	className="handelModal"
																>
																	{this.props.list.city.province} , ایران
																</Row>
															</Col>

															<Col span={2}>
																<img
																	src={IconLocal}
																	style={{ width: '29%', marginTop: '6px' }}
																	className="handelModal"
																/>
															</Col>
														</a>
													</Row>
												) : null}

												{'things-to-do' in this.props.list ? (
													<Row
														onClick={this.setPlace.bind(
															null,
															this.props.list['things-to-do']
														)}
													>
														<a
															href={`/things-to-do?uid=${this.props.list['things-to-do']
																.uid}&city=${this.props.list['things-to-do'].city}`}
														>
															<Col
																span={22}
																className="shabnam handelModal"
																style={{
																	textAlign: 'right',
																	paddingRight: '10px',
																	marginBottom: '13px'
																}}
															>
																<Row
																	data-tag={this.props.list['things-to-do']}
																	style={{ fontSize: '14px', direction: 'rtl' }}
																	className="handelModal"
																>
																	{this.props.list['things-to-do'].title}
																</Row>
																<Row
																	style={{ fontSize: '12px', color: '#757575' }}
																	className="handelModal"
																>
																	{this.props.list['things-to-do'].province} , ایران
																</Row>
															</Col>

															<Col span={2}>
																<img
																	src={IconLocal}
																	style={{ width: '29%', marginTop: '6px' }}
																	className="handelModal"
																/>
															</Col>
														</a>
													</Row>
												) : null}

												{'restaurant' in this.props.list ? (
													<Row onClick={this.setPlace.bind(null, this.props.list.restaurant)}>
														<a
															href={`/restaurant?uid=${this.props.list.restaurant
																.uid}&city=${this.props.list.restaurant.city}`}
														>
															<Col
																span={22}
																className="shabnam handelModal"
																style={{
																	textAlign: 'right',
																	paddingRight: '10px',
																	marginBottom: '13px'
																}}
															>
																<Row
																	data-tag={this.props.list.restaurant}
																	style={{ fontSize: '14px' }}
																	className="handelModal"
																>
																	{this.props.list.restaurant.title}
																</Row>
																<Row
																	style={{ fontSize: '12px', color: '#757575' }}
																	className="handelModal"
																>
																	{this.props.list.restaurant.province} , ایران
																</Row>
															</Col>

															<Col span={2}>
																<img
																	src={IconLocal}
																	style={{ width: '29%', marginTop: '6px' }}
																	className="handelModal"
																/>
															</Col>
														</a>
													</Row>
												) : null}
											</Row>
										) : null}
									</Row>
								) : null}
							</Col>
						</Row>
						{/* ********************************************************** */}
					</div>
				</div>
			</div>
		);
	}
}
// redux code
const mapStateToProps = (state) => ({
	tags: state.posts.city,
	list: state.posts.list
});
// redux code

export default connect(mapStateToProps, { searchCity, TopPlaceSearch })(SearchAll); // redux code
