import React, { Component } from 'react';
import profileDefault from '../assets/profileDefault.jpeg';

export default class ToolbarProfile extends Component {
	ShowModal = () => {
		this.props.ShowModal();
	};
	
	render() {
		return (
			<div className="ProfileDivAbsul">
				{this.props.data.avatarHash === '' || this.props.data.avatarHash === null ? (
					<div
						className="photoEditProfile"
						style={{
							backgroundImage: `URL(${profileDefault})`
						}}
					/>
				) : (
					<div
						style={{
							backgroundImage: `URL(/files/justro.png?mode=view&fcode=${this.props.data.avatarHash})`,
							width: '130px',
							height: '130px',
							backgroundSize: 'cover',
							borderRadius: '50%',
							float: 'right',
							margin: '29px 40px 10px 0px'
						}}
					/>
				)}

				<div className="ContentProfiletwo">
					<div style={{ paddingRight: '30px' }}>
						<div className="ContentProfiletwoTitle shabnam">{this.props.data.name}</div>
						<div className="ContentProfiletwoContent shabnam">{this.props.data.locality}</div>
						<div className="ContentProfiletwoContentsub shabnam">{this.props.data.biography}</div>
						<button className="ContentProfiletwoButtonEdit shabnam" onClick={this.ShowModal} style={{cursor:"pointer"}}>
							ویرایش پروفایل
						</button>
					</div>
				</div>
			</div>
		);
	}
}
