import React, { Component } from 'react';
import DefaultThingsToDo from '../card/DefaultThingsToDo';
import DefaultRes from '../card/DefaultResturant';
import Photo from '../card/PhotoWithOutRadius';
import PhotoRes from '../card/PhotoResWithOutRadius';
import { Row } from 'antd';
import ReactBnbGallery from 'react-bnb-gallery';

export default class Images extends Component {
	constructor() {
		super(...arguments);
		this.state = {
			galleryOpened: false,
			photos: []
		};
		this.toggleGallery = this.toggleGallery.bind(this);
	}
	componentDidMount = () => {
		let o;
		if ('images' in this.props.images) {
			for (let i = 0; i < this.props.images.images.length; i++) {
				if (this.props.images.type === 'things-to-do') {
					o = {
						photo: `/files/justro.png?mode=view&fcode=${this.props.images.images[i]}`,
						caption: '',
						subcaption: '',
						thumbnail: `/files/justro.png?mode=view&fcode=${this.props.images.images[i]}`
					};
				} else {
					o = {
						photo: `/files/justro.png?mode=view&fcode=${this.props.images.images[i]}`,
						caption: '',
						subcaption: '',
						thumbnail: `/files/justro.png?mode=view&fcode=${this.props.images.images[i]}`
					};
				}

				let temp = this.state.photos;
				temp.push(o);
				this.setState({
					photos: temp
				});
			}
		}
	};
	toggleGallery = () => {
		this.setState((prevState) => ({
			galleryOpened: !prevState.galleryOpened
		}));
	};
	render() {
		return (
			<Row>
				{this.props.images.images.length === 0 ? (
					<div style={{ height: '30vh' }}>
						{this.props.images.type === 'things-to-do' ? <DefaultThingsToDo /> : <DefaultRes />}
					</div>
				) : (
					<div style={{ height: '30vh' }} onClick={this.toggleGallery}>
						{this.props.images.type === 'things-to-do' ? (
							<Photo photo={this.props.images.images[0]} />
						) : (
							<PhotoRes photo={this.props.images.images[0]} />
						)}
					</div>
				)}
				<ReactBnbGallery
					show={this.state.galleryOpened}
					photos={this.state.photos}
					onClose={this.toggleGallery}
				/>
			</Row>
		);
	}
}
