import { RESTURANT_SUGGESTION } from './types';

export const placeSuggestion = (place) => (dispatch) => {
	console.log('>>>>>>>>>>>>>>>>>>>>>>>>>>>>', place)
	fetch(`/api/new-config/${place.type}/mainPage${place.uid[0]}${place.uid[1]}`)
		.then((res) => res.json())
		.then((posts) => {
			let temp = [];
			for (let i = 0  ; i < posts.length; i++) {
				if('name' in posts[i] ){
					if (posts[i].items.length === 0) {
						console.log('items is empty : ', posts[i]);
					} else {
						let itemTemp = [];
						for (let j = 0; j < posts[i].items.length; j++) {
							let o = {
								address: posts[i].items[j].address,
								city: posts[i].items[j].city,
								description: posts[i].items[j].description,
								images: 'images' in posts[i].items[j] ? posts[i].items[j].images : ['11'],
								point: posts[i].items[j].point,
								province: posts[i].items[j].province,
								rate: posts[i].items[j].rate,
								tags: posts[i].items[j].tags,
								telephoneNum: posts[i].items[j].telephoneNum,
								title: posts[i].items[j].title,
								type: posts[i].items[j].type,
								uid: posts[i].items[j].uid,
								website: posts[i].items[j].website
							};
	
							if ('images' in posts[i].items[j] !== true) {
								console.log('images is not found', posts[i].items[j]);
							}
							itemTemp.push(o);
							
						}
						temp.push(posts[i]);
						temp.items = itemTemp;
					}
				}
			}
			dispatch({
				type: RESTURANT_SUGGESTION,
				payload: temp
			});
		});
};
