import React, { Component } from 'react';
import { Row, Col } from 'antd';
import { NavLink } from 'react-router-dom';
import 'antd/dist/antd.css';
import '../App.css';

// redux code
import { connect } from 'react-redux';
import { fetchPostsRestaurantDetail, fetchPostsThingsTodoDetail } from '../actions/postActions';

var url_string = window.location.href;
var url = new URL(url_string);
let city = url.searchParams.get('city');
let uid = url.searchParams.get('uid');

class NavBar extends Component {
	constructor(props) {
		super(props);

		this.selectedItem = 0;
		this.handleClick = (e) => {
			e.preventDefault();
			// document
			//   .querySelectorAll(".headerTitleSelected")[0]
			//   .classList.add("headerTitleUnSelected");
			// document
			//   .querySelectorAll(".headerTitleSelected")[0]
			//   .classList.remove("headerTitleSelected");
			// e.target.classList.remove("headerTitleUnSelected");
			// e.target.classList.add("headerTitleSelected");
		};
	}
	getLocation = () => {
		if (navigator.geolocation) {
			navigator.geolocation.getCurrentPosition(this.showPosition);
		} else {
			alert('این مورد در مرورگر شما پشتیبانی نمی شود');
		}
	};

	showPosition = (position) => {
		window.location.href = `./?Latitude=${position.coords.latitude}&Longitude=${position.coords.longitude}`;
	};
	render() {
		let currentRoute = window.location.pathname;
		return (
			<div>
				{currentRoute === '/' ? (
					false
				) : (
					<header className="navigation paddingLayout">
						{this.props.posts.items.length === 0 ? null : (
							<div className="gutter-example">
								<Row gutter={16}>
									<Col span={24}>
										<Col span={8} />
										<Col span={3} />
										{this.props.city == null ? <Col span={3} /> : null}
										{currentRoute === '/ThingsTodoList' || currentRoute === '/RestaurantList' ? (
											<Col span={3} />
										) : null}

										{currentRoute === '/ThingsTodoList' ||
										currentRoute === '/RestaurantList' ? null : (
											<Col span={3} className=" gutter-row headerTitleUnSelected ">
												<div className="gutter-box cool-link">
													{/* <span onClick={this.getLocation}>اطراف من</span> */}
												</div>
											</Col>
										)}

										<Col
											span={3}
											className={
												currentRoute === '/things-to-do' ||
												currentRoute === '/ThingsTodoList' ||
												currentRoute === '/Attraction' ? (
													' gutter-row headerTitleSelected  '
												) : (
													' gutter-row headerTitleUnSelected '
												)
											}
										>
											<div
												className={
													currentRoute === '/ThingsTodoList' ||
													currentRoute === '/things-to-do' ||
													currentRoute === '/Attraction' ||
													this.props.type === 'things-to-do' ? (
														'manual gutter-box cool-link'
													) : (
														'gutter-box cool-link'
													)
												}
											>
												{currentRoute === '/' ? (
													<NavLink to="/Attraction">مکان‌های دیدنی</NavLink>
												) : (
													<span>
														{currentRoute === '/ThingsTodoList' ||
														currentRoute === '/AllPlaces' ||
														currentRoute === '/Attraction' ? (
															<span>مکان‌های دیدنی</span>
														) : (
															<span>
																{currentRoute === '/resturantSearch' ||
																currentRoute === '/profile' ? (
																	<a href="./Attraction">مکان‌های دیدنی</a>
																) : (
																	<NavLink
																		target="_blank"
																		to={`/ThingsTodoList?city=${this.props
																			.city}&uid=${this.props.posts.items
																			.cityUid === undefined
																			? uid
																			: this.props.posts.items.cityUid}`}
																	>
																		مکان‌های دیدنی
																	</NavLink>
																)}
															</span>
														)}
													</span>
												)}
											</div>
										</Col>

										<Col
											span={2}
											className={
												currentRoute === '/restaurant' ? (
													' gutter-row headerTitleSelected'
												) : (
													'gutter-row headerTitleUnSelected'
												)
											}
										>
											<div
												className={
													currentRoute === '/restaurant' ||
													this.props.type === 'restaurant' ||
													currentRoute === '/resturantSearch' ||
													currentRoute === '/RestaurantList' ? (
														'manual gutter-box cool-link headerTitleSelected'
													) : (
														'gutter-box cool-link'
													)
												}
											>
												<div>
													{currentRoute === '/RestaurantList' ||
													currentRoute === '/AllPlaces' ||
													currentRoute === '/resturantSearch' ? (
														<span>رستوران</span>
													) : (
														<span>
															{currentRoute === '/Attraction' ||
															currentRoute === '/profile' ? (
																<a href="./resturantSearch">رستوران</a>
															) : (
																<NavLink
																	target="_blank"
																	to={`/RestaurantList?uid=${this.props.posts.items
																		.cityUid === undefined
																		? uid
																		: this.props.posts.items.cityUid}&city=${this
																		.props.city}`}
																>
																	رستوران
																</NavLink>
															)}
														</span>
													)}
												</div>
											</div>
										</Col>

										{this.props.city == null ? null : (
											<a
												target="_blank"
												href={`./?cityName=${this.props.city}&layer_uid=${this.props.posts.items
													.cityUid === undefined
													? uid
													: this.props.posts.items.cityUid}`}
											>
												<Col span={4} className="gutter-row headerTitleUnSelected">
													<div className="gutter-box cool-link">درباره {this.props.city}</div>
												</Col>
											</a>
										)}
									</Col>
								</Row>
							</div>
						)}
					</header>
				)}
			</div>
		);
	}
}

// redux code
const mapStateToProps = (state) => ({
	posts: state.posts
});
// redux code

export default connect(mapStateToProps, { fetchPostsRestaurantDetail, fetchPostsThingsTodoDetail })(NavBar); // redux code
