import React, { Component } from 'react';
import { Col, Row } from 'antd';
import Photo from '../card/Photo';
import PhotoRes from '../card/ResturantPhoto';
import DefRes from '../card/DefaultResturant';
import DefThing from '../card/DefaultThingsToDo';
import Rating from 'react-rating';

export default class CardSuggestionMap extends Component {
	render() {
		return (
			<Col span={24} style={{ width: '300px',  height: '16vh', padding: '1vh' }}>
				<Col span={14} style={{paddingRight: "3%"}}>
					<Row className="tar rtl mCardSuMap">{this.props.data.title}</Row>
					<Row style={{ textAlign: 'right' }}>
						<Rating className="ratingMobileView" type="custom" initialRating={this.props.data.rate} readonly />
					</Row>
					{'tags' in this.props.data ? (
						<Row className="tar rtl mCardSuMap">
							{this.props.data.tags.slice(0, 2).map((data) => <span>{data}</span>)}
						</Row>
					) : null}
					<Row className="tar rtl mCardSuMap">
						{this.props.data.price === 1 ? '$' : null}
						{this.props.data.price === 2 ? '$$' : null}
						{this.props.data.price === 3 ? '$$$' : null}
						{this.props.data.price === 4 ? '$$$$' : null}
						{this.props.data.price === 5 ? '$$$$$' : null}
					</Row>
				</Col>
				<Col span={10}>
					{'images' in this.props.data || this.props.data.images.length > 0 ? (
						<div>
							{this.props.data.type === 'things-to-do' ? (
								<div className="H14vh">
									<Photo photo={this.props.data.images[0]} />
								</div>
							) : (
								<div className="H14vh">
									<PhotoRes photo={this.props.data.images[0]} />
								</div>
							)}
						</div>
					) : (
						<div>
							{this.props.data.type === 'things-to-do' ? (
								<div className="H14vh">
									<DefThing />
								</div>
							) : (
								<div className="H14vh">
									<DefRes />
								</div>
							)}
						</div>
					)}
				</Col>
			</Col>
		);
	}
}
