import React, { Component } from "react";
import "antd/dist/antd.css";
import SearchSection from "../Components/SearchSection";

export default class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      passengers: {
        roomsCount: 1,
        adult: 2,
        childrenNo: 0,
        rooms: [
          {
            adult: 2,
            childrenNo: 0,
            children: []
          }
        ]
      }
    };
    this.updateTraveller = this.updateTraveller.bind(this);
  }

  componentDidMount() {
    this.props.updateHomeTraveller(this.state.passengers);
  }
  
  updateTraveller = travellers => {
    this.setState({ ["passengers"]: travellers });
    this.props.updateHomeTraveller(travellers);
  };

  render() {
    return (
      <div>
        <header className="App-header">
          <SearchSection updateTraveller={this.updateTraveller.bind(this)} />
        </header>
      </div>
    );
  }
}
