import React, { Component } from 'react';
import Images from '../Images';
import Description from '../Description';
import MiniMap from '../MiniMap';
import Comment from '../Comment';
import { Row } from 'antd';
import Suggestion from '../Suggestion';
import SpotsAround from '../SpotsAround';

// redux code
import { connect } from 'react-redux';
import { placeDetail } from '../../../actions/placeDetail';

var url_string = window.location.href;
var url = new URL(url_string);
var uid = url.searchParams.get('uid');

class Resturant extends Component {
	componentDidMount = () => {
		this.props.placeDetail(uid);
	};
	goToMap = () => {
		window.location.href = `/Map?lat=${this.props.detail.point.coordinates[1]}&lng=${this.props.detail.point
			.coordinates[0]}&type=restaurant`;
	};

	render() {
		return (
			<div>
				{'images' in this.props.detail ? (
					<div>
						<Images images={this.props.detail} />
						<Description description={this.props.detail} />
						<Row style={{ height: '17vh' }} onClick={this.goToMap}>
							<MiniMap data={this.props.detail.point} />
						</Row>
						<Comment />
						<Suggestion data={this.props.detail.similarPlaces} />
						<SpotsAround />
					</div>
				) : (
					<div className="alCenter" style={{ height: '100vh' }}>
						<div className="spinner">
							<div className="dot1" />
							<div className="dot2" />
						</div>
					</div>
				)}
			</div>
		);
	}
}
// redux code
const mapStateToProps = (state) => ({
	detail: state.posts.placeDetail
});
// redux code

export default connect(mapStateToProps, { placeDetail })(Resturant); // redux code
