import React, { Component } from 'react';
import { Row, Col } from 'antd';
import DefaultThings from "../card/DefaultThingsToDo";
import DefaultRes from "../card/DefaultThingsToDo";
import PhotoThings from "../card/Photo";
import PhotoRes from "../card/ResturantPhoto";
import Rating from 'react-rating';


export default class CardList extends Component {
	render() {
		return (
			<Row style={{padding: "2% 0px"}} >
				<Col 
        style={{height: "82px" , paddingRight: "8px" }}
        span={16}>
        <Row className = "titleCardPlacelistMobileView">{this.props.data.title}</Row>
        <Row style = {{textAlign: "right"}}>
        <Rating className="ratingMobileView" type="custom" initialRating={2} readonly />

        </Row>
        <Row className="ta-r tagsPlaceListMobileView">
          مذهبی
        </Row>
        <Row className = "ta-r addressPlaceListMobileView">
        {this.props.data.address}
        </Row>
        </Col>

        <Col
        
        span={8}>
        {this.props.data.images.length > 0 ?
        <div style={{height: "82px"}} >
          {this.props.data.type === "things-to-do" ? 
          <PhotoThings photo={this.props.data.images[0]} />
          :
          <PhotoRes photo={this.props.data.images[0]} />
          }
        
        </div>
        
        :
        <DefaultThings />
        }

        
        </Col>
			</Row>
		);
	}
}
