import React, { Component } from 'react';
import { Row, Col } from 'antd';
// redux code
import { connect } from 'react-redux';
import { topCity } from '../../actions/topCitySuggestion';

class FavouriteCity extends Component {
	componentDidMount = () => {
		this.props.topCity();
	};
	render() {
		return (
			<div style={{ padding: '10px 10px' }}>
				<Row className="titleFavouriteCityMobileView">محبوبترین مقصدها بر اساس شهر</Row>
				<Row style={{ textAlign: 'center', padding: '5px' }}>
					{this.props.top.map((data, i) => (
						<Col span={8}>
							<a
								href={`/?cityName=${data.title}&layer_uid=${data.uid}`}
								className="buttonFavouriteCityMobileView"
							>
								{data.title}
							</a>
						</Col>
					))}
				</Row>
			</div>
		);
	}
}

// redux code
const mapStateToProps = (state) => ({
	top: state.posts.topCity
});
// redux code

export default connect(mapStateToProps, { topCity })(FavouriteCity); // redux code
