import React, { Component } from 'react';
import Card from './card/CardTextRate';
import { func } from 'prop-types';
import { Row, Skeleton, Col } from 'antd';

// redux code
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { createPost } from '../actions/postActions';

var url_string = window.location.href;
var url = new URL(url_string);

class Allpalces extends Component {
	constructor(props) {
		super(props);
		this.state = {
			DataGhabli: [],

			forPostToUrl: {
				name: url.searchParams.get('name'),
				visitPage: url.searchParams.get('visitPage'),
				type: url.searchParams.get('type'),
				sortBy: this.mapSortingByFromURL(String(url)),
				filter: this.mapFilteringFromURL(String(url))
			},
			start: 0
		};
	}

	mapFilteringFromURL = (urlValue) => {
		var subStringUrl = urlValue.substring(urlValue.indexOf('filtering'), urlValue.length);
		subStringUrl = decodeURIComponent(subStringUrl);

		let resultObject = this.convertStringQueryToJsonObject(subStringUrl);
		delete resultObject.filteringCount;
		this.convertStringValuesToArray(resultObject);
		console.log("aliiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiii",resultObject)
		return resultObject;
	};

	mapSortingByFromURL = (urlValue) => {
		var subStringUrl = urlValue.substring(urlValue.indexOf('sortByCount'), urlValue.indexOf('filteringCount') - 1);
		subStringUrl = decodeURIComponent(subStringUrl);
		let resultObject = this.convertStringQueryToJsonObject(subStringUrl);
		delete resultObject.sortByCount;
		console.log("aliiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiii2",resultObject)
		return resultObject;
	};

	convertStringValuesToArray = (object) => {
		for (var obj in object) {
			object[obj] = object[obj].split(',');
		}

	};

	convertStringQueryToJsonObject = (queryParamString) => {
		var result = {};
		var urlVariables = queryParamString.split('&');
		for (var i = 0; i < urlVariables.length; i++) {
			var parameterName = urlVariables[i].split('=');
			result[parameterName[0]] = parameterName[1];
		}
		console.log("4444444444444444444", result)
		return result;
	};

	componentWillMount = () => {
		this.props.createPost(this.state.forPostToUrl, this.state.start);
	};

	componentWillReceiveProps(nextProps) {
		if (nextProps.newPost) {
			this.props.posts.unshift(nextProps.newPost);
		}
	}
	
	ShowMoreCard = () => {
		let temp = this.state.start;
		temp = temp + 20;
		this.setState({
			start: temp,
			DataGhabli: this.state.DataGhabli.concat(this.props.posts)
		});
		let o = document.getElementById('place');

		window.scrollTo(0, o.offsetHeight+1500);

		this.props.createPost(this.state.forPostToUrl, temp);
	};
	render() {
		return (
			<div>
				<div className="TopListHeaderTitle">{this.state.forPostToUrl.name}</div>
				<div className="paddingLayout mb10">
					<div className="gutter-example">
						<Row gutter={16}>
							{this.state.DataGhabli.map((place, index) => (
								<Card data={place} style={{ float: 'right' }} />
							))}
							<div id="place"></div>

							{this.props.posts.length > 0 ? (
								<div>
									{this.props.posts
										.slice(0, 20)
										.map((place) => <Card data={place} style={{ float: 'right' }} />)}

									{this.props.posts.length > 19 ? (
										<button className="seeMoreButton" onClick={this.ShowMoreCard}>
											مشاهده بیشتر
										</button>
									) : null}
								</div>
							) : (
								<Row gutter={16}>
									{[ 1, 2, 3, 4 ].map((item) => {
										return (
											<Col className="gutter-row main-front-view modifySkeleton" span={6}>
												<Skeleton
													className="SkeletonAllPlaces"
													avatar
													active
													paragraph={{ rows: 3 }}
												/>
											</Col>
										);
									})}

									{[ 1, 2, 3, 4 ].map((item) => {
										return (
											<Col className="gutter-row main-front-view modifySkeleton" span={6}>
												<Skeleton
													className="SkeletonCardRow"
													active
													avatar
													paragraph={{ rows: 3 }}
												/>
											</Col>
										);
									})}
								</Row>
							)}
						</Row>
					</div>
				</div>
			</div>
		);
	}
}

// redux code
Allpalces.propTypes = {
	fetchPosts: PropTypes.func.isRequired,
	posts: PropTypes.array.isRequired,
	newPost: PropTypes.object
};

const mapStateToProps = (state) => ({
	posts: state.posts.items,
	newPost: state.posts.item
});

Allpalces.PropTypes = {
	createPost: PropTypes.func.isRequired
};
// redux code

export default connect(mapStateToProps, { createPost })(Allpalces); // redux code
