import React, { Component } from "react";
import { Row, Col } from "antd";
import Rating from "react-rating";
// import defaultImage from '../assets/'
import Card from "./card/CardTextRate";

class Suggestion extends Component {
  mapConfig = config => {
    let url = `../AllPlaces?name=${config.name}&visitPage=${config.visitPage}&type=${config.type}&`;

    if (config.sortBy !== undefined) {
      let sortByStringValue = this.mapSortByValues(config.sortBy);

      if (sortByStringValue !== undefined && sortByStringValue !== "") {
        url += sortByStringValue + "&";
      }
    }

    if (config.filter !== undefined) {
      let filteringStringValue = this.mapFilteringValues(config.filter);
      url += filteringStringValue;
    }
  
    return url;
  };

  mapCityConfig = config => {
    var url = `../`;
    if (this.props.data.type === "things-to-do") {
      url += "ThingsTodoList";
    } else {
      url += "RestaurantList";
    }

    url += `?uid=NoUid&city=${config.filter.cityName}&visitPage=${
      config.visitPage
    }&type=${config.type}&`;

    if (config.sortBy !== undefined) {
      let sortByStringValue = this.mapSortByValues(config.sortBy);

      if (sortByStringValue !== undefined && sortByStringValue !== "") {
        url += sortByStringValue + "&";
      }
    }

    if (config.filter !== undefined) {
      let filteringStringValue = this.mapFilteringValues(config.filter);
      url += filteringStringValue;
    }
    
    return url;
  };

  mapSortByValues = sortBy => {
    let sortByStringValue = "";
    let sortByObjectKeys = Object.keys(sortBy);

    if (sortByObjectKeys !== undefined && sortByObjectKeys.length > 0) {
      sortByStringValue = "sortByCount=" + sortByObjectKeys.length + "&";
      for (var key in sortByObjectKeys) {
        sortByStringValue +=
          sortByObjectKeys[key] + "=" + sortBy[sortByObjectKeys[key]] + "&";
      }

      var lastChar = sortByStringValue[sortByStringValue.length - 1];
      if (lastChar !== undefined && lastChar === "&") {
        return sortByStringValue.substring(0, sortByStringValue.length - 1);
      }
    }
    return "";
  };

  mapFilteringValues = filter => {
    let filteringStringValue = "";
    let filteringObjectKeys = Object.keys(filter);

    if (filteringObjectKeys !== undefined && filteringObjectKeys.length > 0) {
      filteringStringValue +=
        "filteringCount=" + filteringObjectKeys.length + "&";

      for (var key in filteringObjectKeys) {
        if (filteringObjectKeys[key] === "cityName") {
          filteringStringValue +=
            "cityName=" + filter[filteringObjectKeys[key]] + "&";
        } else if (filteringObjectKeys[key] === "cityUid") {
          filteringStringValue +=
            "cityUid=" + filter[filteringObjectKeys[key]] + "&";
        } else {
          
          var valueArray = filter[filteringObjectKeys[key]];
          if (valueArray !== undefined && valueArray.length > 0) {
            let valueArrayObjectKeys = Object.keys(valueArray);
            for (var valueArrayKey in valueArrayObjectKeys) {
              filteringStringValue +=
                filteringObjectKeys[key] +
                "=" +
                valueArray[valueArrayObjectKeys[valueArrayKey]] +
                "&";
            }
          }
        }
      }
      var lastChar = filteringStringValue[filteringStringValue.length - 1];
      if (lastChar !== undefined && lastChar === "&") {
        return filteringStringValue.substring(
          0,
          filteringStringValue.length - 1
        );
      }
    }

    return "";
  };

  render() {
    return (
      <div>
        {this.props.data.items.length == 0 ? null : (
          <div className="paddingLayout mb10">
            <div className="gutter-example">
              <Row className="headerSection">
                <Col className="flr sectionTitle ">{this.props.data.name}</Col>
                {this.props.data.items.length > 4 ? (
                  <Col className=" fll seeAll ">
                    {this.props.data.visitPage === 1 ? (
                      <a 
                      style = {{verticalAlign: '-webkit-baseline-middle'}}
                      href={this.mapConfig(this.props.data)}>مشاهده همه</a>
                    ) : (
                      <a 
                      style = {{verticalAlign: 'baseline'}}
                      href={this.mapCityConfig(this.props.data)}>
                        مشاهده همه
                      </a>
                    )}
                  </Col>
                ) : null}
              </Row>

              <Row gutter={16}>
                <Card data={this.props.data.items[0]} />
                <Card data={this.props.data.items[1]} />
                <Card data={this.props.data.items[2]} />
                <Card data={this.props.data.items[3]} />
              </Row>
            </div>
          </div>
        )}
      </div>
    );
  }
}
export default Suggestion;
