import { PLACE_LIST } from './types';

export const placeList = (place) => (dispatch) => {
	fetch(`api/${place.type}/items?layerUid=${place.layerUid}${place.tags}&rateSort=DSC&start=${place.start}&len=10`)
		.then((res) => res.json())
		.then((list) => {
			console.log(list);
			let array = [];
			let go;
			if ('items' in list) {
				for (let i = 0; i < list.items.length; i++) {
					let temp = {
						address: list.items[i].address,
						city: list.items[i].city,
						description: list.items[i].description,
						images: 'images' in list.items[i] ? list.items[i].images : [],
						point: list.items[i].point,
						province: list.items[i].province,
						rate: list.items[i].rate,
						tags: list.items[i].tags,
						telephoneNum: list.items[i].telephoneNum,
						title: list.items[i].title,
						type: list.items[i].type,
						uid: list.items[i].uid,
						website: list.items[i].website
					};
					if ('images' in list.items[i] === false) {
						console.log('images not found : ', list.items[i]);
					}
					array.push(temp);
				}
				go = {
					items: array,
					totalCount: list.totalCount
				};
			} else {
				go = {
					items: array,
					totalCount: 0
				};
			}
			dispatch({
				type: PLACE_LIST,
				payload: go
			});
			
		});
};
