import React, { Component } from 'react';
import { Row, Col } from 'antd';
import Rating from 'react-rating';
// import AddToFavorite from '../AddToFavorite';
import personAvatar from '../../assets/profileDefault.jpeg';
var url_string = window.location.href;
var url = new URL(url_string);
var URLcity = url.searchParams.get('city');

export default class CardComment extends Component {
	render() {
		return (
			<a href={`./profile?Useruid=${this.props.data.user.uid}&city=${URLcity}`} target="_blank">
				<Row>
					{/* <Row className="reviewHeader">مشاهده 15 دیدگاه از 298 دیدگاه</Row> */}
					<Row className="reviewHeader" />
					<Col className=" App flr bg-grey" span={3}>
						{'avatar' in this.props.data.user ? (
							<Col className=" App flr bg-grey" span={24}>
								{this.props.data.user.avatar === null ? (
									<div
										className="avatarImageComment"
										style={{
											background: `URL(${personAvatar})`,

											backgroundSize: 'cover'
										}}
									/>
								) : (
									<div
										className="avatarImageComment"
										style={{
											background: `URL(/files/justro.png?mode=view&fcode=${this.props.data.user
												.avatar})`,

											backgroundSize: 'cover'
										}}
									/>
								)}
							</Col>
						) : (
							<Col className=" App flr bg-grey" span={24}>
								<div
									className="avatarImageComment"
									style={{
										background: `URL(${personAvatar})`,

										backgroundSize: 'cover'
									}}
								/>
							</Col>
						)}

						<div className="UserName">
							{/* <Col className="App flr bg-grey" span={24} > */}
							<span className="App avatarTextName">{this.props.data.user.username}</span>
							{/* </Col> */}
						</div>
					</Col>

					<Col span={21} className="rtl ta-r reviewDetail ">
						<Row>
							<Rating
								style={{ marginRight: '-5px' }}
								className="resultRating marginRating rtl flr mar rating"
								type="custom"
								initialRating={this.props.data.rate}
								readonly
							/>
							<span className="distanceText mr-10px">{this.props.data.creationDate}</span>
						</Row>
						<Row className="reviewTitle">
							{this.props.data.title === '' ? (
								<span style={{ color: '#606060' }}>متنی برای نظر نوشته نشده</span>
							) : (
								<span>{this.props.data.title}</span>
							)}
						</Row>
						<Row>
							<div className="reviewContent">{this.props.data.text}</div>
						</Row>
					</Col>
					<Col span={24}>
						{/* <Button
            className="flr"
            type="primary"
            icon="personAvatar"
            size="large"
          >
            
          </Button> */}
					</Col>
				</Row>
			</a>
		);
	}
}
