import React, { Component } from 'react';
import { Map, GoogleApiWrapper, InfoWindow, Marker } from 'google-maps-react';
import { Row, Dropdown, Checkbox, Col } from 'antd';
import Card from '../card/CardMap';
// import AddToFavorite from '../AddToFavorite';
// import Rating from 'react-rating';
import axios from 'axios';
import CardSuggestion from '../card/CardSuggestionMap';

import hotelMap from '../../assets/hotelMap.png';
import ttdMap from '../../assets/ttdMap.png';
import restaurantMap from '../../assets/restaurantMap.png';
// import close from '../../assets/close.png';
import Layer from '../../assets/icon_also_show_layers.png';

var url_string = window.location.href;
var url = new URL(url_string);
let uid = url.searchParams.get('uid');
let latUrl = url.searchParams.get('lat');
let lngUrl = url.searchParams.get('lng');
let typeUrl = url.searchParams.get('type');

export class ModalMap extends Component {
	constructor(props) {
		super(props);
		this.state = {
			showingInfoWindow: false, //Hides or the shows the infoWindow
			activeMarker: {}, //Shows the active marker upon click
			selectedPlace: {}, //Shows the infoWindow to the selected place upon a marker

			location: {
				lat: '',
				lng: '',
				MapArrayLocation: [],
				loaded: false
			},
			zoom: '',
			type: [ '', '', '' ],
			checkedthi: false,
			checkedRes: false
		};
	}

	onMarkerClick = (props, marker, e) => {
		this.setState({
			selectedPlace: props,
			activeMarker: marker,
			showingInfoWindow: true
		});
	};
	onClose = (props) => {
		if (this.state.showingInfoWindow) {
			this.setState({
				showingInfoWindow: false,
				activeMarker: null
			});
		}
	};

	componentWillMount = () => {
		let temp = this.state.type;
		temp[0] = typeUrl;

		if (typeUrl === 'things-to-do') {
			this.setState({
				checkedthi: true,
				checkedRes: false
			});
		} else {
			this.setState({
				checkedthi: false,
				checkedRes: true
			});
		}
		if (uid === null) {
			axios
				.get(`/api/filter/point?name=&x=${latUrl}&y=${lngUrl}&collection=${typeUrl}`)
				.then((res) => {
					let temp = this.state.location;
					temp.loaded = true;
					temp.lat = res.data[1][0].items[0].point.coordinates[1];
					temp.lng = res.data[1][0].items[0].point.coordinates[0];
					temp.MapArrayLocation = res.data[1][0].items;
					this.setState({
						location: temp,
						zoom: 16
					});
				})
				.catch(function(error) {
					console.log('error catch : ', error);
				})
				.then(function() {});
		} else if (uid !== null) {
			axios
				.get(`/api/${typeUrl}/items?layerUid=${uid}&sort=DSC&start=0&length=10`)
				.then((res) => {
					let temp = this.state.location;
					temp.loaded = true;
					temp.lat = res.data.items[0].point.coordinates[1];
					temp.lng = res.data.items[0].point.coordinates[0];
					temp.MapArrayLocation = res.data.items;

					this.setState({
						location: temp,
						zoom: 16
					});
				})
				.catch(function(error) {
					console.log('error catch : ', error);
				})
				.then(function() {});
		}
	};

	updata = () => {
		axios
			.get(
				`/api/filter/point?name=&x=${this.state.location.lat}&y=${this.state.location.lng}&collection=${this
					.state.type[0]}${this.state.type[1]}${this.state.type[2]}`
			)
			.then((res) => {
				let temp = this.state.location;
				temp.loaded = true;
				temp.lat = res.data[1][0].items[0].point.coordinates[1];
				temp.lng = res.data[1][0].items[0].point.coordinates[0];
				temp.MapArrayLocation = [];
				for (let k = 0; res.data[1].length; k++) {
					for (let i = 0; i < res.data[1][k].items.length; i++) {
						temp.MapArrayLocation.push(res.data[1][k].items[i]);
					}
				}

				this.setState({
					location: temp,
					zoom: 16
				});
			})
			.catch(function(error) {
				console.log('error catch : ', error);
			})
			.then(function() {});
	};

	backToPreviousPage = () => {
		window.history.back();
	};

	centerMoved = (mapProps, map) => {
		let temp = this.state.location;
		temp.lat = map.center.lat();
		temp.lng = map.center.lng();
		this.setState({
			location: temp
		});
		this.updata();
	};
	setMarker = (e) => {
		var ref = this.refs[`Marker${e}`];
		this.onMarkerClick(ref.props, ref.marker, e);
		this.setState({
			showCard: false
		});
	};
	SelectDetails = (e) => {
		if (e.target.checked === true) {
			if (this.state.type[0] === '') {
				let temp = this.state.type;
				temp[0] = e.target.name;
				this.setState({
					type: temp
				});
				this.updata();
			} else {
				let temp = this.state.type;
				temp[1] = '&collection=';
				temp[2] = e.target.name;
				this.setState({
					type: temp
				});
				if (e.target.name === 'things-to-do') {
					this.setState({
						checkedthi: true
					});
				} else {
					this.setState({
						checkedRes: true
					});
				}
				this.updata();
			}
		} else {
			let temp = this.state.type;
			for (let i = 0; i < temp.length; i++) {
				if (temp[i] === e.target.name) {
					if (i === 2) {
						let golabi = this.state.type;
						golabi[1] = '';
						golabi[2] = '';
						this.setState({
							type: golabi
						});
						this.updata();
						if (e.target.name === 'things-to-do') {
							this.setState({
								checkedthi: false
							});
						} else {
							this.setState({
								checkedRes: false
							});
						}
					} else if (i === 0) {
						if (temp[2] !== '') {
							if (temp[2] !== temp[0]) {
								let golabi = this.state.type;
								golabi[1] = '';
								golabi[0] = golabi[2];
								golabi[2] = '';
								this.setState({
									type: golabi
								});

								if (e.target.name === 'things-to-do') {
									this.setState({
										checkedthi: false
									});
								} else {
									this.setState({
										checkedRes: false
									});
								}
								this.updata();
							}
						} else {
						}
					}
				}
			}
		}
	};

	render() {
		const style = {
			width: '100%',
			height: '100vh',
			position: 'releative'
		};
		const hotelIcon = {
			url: hotelMap,
			scaledSize: new this.props.google.maps.Size(31, 31)
		};
		const ttdIcon = {
			url: ttdMap,
			scaledSize: new this.props.google.maps.Size(31, 31)
		};
		const restIcon = {
			url: restaurantMap,
			scaledSize: new this.props.google.maps.Size(31, 31)
		};

		return (
			<div style={{position: "fixed", width: "100vw"}}>
				{this.state.location.MapArrayLocation.length > 0 ? (
					<Row>
						<Dropdown
							overlay={
								<div className="toolBarMapMobileView">
									<Row className="">: نمایش نقاط در نقشه</Row>
									<Row style={{ margin: '3px 0px' }}>
										<span className="">رستوران</span>
										<Checkbox
											checked={this.state.checkedRes}
											name="restaurant"
											style={{ marginLeft: '4px', color: '#757575' }}
											onChange={this.SelectDetails}
										/>
									</Row>
									<Row>
										<span className="">مکان‌های دیدنی</span>
										<Checkbox
											checked={this.state.checkedthi}
											style={{ marginLeft: '4px', color: '#757575' }}
											name="things-to-do"
											onChange={this.SelectDetails}
										/>
									</Row>
								</div>
							}
							trigger={[ 'click' ]}
						>
							<img src={Layer} className="layerMapMobileView" />
						</Dropdown>

						<Map
							onDragend={this.centerMoved}
							className="fullMapPageGoogleMap"
							defaultCenter={{
								lat: this.state.location.lat,
								lng: this.state.location.lng
							}}
							fullscreenControl={false}
							zoomControl={false}
							streetViewControl={false}
							mapTypeControl={false}
							google={this.props.google}
							zoom={14}
							style={style}
							initialCenter={{
								lat: this.state.location.lat,
								lng: this.state.location.lng
							}}
						>
							{this.state.location.MapArrayLocation.map((data, i) => (
								<Marker
									ref={'Marker' + i}
									onClick={this.onMarkerClick}
									title={data.name}
									name={data.title}
									image={data.images}
									rate={data.rate}
									type={data.type}
									tags={data.tags}
									uid={data.uid}
									city={data.city}
									position={{
										lat: data.point.coordinates[1],
										lng: data.point.coordinates[0]
									}}
									icon={
										data.type === 'hotel' ? (
											hotelIcon
										) : data.type === 'restaurant' ? (
											restIcon
										) : (
											ttdIcon
										)
									}
								/>
							))}

							<InfoWindow
								marker={this.state.activeMarker}
								visible={this.state.showingInfoWindow}
								// onClose={this.onClose}
							>
								<Card selectedPlace={this.state.selectedPlace} />
							</InfoWindow>
						</Map>

						<div className="showCardMapPageMobileView juCenter">
							<div className="beforeScrollCardMapPageMv">
								<Row style={{ overflow: 'auto', whiteSpace: 'nowrap', direction: 'rtl' }}>
									<Row
										style={{
											width: `${3 * this.state.location.MapArrayLocation.length}00px`,
											height: '16vh'
										}}
									>
										{this.state.location.MapArrayLocation.map((data, i) => (
											<div onClick={this.setMarker.bind(null, i)}>
												<CardSuggestion key={i} data={data} />
											</div>
										))}
									</Row>
								</Row>
							</div>
						</div>
					</Row>
				) : null}
			</div>
		);
	}
}

export default GoogleApiWrapper({
	apiKey: 'AIzaSyD2oGLvgo2obuWUKJYdJ-W8wGKDFfW_LWk'
})(ModalMap);
