import React, { Component } from 'react';
import Photo from '../card/Photo';
import DefThings from './DefaultThingsToDo';
import DefRes from './DefaultResturant';
import PhotlRes from './ResturantPhoto';
import { Row } from 'antd';
import Rating from 'react-rating';

export default class CardTitelAndRate extends Component {
	render() {
		return (
			<a href={`/${this.props.data.type}?uid=${this.props.data.uid}`}>
				<div>
					<Row>
						{this.props.data.images.length > 0 ? (
							<div style={{ height: '15vh' }}>
								{this.props.data.type === 'restaurant' ? (
									<PhotlRes photo={this.props.data.images[0]} />
								) : (
									<Photo photo={this.props.data.images[0]} />
								)}
							</div>
						) : (
							<div style={{ height: '15vh' }}>
								{this.props.data.type === 'restaurant' ? <DefRes /> : <DefThings />}
							</div>
						)}
					</Row>
					<Row>
						<Row className="tar overFlowHidden bold">{this.props.data.title}</Row>
						<Row style={{ textAlign: 'right' }}>
							<Rating
								className="ratingMobileView"
								type="custom"
								initialRating={this.props.data.rate / 2}
								readonly
							/>
						</Row>
					</Row>
				</div>
			</a>
		);
	}
}
