import React, { Component } from 'react';
import Button from 'antd/lib/button';
import 'antd/lib/button/style/css';
import { Row, Col, Input, Form, Icon } from 'antd';
import LocationIcon from '../../assets/markerIcon.png';

// redux code
import { connect } from 'react-redux';
import { searchCityThingToDo } from '../../actions/postActions';
import thingsToDo from '../../mobileComponents/placeDetails/thingsToDo/thingsToDo';

class ThingsToDoSearch extends Component {
	constructor(props) {
		super(props);
		this.state = {
			city: {
				name: '',
				uid: ''
			},
			disable: true
		};
	}
	handleChange = (e) => {
		let temp = this.state.city;
		temp.name = e.target.value;

		if (e.target.value.length > 1) {
			this.setState({
				city: temp,
				disable: true
			});

			this.props.searchCityThingToDo(e.target.value,this.props.section);
		} else {
			this.setState({
				city: temp,
				disable: false
			});
		}
	};

	selectCity = (Event) => {
		let temp = this.state.city;
		temp.name = Event.name;
		temp.uid = Event.layer_uid;
		this.setState({
			city: temp,
			disable: false
		});
	};
	goToThingsToDoPage = () => {

		// ThingsTodoList
		if (this.state.city.uid !== '') {
			window.location.href = `./${this.props.section}?uid=${this.state.city.uid}&city=${this.state.city
				.name}&visitPage=0`;
		}
	};

	render() {
		const {tags}=this.props;
		let city ,thingsToDo,number;
		if(tags)
		{
			city=tags[1][0].items;
			thingsToDo=tags[1][1].items;
			// "things-to-do"
			number=tags[0].city+tags[0][this.props.section];
		}
		let typeSearch=this.props.section==="things-to-do" ? "جاذبه" : "رستوران"
		return (
			<div className="searchBarContainer">
				<div className="gutter-example searchBar">
					<Row gutter={24}>
						<Col className="gutter-row sepBorder searchElementContainer " span={4}>
							<div className=" searchElement borderRadiusLeft">
								<Button
									onClick={this.goToThingsToDoPage}
									className=" minHeight70 searchButton borderRadiusLeft"
									type="primary"
								>
									جستجو
								</Button>
							</div>
						</Col>

						<Col
							style={{ zIndex: '3', padding: '0px !important' }}
							className="gutter-row sepBorde searchElementContainerr ColSearchPlace "
							span={20}
						>
							<div className=" gutter-box searchElement pt-0">
								<img 
								style = {{marginTop: "1%"}}
								src={LocationIcon} 
								width="2%" />
								<input
								//  
									placeholder={`نام مکان، شهر یا ${typeSearch} مورد نظر شما `} 
									value={this.state.city.name}
									onChange={this.handleChange}
									autoComplete="off"
									name="city"
									style={{
										marginTop: '1.5%',
										width: '93%',
										color: 'black',
										paddingRight: '10px',
										fontSize: '16px',
										border: 'none'
									}}
								/>

								{this.state.disable !== true ? null : (
									<div>
										{this.props.tags === undefined ? null : (
											<div className="divDropDownSearchCity">
												{

												city.map((data) => (
													<Row
	
														className="dropDownSearchCity"
														style={{ marginBottom: '10px' }}
													>
													<a href={`/?cityName=${data.title}&layer_uid=${data.uid}`}>
														<Row style={{ color: 'black', fontSize: '14px' }}>
														
															<b data-tag={data}>{data.title}</b>
														</Row>
														<Row
															style={{
																fontSize: '12px',
																paddingRight: '10px',
																color: '#9e9e9e'
															}}
														>
															{data.province}, ایران
														</Row>
														</a>
													</Row>
												))
												}
												{
													thingsToDo.map((data,index) => {

														if(index+city.length<10)
														return (
														<Row
															className="dropDownSearchCity"
															style={{ marginBottom: '10px' }}
														>
														<a  href={`/things-to-do?uid=${data.uid}&city=${data.city}`}>
															<Row style={{ color: 'black', fontSize: '14px' }}>
															<b data-tag={data}>{data.title}</b>
															</Row>
															<Row
																style={{
																	fontSize: '12px',
																	paddingRight: '10px',
																	color: '#9e9e9e'
																}}
															>
																{data.city}, ایران
															</Row>
															</a>
														</Row>)
												
												})
												}

												{
													number>10 ? (<Row
														onClick={this.selectCity.bind(null, "ShowAll")}
														className="dropDownSearchCity"
														style={{ marginBottom: '10px' }}
													>
														<Row style={{ color: '#00a680', fontSize: '12px' }}>
														<a href ={`/search?CityName=&uid=&Search=${this.state.city.name}`}>
														<b>نمایش بیشتر نتایج برای " {this.state.city.name} "</b>
														</a>
															
														</Row>
											</Row>) :  
											null
												}
												
											{ number===0 && this.props.load? <span style={{color:"black"}}>نتایجی یافت نشد</span> : null}	
											</div>

										)}
									</div>
								)}
							</div>
						</Col>
					</Row>
				</div>
			</div>
		);
	}
}

// redux code
const mapStateToProps = (state) => ({
	tags: state.posts.tags,
	load: state.posts.loaded
});
// redux code

export default connect(mapStateToProps, { searchCityThingToDo })(ThingsToDoSearch); // redux code
