import React, { Component } from 'react';
import { Modal, Button, Input, Menu, Dropdown, Icon, Row, notification } from 'antd';
import circle from '../../assets/RateAddCom.png';
import { timingSafeEqual } from 'crypto';
import axios from 'axios';
import Rating from 'react-rating';
import justroIcon from '../../assets/justro-logo.png';

// redux import
import { connect } from 'react-redux';
import { writeReview } from '../../actions/postActions';

const { TextArea } = Input;

const menu = (
	<Menu>
		<Menu.Item key="0">
			<a href="http://www.alipay.com/">1st menu item</a>
		</Menu.Item>
		<Menu.Item key="1">
			<a href="http://www.taobao.com/">2nd menu item</a>
		</Menu.Item>
		<Menu.Divider />
		<Menu.Item key="3">3rd menu item</Menu.Item>
	</Menu>
);

class AddComment extends Component {
	state = {
		loading: false,
		visible: false,
		review: {
			title: '',
			text: '',
			rate: '',
			visitedDate: 1932462384,
			visitType: ''
		},
		existLogin: 'NOTHING'
	};
	showModal = () => {
		this.setState({
			visible: true
		});
	};

	handleCancel = () => {
		this.setState({ visible: false });
	};
	addMoraje = (e) => {
		let temp = this.state.review;
		temp.visitType = e;
	};
	setRate = (value) => {
		console.log('++++++++++++++++++++++', value);
		let temp = this.state.review;
		temp.rate = value * 2;
		this.setState({
			review: temp
		});
	};
	sendReview = () => {
		var url_string = window.location.href;
		var url = new URL(url_string);
		let URLparam = url.searchParams.get('uid');
		if (this.state.review.rate !== '') {
			let send = {
				body: this.state.review,
				uid: URLparam
			};
			this.setState({ visible: false });

			this.props.writeReview(send);
			alert('نظر ثبت شد و پس از بررسی نمایش داده می شود');
			// window.location.reload();
		}
	};
	onChangeInput = (e) => {
		if (e.target.name === 'title') {
			let temp = this.state.review;
			temp.title = e.target.value;
			this.setState({
				review: temp
			});
		} else if (e.target.name === 'bazkhord') {
			let temp = this.state.review;
			temp.text = e.target.value;
			this.setState({
				review: temp
			});
		}
	};

	componentWillMount = () => {
		axios
			.get(`/api/cu/v1/profile`)
			.then((res) => {
				this.setState({ existLogin: res.data });
			})
			.catch(function(error) {
				// this.setState({ existLogin: 'NOTHING' });
				console.log(error);
			})
			.then(function() {});
	};
	ShowModalLogin = () => {
		let bu = document.getElementById('buttonLogin');
		bu.click();
	};

	notLogin = () => {
		notification.open({
			message: 'برای نوشتن نظر ابتدا وارد شوید',
			description: (
				<Button
					style={{ boxShadow: '0 3px 10px 0 rgba(0, 166, 128, 0.35)' }}
					className="loginButton"
					onClick={this.ShowModalLogin}
				>
					عضویت
				</Button>
			),
			icon: <img src={justroIcon} width="17%" />
		});
	};
	// <Icon type="smile" style={{ color: '#108ee9' }} />
	render() {
		return (
			<div>
				{console.log(this.state.existLogin)}
				{this.state.existLogin === 'NOTHING' ? (
					<Button onClick={this.notLogin} className="loginButton">
						افزودن بازخورد
					</Button>
				) : (
					<div>
						<Button onClick={this.showModal} className="loginButton">
							افزودن بازخورد
						</Button>

						<Modal
							visible={this.props.visible || this.state.visible}
							onOk={this.handleOk}
							width={'500px'}
							top={'10px'}
							className="modalAddComment"
						>
							<Row>
								<div className="Justify titleAddComment shabnam">افزودن بازخورد</div>
							</Row>

							<div>
								<Row>
									<div className="RateInAddComment shabnam">
										{' '}
										<span style={{ color: 'red' }}>* </span>امتیاز شما
									</div>
								</Row>
								<Row style={{ textAlign: 'right' }}>
									<Rating
										type="custom"
										className="ratingAddComment"
										initialRating={this.state.review.rate / 2}
										onChange={this.setRate}
									/>

									{/* <Rate onChange={this.setRate} allowHalf defaultValue={2.5} /> */}

									{/* <div style = {{float : "right"}}>
                  <img src = {circle} style = {{width : "32px", height : "32px", margin: "10px 10px"}}/>
                  <img src = {circle} style = {{width : "32px", height : "32px", margin: "10px 10px"}}/>
                  <img src = {circle} style = {{width : "32px", height : "32px", margin: "10px 10px"}}/>
                  <img src = {circle} style = {{width : "32px", height : "32px", margin: "10px 10px"}}/>
                  <img src = {circle} style = {{width : "32px", height : "32px", margin: "10px 10px"}}/>
              </div> */}
								</Row>
								<Row>
									<div className="refAddcomment shabnam">نوع مراجعه</div>
								</Row>
								<Row>
									<Dropdown
										overlay={
											<div className="tar dir bgWhite shadow">
												<div
													className="addCommentMoraje"
													onClick={this.addMoraje.bind(null, 'تنهایی')}
												>
													تنهایی
												</div>
												<div
													className="addCommentMoraje"
													onClick={this.addMoraje.bind(null, 'کاری')}
												>
													کاری
												</div>
												<div
													className="addCommentMoraje"
													onClick={this.addMoraje.bind(null, 'زوج')}
												>
													زوج
												</div>
												<div
													className="addCommentMoraje"
													onClick={this.addMoraje.bind(null, 'دوستانه')}
												>
													دوستانه
												</div>
												<div
													className="addCommentMoraje"
													onClick={this.addMoraje.bind(null, 'خانواده همراه با کودک')}
												>
													خانواده همراه با کودک
												</div>
												<div
													className="addCommentMoraje"
													onClick={this.addMoraje.bind(null, 'خانواده بدون کودک')}
												>
													خانواده بدون کودک
												</div>
											</div>
										}
										trigger={[ 'click' ]}
										className="dropDownAddComment"
									>
										<a className="SelectAddComment" href="#">
											<Icon style={{ float: 'left', marginTop: '2%' }} type="down" />
											<span className="shabnam color9e9e9e">
												{this.state.review.visitType === '' ? (
													'انتخاب کنید'
												) : (
													this.state.review.visitType
												)}
											</span>
										</a>
									</Dropdown>
								</Row>
								<Row>
									<div className="TitleBazkhord shabnam">عنوان بازخورد</div>
								</Row>
								<Row>
									<input
										autoComplete="off"
										value={this.state.review.title}
										name="title"
										onChange={this.onChangeInput}
										className="inputBazkhord"
									/>
								</Row>
								<Row>
									<div className="sharhBazkhord shabnam">شرح بازخورد</div>
								</Row>
								<TextArea
									value={this.state.review.text}
									name="bazkhord"
									onChange={this.onChangeInput}
									className="TextAreaAddComment"
									rows={4}
								/>
							</div>

							<button onClick={this.sendReview} className="sabtbazkhord shabnam">
								ثبت بازخورد
							</button>
							<button className="enseraf shabnam" onClick={this.handleCancel}>
								انصراف
							</button>
						</Modal>
					</div>
				)}
			</div>
		);
	}
}

const mapStateToProps = (state) => ({
	newPost: state.posts.item
});

// redux code

export default connect(mapStateToProps, { writeReview })(AddComment); // redux code
