import React, { Component } from 'react';
import DefualtRes from './DefaultResturant';
import DefualtThin from './DefaultThingsToDo';
import Photo from './Photo';
import PhotoRes from './ResturantPhoto';
import { Row } from 'antd';
import Rating from 'react-rating';

export default class CardTextRate extends Component {
	render() {
		return (
			<a href = {`/${this.props.data.type}?uid=${this.props.data.uid}`}>
			<div style={{ height: '100%' }}>
				{this.props.data.images.length === 0 ? (
					<div>
						<div style={{ height: '117px', borderRadius: '5px' }}>
							{this.props.data.type === 'things-to-do' ? <DefualtThin /> : <DefualtRes />}
						</div>

						<div style={{ marginTop: '8px' }}>
							<Row className="titleCardtextRateMobileView">{this.props.data.title}</Row>
							<Row style={{textAlign: "right"}}>
								<Rating className="ratingMobileView" type="custom" initialRating={2} readonly />
							</Row>
							<Row className="CiAndProCardtextRateMobileView">
								<span>{this.props.data.city} , </span>
								<span>{this.props.data.province}</span>
							</Row>
						</div>
					</div>
				) : (
					<div>
						<div
							style={{
								height: '117px',
								borderRadius: '5px'
							}}
						>
							{this.props.data.type === 'things-to-do' ? (
								<Photo photo={this.props.data.images[0]} />
							) : (
								<PhotoRes photo={this.props.data.images[0]} />
							)}
						</div>

						<div style={{ marginTop: '8px' }}>
							<Row className="titleCardtextRateMobileView">{this.props.data.title}</Row>
							<Row style={{textAlign: "right"}}>
								<Rating className="ratingMobileView" type="custom" initialRating={2} readonly />
							</Row>
							<Row className="CiAndProCardtextRateMobileView">
								<span>{this.props.data.city} , </span>
								<span>{this.props.data.province}</span>
							</Row>
						</div>
					</div>
				)}
			</div>
			</a>
		);
	}
}
