import React, { Component } from 'react';
import 'antd/dist/antd.css';
import WhereYouGoButton from './WhereYouGoButton';

export default class HeaderWUG extends Component {
	constructor() {
		super();
		this.state = {
			isVisible: false
		};

		this.showMainCitySearch = this.showMainCitySearch.bind(this);
	}

	showMainCitySearch = (e) => {
		this.setState({ isVisible: true });
	};

	// in bayad negah shvad va bebinam chi hast
	componentDidMount() {
		document.addEventListener('click', function(e) {
			try {
				if (document.getElementsByClassName('MainCitySearch')[0] !== 'undefined') {
					if (document.getElementsByClassName('MainCitySearch')[0].contains(e.target)) {
					} else if (
						document.getElementsByClassName('WhereYouGoBar')[0].contains(e.target) ||
						e.target.className === 'WhereYoyGoText'
					) {
						document.getElementsByClassName('MainCitySearch')[0].style.display = 'block';
					} else {
						if (e.target.nodeName !== 'LI') {
							document.getElementsByClassName('MainCitySearch')[0].style.display = 'none';
						}
					}
				}
			} catch (err) {}
		});
	}

	render() {
		return (
			<div>
				{this.props.data.length === undefined ? (
					<div>
						<header className="App-header-main" />

						<div className="WhereYouGoButtonForRootPage">
							<WhereYouGoButton data={{ destination: 'کجا میری ؟', scale: 'scale(0.9)' }} />
						</div>
					</div>
				) : (
					<div>
						{this.props.data.length === 0 ? (
							<div>
								<header className="App-header-main" />

								<div className="WhereYouGoButtonForRootPage">
									<WhereYouGoButton data={{ destination: 'کجا میری ؟', scale: 'scale(0.9)' }} />
								</div>
							</div>
						) : (
							<div>
								{this.props.data[0].name === undefined ? (
									<div>
										<div
											className="nearbyMask grad backgroundKojaRootPageBarayeHameShahrha"
											style={{
												background: `URL(/files/justro.png?mode=view&fcode=${this
													.props.data[0].cityDetail[0].images})`
											}}
										/>

										<div className="WhereYouGoButtonForRootPage">
											<WhereYouGoButton
												data={{
													destination: this.props.data[0].cityDetail[0].title,
													scale: 'scale(0.9)'
												}}
											/>
										</div>
									</div>
								) : (
									<div>
										<header className="App-header-main" />

										<div className="WhereYouGoButtonForRootPage">
											<WhereYouGoButton
												data={{ destination: 'کجا میری ؟', scale: 'scale(0.9)' }}
											/>
										</div>
									</div>
								)}
							</div>
						)}
					</div>
				)}
			</div>
		);
	}
}
