import React, { Component } from 'react';
import { Row, Col } from 'antd';
import Card from '../card/CardNearbyThings';

export default class NearbyThings extends Component {
	render() {
		return (
			<Row className=" ">
				{this.props.data === undefined ? null : (
					<div>
						{this.props.data.length > 0 ? (
							<Row className="headerSection">
								<Col className="flr sectionTitle ">مکان‌های دیدنی اطراف</Col>
								{this.props.data.length > 8 ? (
									<Col className=" fll seeAll ">
										{/* <a  href= {`../Allplaces?name=${this.props.data.name}&visitPage=${this.props.data.visitPage}&type=${this.props.data.type}&sortBy=${JSON.stringify(this.props.data.sortBy)}&filter=${JSON.stringify(this.props.data.filter)}`}> */}
										<a
										href={`../ThingsTodoList?uid=${this.props.cityAndUid.cityUid}&city=${this.props
											.cityAndUid.city}&visitPage=${this.props.cityAndUid.similarPlaces.visitPage}&filter=${JSON.stringify(this.props.cityAndUid.similarPlaces.filter)}`}
									>
										مشاهده همه
										</a>
									</Col>
								) : null}

								<Col span={24} />

								{this.props.data.slice(0,6).map((items) => <Card data={items} />)}
							</Row>
						) : null}
					</div>
				)}
			</Row>
		);
	}
}
