import React, { Component } from 'react';
import { Row, Dropdown } from 'antd';
import Down from '../../../assets/IconDownGray.png';

var star = [ 5, 4, 3, 2, 1 ];

export default class Rating extends Component {
	rateFilter = (rate) => {
		this.props.updateRate(rate);
	};
	render() {
		return (
			<Dropdown
				overlayStyle={{ width: '80%' }}
				overlayClassName="categoryTags"
				overlay={
					<div style={{ width: '100%' }}>
						<div className="divIconDownRatePlaceMobileView">
							{star.map((data) => (
								<Row className="dropDrownCategoryTags dir" onClick={this.rateFilter.bind(null, data)}>
									<Row className='rtl' style={{ margin: '2% 4%' }}> {data} ستاره</Row>
								</Row>
							))}
						</div>
					</div>
				}
				trigger={[ 'click' ]}
			>
				<Row className="divDropDownModalTagsPlaceListMobileView">
					<img src={Down} className="IconDownFillterPlaceMobileView" />
					<span>رتبه بندی :</span>
					<span> {this.props.selectedRate === undefined ? '5' : this.props.selectedRate} ستاره </span>
				</Row>
			</Dropdown>
		);
	}
}
