import React, { Component } from 'react';
import { Col, Row } from 'antd';
import Suggestions from '../card/CardTextRate';

export default class Suggestion extends Component {
	render() {
		return (
			<div style={{ marginTop: '8%' }}>
				<Row>
					<Col span={8} style={{ textAlign: 'left' }}>
						{this.props.data.items.length > 3 ? <a className="seeMoreMobileView">مشاهده همه</a> : null}
					</Col>
					<Col span={16} style={{ textAlign: 'right' }}>
						<span className="titleSuggestionMobileView">{this.props.data.name}</span>
					</Col>
				</Row>
				<Row style={{ overflow: 'auto', whiteSpace: 'nowrap', direction: 'rtl' }}>
					{this.props.data.items.length === 1 ? (
						<Row style={{ width: '140px' }}>
							{this.props.data.items.slice(0, 1).map((data) => (
								<Col span={15} style={{ width: '100%', padding: '10px' }}>
									<Suggestions data={data} />
								</Col>
							))}
						</Row>
					) : (
						<div style={{ height: '100%' }}>
							{this.props.data.items.length === 2 ? (
								<Row style={{ width: '290px' }}>
									{this.props.data.items.slice(0, 4).map((data) => (
										<Col span={15} style={{ width: '50%', padding: '10px' }}>
											<Suggestions data={data} />
										</Col>
									))}
								</Row>
							) : (
								<div style={{ height: '100%' }}>
								{this.props.data.items.length === 3 ? 
									<Row style={{ width: '435px' }}>
										{this.props.data.items.slice(0, 4).map((data) => (
											<Col span={15} style={{ width: '33.333%', padding: '10px' }}>
												<Suggestions data={data} />
											</Col>
										))}
									</Row>
									:
									<Row style={{ width: '580px' }}>
										{this.props.data.items.slice(0, 4).map((data) => (
											<Col span={15} style={{ width: '25%', padding: '10px' }}>
												<Suggestions data={data} />
											</Col>
										))}
									</Row>
								}
								</div>
							)}
						</div>
					)}
				</Row>
			</div>
		);
	}
}
