import React, { Component } from 'react';
import { Row, Col, Tabs } from 'antd';

import InputSearchCity from './InputSearchCity';
import InputSearchPlace from './InputSearchPlace';

const TabPane = Tabs.TabPane;

export default class ToolBarSearch extends Component {
	constructor(props) {
		super(props);
		this.state = {
			InputSearch: {
				text: '',
				city: 'collection=city&',
				thingToDo: 'collection=things-to-do&',
				restaurant: 'collection=restaurant&',
				start: 0,
				selectCity: '',
				layerUid: ''
			},
			dropDwonTopPlace: {
				name: '',
				uid: [ '', '' ]
			},
			loaded: false,
			disable: true,
			disableCity: true,
			dafeAvalInputSearchCity: true,
			displayDropDown: true,
			currentPagination: 1,
			disableModal: true,
			counter: {
				total: '',
				things: '',
				resturant: '',
				city: ''
			}
		};
	}

	onClickButton = () => {
		this.setState({
			displayDropDown: false,
			disableModal: false
		});
		this.props.Button();
	};

	existCity = (object) => {
		let setToState = this.state.InputSearch;
		setToState.selectCity = object.name;
		setToState.layerUid = object.uid[1];

		this.setState({
			InputSearch: setToState,
			dropDwonTopPlace: object,
			disableModal: true
		});
		this.props.NameUid(object);
		this.props.Search(setToState);
	};
	changeHandelCity = () => {
		this.setState({
			disableModal: true
		});
	};

	textForSearch = (text) => {
		let search = this.state.InputSearch;
		search.text = text;
		this.setState({
			InputSearch: search,
			disableModal: true
		});

		this.props.GetText(text);
		this.props.Search(search);
	};

	callback = (key) => {
		switch (key) {
			case '1': {
				let InputSearch = Object.assign({}, this.state.InputSearch); //creating copy of object
				InputSearch.city = 'collection=city&';
				InputSearch.thingToDo = 'collection=things-to-do&';
				InputSearch.restaurant = 'collection=restaurant&';

				this.props.setTags(InputSearch);
				this.setState({ InputSearch });

				break;
			}

			case '2': {
				let InputSearch = Object.assign({}, this.state.InputSearch); //creating copy of object
				InputSearch.city = 'collection=city&';
				InputSearch.thingToDo = 'collection=things-to-do&';
				InputSearch.restaurant = 'collection=restaurant&';

				InputSearch.thingToDo = '';
				InputSearch.restaurant = '';

				this.props.setTags(InputSearch);

				this.setState({ InputSearch });

				break;
			}
			case '3': {
				let InputSearch = Object.assign({}, this.state.InputSearch); //creating copy of object
				InputSearch.city = 'collection=city&';
				InputSearch.thingToDo = 'collection=things-to-do&';
				InputSearch.restaurant = 'collection=restaurant&';

				InputSearch.city = '';
				InputSearch.restaurant = '';

				this.props.setTags(InputSearch);

				this.setState({ InputSearch });

				break;
			}
			case '4': {
				let InputSearch = Object.assign({}, this.state.InputSearch); //creating copy of object
				InputSearch.city = 'collection=city&';
				InputSearch.thingToDo = 'collection=things-to-do&';
				InputSearch.restaurant = 'collection=restaurant&';

				InputSearch.city = '';
				InputSearch.thingToDo = '';

				this.props.setTags(InputSearch);

				this.setState({ InputSearch });

				break;
			}
		}
	};

	render() {
		return (
			<Col span={24} className="toolBarSearch" style={{ marginBottom: '50px' }}>
				<Row style={{ margin: '3% 3% 0 3%' }}>
					<Col span={4}>
						<button onClick={this.onClickButton} className="buttonToolSearch">
							جستجو
						</button>
					</Col>
					{/* input search city **************************************************** */}
					<Col span={8} style={{ marginLeft: '3%', zIndex: '3' }}>
						<InputSearchCity
							GetNameUid={this.existCity}
							disable={this.state.displayDropDown}
							disableModals={this.state.disableModal}
							changeHandelCity={this.changeHandelCity}
						/>
					</Col>
					{/* input search things and resturant **************************************** */}
					<Col span={10} style={{ marginLeft: '3%', zIndex: '3' }}>
						<InputSearchPlace
							insertText={this.textForSearch}
							city={this.state.dropDwonTopPlace}
							disable={this.props.disable}
							disableModals={this.state.disableModal}
						/>
					</Col>
				</Row>

				<div className="shabnam ">
					<Tabs defaultActiveKey="1" onChange={this.callback} className="shabnam">
						<TabPane
							tab={
								this.props.disableSearch === true ? (
									`رستوران (${this.props.searchWithOutCityProps === null
										? 0
										: this.props.searchWithOutCityProps[0].restaurant})`
								) : (
									`رستوران (${this.props.searchWithCityProps === null
										? 0
										: this.props.searchWithCityProps[0].restaurant})`
								)
							}
							key="4"
						/>
						<TabPane
							tab={
								this.props.disableSearch === true ? (
									`مکان های دیدنی (${this.props.searchWithOutCityProps === null
										? 0
										: this.props.searchWithOutCityProps[0]['things-to-do']})`
								) : (
									`مکان های دیدنی (${this.props.searchWithCityProps === null
										? 0
										: this.props.searchWithCityProps[0]['things-to-do']})`
								)
							}
							key="3"
						/>
						<TabPane
							tab={
								this.props.disableSearch === true ? (
									`شهرها (${this.props.searchWithOutCityProps === null
										? 0
										: this.props.searchWithOutCityProps[0].city})`
								) : (
									`شهرها (${this.props.searchWithCityProps === null
										? 0
										: this.props.searchWithCityProps[0].city})`
								)
							}
							key="2"
						/>
						<TabPane
							tab={
								this.props.disableSearch === true ? (
									`همه نتایج (${this.props.searchWithOutCityProps === null
										? 0
										: this.props.searchWithOutCityProps[0].city +
											this.props.searchWithOutCityProps[0].restaurant +
											this.props.searchWithOutCityProps[0]['things-to-do']})`
								) : (
									`همه نتایج (${this.props.searchWithCityProps === null
										? 0
										: this.props.searchWithCityProps[0].city +
											this.props.searchWithCityProps[0].restaurant +
											this.props.searchWithCityProps[0]['things-to-do']})`
								)
							}
							key="1"
						/>
					</Tabs>
				</div>
			</Col>
		);
	}
}
