import React, { Component } from 'react';
import { Row, Col } from 'antd';
import Photo from './Photo';
import PhotoRes from './ResturantPhoto';
import DefRes from './DefaultResturant';
import DefThing from './DefaultThingsToDo';
import Rating from 'react-rating';

export default class CardMap extends Component {
	render() {
		return (
			<div style={{ width: '200px', height: '100px' }}>
				{'name' in this.props.selectedPlace ? (
					<Row>
						<Col span={12} style={{ paddingRight: '5px' }}>
							<Row className="tar shabnam overFlowHidden">{this.props.selectedPlace.name}</Row>
							<Row style={{ textAlign: 'right', marginTop: '7px' }}>
								<Rating className="ratingMobileView" type="custom" initialRating={2} readonly />
							</Row>
							<Row className="tar shabnam overFlowHidden rtl">
								{this.props.selectedPlace.tags.slice(0, 2).map((data) => <span>{data}</span>)}
							</Row>
							{/* <div><span>اطلاعات بیشتر</span> <span>رده قیمت</span> </div> */}
						</Col>
						<Col span={12}>
							<div>
								{'image' in this.props.selectedPlace || this.props.selectedPlace.image.length > 0 ? (
									<div>
										{this.props.selectedPlace.type === 'things-to-do' ? (
											<div style={{ height: '10px' }}>
												<Photo photo={this.props.selectedPlace.image[0]} />
											</div>
										) : (
											<div style={{ height: '100px' }}>
												<PhotoRes photo={this.props.selectedPlace.image[0]} />
											</div>
										)}
									</div>
								) : (
									<div>
										{this.props.selectedPlace.type === 'things-to-do' ? (
											<div style={{ height: '10px' }}>
												<DefThing photo={this.props.selectedPlace.image[0]} />
											</div>
										) : (
											<div style={{ height: '100px' }}>
												<DefRes photo={this.props.selectedPlace.image[0]} />
											</div>
										)}
									</div>
								)}
							</div>
						</Col>
					</Row>
				) : null}
			</div>
		);
	}
}
