import React, { Component } from 'react';
import { Row, Col } from 'antd';
import 'antd/dist/antd.css';
import './../../App.css';
import 'antd/dist/antd.css';
import defultResturant from '../../assets/defultResturant.jpeg';

import ReactBnbGallery from 'react-bnb-gallery';

export default class Images extends Component {
	constructor() {
		super();
		this.searchAllEnabled = false;
		this.state = {
			photoForReactBnbGallery: [],
			showPhotoBnbGallery: 0
		};
	}

	// bara in zade shode react bnbgalary

	toggleGallery = () => {
		this.setState((prevState) => ({
			galleryOpened: !prevState.galleryOpened
		}));
	};
	showPhotoToggleGallery = (e) => {
		this.setState({
			showPhotoBnbGallery: e
		});
		this.toggleGallery();
	};

	componentDidMount = () => {
		this.fillPhoto(this.props.data);
	};

	// fuction por kardan photo bara Image
	fillPhoto = (image) => {
		let counter = 0;

		if (image.length !== 0) {
			const tempImage = [];
			for (counter; counter < image.images.length; counter++) {
				let fillphoto = {
					photo: `/files/justro.png?mode=view&fcode=${image.images[counter]}`,
					caption: ' ',
					subcaption: ' ',
					thumbnail: `/files/justro.png?mode=view&fcode=${image.images[counter]}`
				};
				tempImage.push(fillphoto);
			}
			this.setState({
				photoForReactBnbGallery: tempImage
			});
		}
	};

	render() {
		let Detail = [];
		return (
			<div>
				
						<Row className="restaurantImages">
							{/* male zamani ke ax nadashte bashad */}

							{this.props.data.images.length === 0 ? (
								<div
									className="PictureResturantForZeroPicture"
									style={{ background: `URL(${defultResturant})` }}
								/>
							) : null}

							{/* vaghti ke yek ax dashte bashad */}
							{this.props.data.images.length === 1 ? (
								<div
									data-tag={0}
									onClick={this.showPhotoToggleGallery.bind(null, 0)}
									className="PictureResturantForOnePicture"
								>
									<div
										className="ResrutantDetailOnePicture"
										style={{ background: `URL(${this.props.data.images[0]})` }}
									/>
								</div>
							) : null}

							{/* vaghti ke two ax dashte bashad */}
							{this.props.data.images.length === 2 ? (
								<div>
									<Col
										data-tag={0}
										onClick={this.showPhotoToggleGallery.bind(null, 0)}
										span={9}
										className="restaurantBackgroundImage"
										style={{
											backgroundImage: `url(${this.props.data.images[0]})`
										}}
									/>
									<Col
										data-tag={1}
										onClick={this.showPhotoToggleGallery.bind(null, 1)}
										span={15}
										className="restaurantBackgroundImage maskHeroThree"
										style={{
											backgroundImage: `url(${this.props.data.images[1]})`,
											borderLeft: '5px solid white'
										}}
									/>
								</div>
							) : null}

							{/* vaghti ke two ax dashte bashad */}
							{this.props.data.images.length === 3 ? (
								<div>
									<Col
										data-tag={0}
										onClick={this.showPhotoToggleGallery.bind(null, 0)}
										span={9}
										className="restaurantBackgroundImage  "
										style={{ backgroundImage: `url(${this.props.data.images[0]})` }}
									/>
									<Col
										data-tag={1}
										onClick={this.showPhotoToggleGallery.bind(null, 1)}
										span={15}
										className="restaurantBackgroundImage maskHeroThree"
										style={{ backgroundImage: `url(${this.props.data.images[1]})` }}
									/>
								</div>
							) : null}

							{/* vaghti ke bishtar az charta ax dashte bashad */}
							{this.props.data.images.length >= 4 ? (
								<div>
									<Col span={9} className="restaurantBackgroundImage">
										<Col
											data-tag={3}
											onClick={this.showPhotoToggleGallery.bind(null, 3)}
											span={12}
											className="rightSideRestaurantBackgroundImage "
											style={{ backgroundImage: `url(${this.props.data.images[3]})` }}
										/>
										<Col
											data-tag={2}
											onClick={this.showPhotoToggleGallery.bind(null, 2)}
											span={12}
											className="rightSideRestaurantBackgroundImage "
											style={{ backgroundImage: `url(${this.props.data.images[2]})` }}
										/>
										<Col
											data-tag={0}
											onClick={this.showPhotoToggleGallery.bind(null, 0)}
											span={24}
											className="rightBottomSideRestaurantBackgroundImage "
											style={{ backgroundImage: `url(${this.props.data.images[0]})` }}
										/>
									</Col>
									<Col
										span={15}
										data-tag={1}
										onClick={this.showPhotoToggleGallery.bind(null, 1)}
										className="restaurantBackgroundImage mtImage-2 maskHeroThree"
										style={{ backgroundImage: `url(${this.props.data.images[1]})` }}
									/>
									{this.props.data.images.length > 4 ? (
										<span
											style={{ cursor: 'pointer' }}
											onClick={this.toggleGallery}
											className="morePhotoRestutantList"
										>
											(22) مشاهده تصاویر{' '}
											<img
												height="18px"
												width="18px"
												src="data:image/png;base64,"
											/>
										</span>
									) : null}
								</div>
							) : null}
						</Row>

						{/*  bara in zade shode react bnbgalary */}

						<ReactBnbGallery
							activePhotoIndex={this.state.showPhotoBnbGallery}
							show={this.state.galleryOpened}
							photos={this.state.photoForReactBnbGallery}
							onClose={this.toggleGallery}
						/>
			

				{/*  bara in zade shode react bnbgalary */}
			</div>
		);
	}
}
