import React, { Component } from 'react';
import IconDown from '../../assets/IconDownGray.png';
import IconLocation from '../../assets/location.png';
import { Row, Col, Tabs } from 'antd';

import { connect } from 'react-redux';
import { fetchPostsSearch, searchCity, SearchWithCity, TopPlaceSearch } from '../../actions/postActions';

class InputSearchCity extends Component {
	constructor(props) {
		super(props);
		this.state = {
			city: {
				name: '',
				uid: [ '', '' ]
			},
			disable: false
		};
	}
	componentDidMount = () => {
		var url_string = window.location.href;
		var url = new URL(url_string);

		let cityQuery = this.state.city;
		cityQuery.name = url.searchParams.get('CityName');
		cityQuery.uid[0] = '&cityUid=';
		cityQuery.uid[1] = url.searchParams.get('uid');

		this.setState({
			city: cityQuery
		});
	};

	SelectCitySearch = (Event) => {
		if (Event.target.value.length >= 2 && Event.target.value.length != 0) {
			this.props.searchCity(Event.target.value);
			this.props.changeHandelCity();
			this.setState({
				disable: false
			});
		} else {
			this.props.changeHandelCity();
			this.setState({
				disable: true
			});
		}
	};

	selectCity = (Event) => {
		let temp = this.state.city;
		temp.name = Event.name;
		temp.uid[0] = '&cityUid=';
		temp.uid[1] = Event.layer_uid;
		let inputCity = document.getElementById('realInputSearchCity');
		inputCity.value = '';
		this.props.GetNameUid(temp);
		this.setState({
			city: temp,
			disable: true
		});
	};

	clearSearchCity = () => {
		let temp = this.state.city;
		temp.name = '';

		this.setState({
			city: temp
		});
	};

	render() {
		return (
			<div>
				<div className="inputSearchCity">
					<img src={IconDown} className="ImageInputSearchCity" />
					<input
						autoComplete="off"
						id="realInputSearchCity"
						onClick={this.clearSearchCity}
						placeholder={this.state.city.name === '' ? 'سراسر ایران' : this.state.city.name}
						className="inputSearchAllCity"
						onChange={this.SelectCitySearch}
					/>
					<img src={IconLocation} className="ImageInputSearchCity" />
				</div>
				{/* drop down************************************************************ */}

				{this.props.disableModals === true ? (
					<div>
						{this.state.disable === true ? null : (
							<div>
								{this.props.tags === undefined ? null : (
									<div
										className="divDropDownSearchCity"
										style={{ display: this.state.displayDropDown }}
									>
										<Row className="dropDownSearchCity">
											<Row style={{ marginBottom: '10px', paddingTop: '10px' }}>
												<img
													src={IconLocation}
													className="ImageInputSearchCity"
													style={{ marginLeft: '10px' }}
												/>
												<b>سراسر ایران</b>
											</Row>
										</Row>

										{this.props.tags.slice(0, 5).map((data) => (
											<Row
												onClick={this.selectCity.bind(null, data)}
												className="dropDownSearchCity"
												style={{ marginBottom: '10px' }}
											>
												<Row>
													<b data-tag={data}>{data.name}</b>
												</Row>
												<Row style={{ fontSize: '12px', paddingRight: '10px' }}>
													{data.parent}, ایران
												</Row>
											</Row>
										))}
									</div>
								)}
							</div>
						)}
					</div>
				) : null}
			</div>
		);
	}
}

// redux code
const mapStateToProps = (state) => ({
	posts: state.posts.items,
	loaded: state.posts.loaded,
	tags: state.posts.tags,
	search: state.posts.search,
	list: state.posts.list
});
// redux code

export default connect(mapStateToProps, { searchCity })(InputSearchCity); // redux code
