import React, { Component } from 'react';
import Header from './HeaderWhereYouGo';
import Categories from './CategoriesSectionMobileview';
// import Banner from "./banner/Banner"
import Suggestion from './Suggestions';
import FavouriteCity from './rootPage/FavouriteCity';
import { Skeleton } from 'antd';
import SkeletonRoot from './rootPage/SkeletonRoot';

// redux code
import { connect } from 'react-redux';
import { suggestionRoot } from '../actions/suggestionRootPage';

var url_string = window.location.href;
var url = new URL(url_string);
let layer_uid = url.searchParams.get('layer_uid');
let cityName = url.searchParams.get('cityName');

class WhereYouGo extends Component {
	constructor(props) {
		super(props);
		this.state = {
			destination: 'کجا میری ؟',
			request: [ '', '', '', '' ]
		};
	}

	componentDidMount = () => {
		if (layer_uid === null && cityName === null) {
			this.props.suggestionRoot(this.state.request);
		} else {
			this.setState({
				destination: cityName
			});
			let o = this.state.request;
			o[0] = 'cityName=';
			o[1] = cityName;
			this.props.suggestionRoot(o);
			this.setState({
				request: o
			});
		}
	};
	render() {
		return (
			<div style={{ marginBottom: '20px' }}>
				<Header destination={this.state.destination} image={this.props.suggestion.items[0]} />
				<div className="categoriesMarginMobileview">
					<Categories uid={layer_uid} cityName={cityName} />
				</div>
				{/* <Banner /> */}
				{this.props.suggestion.items.length > 0 ? (
					<div>{this.props.suggestion.items.map((data) => <Suggestion data={data} />)}</div>
				) : (
					<div>{[ 1, 2, 3 ].map((data, index) => <SkeletonRoot />)}</div>
				)}
				<FavouriteCity />
			</div>
		);
	}
}

// redux code
const mapStateToProps = (state) => ({
	suggestion: state.posts
});
// redux code

export default connect(mapStateToProps, { suggestionRoot })(WhereYouGo); // redux code
