import React, { Component } from 'react';
import {Select} from "antd";
import Axios from "axios"
import "../../../node_modules/antd/dist/antd.css";
class ModalRegister extends Component{
   state={
    province:[],
    selectprovince:"",
    visibleProvince:false,
    city:[],
    selectCity:""
   }
    componentWillMount=()=>{
        Axios.get("/api/province/get-name?").then((res)=>{
            this.setState({province:res.data});
        })
    }
    ostan=(name,uid)=>{
        console.log(uid)
        this.setState({selectprovince:name});
        Axios.get(`/api/city/get-city-by-province?uid=${uid}`).then((res)=>{
            this.setState({city:res.data});
        })
    }
    city=(name)=>{
        this.setState({selectCity:name});
    }
    register=()=>{
        if(this.state.selectCity!=="" && this.state.selectprovince!=="")
        alert(this.state.selectCity+"  "+this.state.selectprovince)
    }
    render()
    {
     
          const {province,city}=this.state;
        return (
        <div style={{display:"flex",flexDirection:"column",paddingTop:"5em",paddingBottom:"3em"}}>
        <div className="campain" style={{display:"flex",flexDirection:"row",alignItems:"center",justifyContent:"center"}}>
  <Select
    showSearch
    style={{width:"20vw",minWidth:"200px",direction:"rtl",fontSize:"12px",textAlign:"right"}}
    placeholder="انتخاب کنید"
    notFoundContent="پیدا نشد."
    showArrow={true}
    size="large"
    optionFilterProp="children"
    filterOption={(input, option) => option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
  >
    {
         province.map((value)=>{
           return <option className="hoverColor" value={value.name} style={{textAlign:"right"}} onClick={()=>this.ostan(value.name,value.uid)}>{value.name}</option>
         })
       }
  </Select>
  
  <p style={{textAlign:"right",direction:"rtl",fontSize:"1.5em",marginTop:"1em",width:"20%",fontWeight:800}}>استان:</p>
        </div>
       <div className="campain"  style={{display:"flex",flexDirection:"row",justifyContent:"center",alignItems:"center",marginTop:"1.5em"}}>
  <Select
    showSearch
    style={{width:"20vw",minWidth:"200px",direction:"rtl",fontSize:"12px",textAlign:"right"}}
    placeholder="انتخاب کنید"
    notFoundContent="پیدا نشد."
    showArrow={true}
    size="large"
    optionFilterProp="children"
    filterOption={(input, option) => option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
  >
    {
         city.map((value)=>{
           return <option className="hoverColor" value={value.name} style={{textAlign:"right"}}  onClick={()=>this.city(value.name)}>{value.name}</option>
         })
       }
  </Select>
  <p style={{textAlign:"right",direction:"rtl",fontSize:"1.5em",marginTop:"1em",width:"20%",fontWeight:800}}>شهر :</p>
       </div>
       <div style={{display:"flex",direction:"rowReverse",justifyContent:"center",alignItems:"center"}}>
      
       <div style={{display:"flex",flexDirection:"column",direction:"rtl",width:"20vw",minWidth:"200px"}}>
       <button style={{padding:"0.75em ",width:"50%",minWidth:"100px",marginTop:"1.5em",color:"white",borderRadius:"50px",border:"none",cursor:"pointer"}} className="bgColor-jusrtu " onClick={this.register}>ثبت</button>

           <p style={{fontSize:"2.5em",fontWeight:900,marginTop:"1em",textAlign:"right"}} className="color-jusrtu">پرچم-بالا<span style={{color:"rgb(249, 126, 87)"}}>#</span></p>
       </div>
       <div style={{width:"20%",height:"50px"}}>

</div>
      

       </div>
       
        </div>

        )
    }
}
export {ModalRegister}