import React, { Component } from 'react';
import { Col, Skeleton, Row } from 'antd';

import 'antd/dist/antd.css';
import './../App.css';
import CategoriesSection from './CategoriesSection';
import HeaderWUG from './WhereYouGoHeader';
import Suggestion from './suggestions';
// import InfiniteScroll from 'react-infinite-scroller';

// redux code
import { connect } from 'react-redux';
import { fetchPosts } from '../actions/postActions';
var url_string = window.location.href;
var url = new URL(url_string);
var uid = url.searchParams.get('layer_uid');
var city = url.searchParams.get('cityName');
var Latitude = url.searchParams.get('Latitude');
var Longitude = url.searchParams.get('Longitude');

let counter = 0;

class WhereYouGo extends Component {
	constructor(props) {
		super(props);
		this.state = {
			city: {
				name : ['','','','']
			}
		};
	}

	componentWillMount() {
		url_string = window.location.href;
		url = new URL(url_string);
		let URLparam = url.searchParams.get('cityName');
		

		if (URLparam !== null && counter < 1) {
			let temp = this.state.city;
			temp.name[0]='cityName=';
			temp.name[1]= URLparam;
			this.setState({
				city: temp
			})
			this.props.fetchPosts(temp);
		} else {
			if(Latitude !== null && Longitude !== null ){

				let temi = this.state.city;
				temi.name[0]='x=';
				temi.name[1]= Latitude;
				temi.name[2]='&y=';
				temi.name[3]= Longitude;
				this.props.fetchPosts(temi);
				this.setState({
					city: temi
				})
			}else{
				this.props.fetchPosts(this.state.city);
			}
			
		}
	}

	getSpecificCity = (city) => {
		this.setState({
			city: city
		});
	};

	render() {
		return (
			<div>
				{/* shamel koja miri va ax poshtesh */}
				<HeaderWUG data={this.props.posts} />
				{/* ******************************** */}

				{/* tool bar vasat RootPage */}
				<div className="categoriesMargin">
					<CategoriesSection data={uid} city={city} />
				</div>
				{/* ************************ */}

				{/* <MainPageBanner /> */}
				{/* <FeaturesSection /> */}

				{this.props.posts[0] === undefined ? (
					// for skeleton start *****************************************************************
					<div className="paddingLayout mb10">
						<Row>
							<Col className="gutter-row main-front-view modifySkeletonWhereYouGoHeaderOne" span={6}>
								<Skeleton className="SkeletonAllPlaces" paragraph={{ rows: 1 }} />
							</Col>
							<Col className="gutter-row main-front-view" span={18} />
							<Col className="gutter-row main-front-view" span={18} />
							<Col className="gutter-row main-front-view modifySkeletonWhereYouGoHeaderTwo" span={18}>
								<Skeleton className="SkeletonAllPlaces" paragraph={{ rows: 1 }} />
							</Col>
						</Row>

						<Row gutter={16}>
							{[ 1, 2, 3, 4 ].map((item) => {
								return (
									<Col className="gutter-row main-front-view modifySkeletonWhereYouGo" span={6}>
										<Skeleton className="SkeletonAllPlaces" active avatar paragraph={{ rows: 3 }} />
									</Col>
								);
							})}
						</Row>

						<Row>
							<Col className="gutter-row main-front-view modifySkeletonWhereYouGoHeaderOne" span={6}>
								<Skeleton className="SkeletonAllPlaces" active paragraph={{ rows: 1 }} />
							</Col>
							<Col className="gutter-row main-front-view" span={18} />
							<Col className="gutter-row main-front-view" span={18} />
							<Col className="gutter-row main-front-view modifySkeletonWhereYouGoHeaderTwo" span={18}>
								<Skeleton className="SkeletonAllPlaces" active paragraph={{ rows: 1 }} />
							</Col>
						</Row>

						<Row gutter={16}>
							{[ 1, 2, 3, 4 ].map((item) => {
								return (
									<Col className="gutter-row main-front-view modifySkeletonWhereYouGo" span={6}>
										<Skeleton className="SkeletonCardRow" active avatar paragraph={{ rows: 3 }} />
									</Col>
								);
							})}
						</Row>
					</div>
				) : (
					// for skeleton end *******************************************************************
					// baraye inke dar aray haye mainpage dar item 0 moshkel dasht ************************

					<div>
						{'name' in this.props.posts[0] ? (
							<div>{this.props.posts.map((suggestion) => <Suggestion data={suggestion} />)}</div>
						) : (
							<div>
								{this.props.posts.slice(1, 200).map((suggestion) => <Suggestion data={suggestion} />)}
							</div>
						)}
					</div>
				)}

				{/* <CitySuggestion /> */}
			</div>
		);
	}
}

// redux code
const mapStateToProps = (state) => ({
	posts: state.posts.items
});
// redux code

export default connect(mapStateToProps, { fetchPosts })(WhereYouGo); // redux code
