import React, { Component } from "react";
import { Select, Row, Col, Button } from "antd";

import "antd/dist/antd.css";
import "../App.css";
const Option = Select.Option;
export default class Passengers extends Component {
  constructor(props) {
    super(props);

    this.state = {
      traveller: {
        roomsCount: 1,
        adult: 2,
        childrenNo: 0,
        rooms: [
          {
            adult: 2,
            childrenNo: 0,
            children: [],
            addAdult() {
              this.adult++;
              // context.state.traveller.adult++;
              // this.state.traveller.adult++;
            },
            decreaseAdult: function() {
              if (this.adult > 1) {
                this.adult--;
                //this.state.traveller.adult--;
                // $scope.traveller.decreaseAdult();
              }
            },
            addchildren: function() {
              if (this.childrenNo < 4) {
                this.childrenNo++;
                //this.state.traveller.childrenNo++;
                // $scope.traveller.addchildren();
                //   var newChild = new ChildrenAge();
                this.children.push({ age: 0 });
              }
            },
            decreaseChidlren: function() {
              if (this.childrenNo >= 1) {
                this.childrenNo--;
                //this.state.traveller.childrenNo--;
                //$scope.traveller.decreaseChidlren();
                this.children.pop();
              }
            }
          }
        ],

        addRoom: function() {
          this.rooms.push({
            adult: 1,
            childrenNo: 0,
            children: [],
            addAdult: function() {
              this.adult++;
              this.state.traveller.adult++;
              // $scope.traveller.addAdult();
            },
            decreaseAdult: function() {
              if (this.adult > 1) {
                this.adult--;
                this.state.traveller.adult--;
                // $scope.traveller.decreaseAdult();
              }
            },
            addchildren: function() {
              if (this.childrenNo < 4) {
                this.childrenNo++;
                this.state.traveller.childrenNo++;
                // $scope.traveller.addchildren();
                //   var newChild = new ChildrenAge();
                this.children.push({ age: 0 });
              }
            },
            decreaseChidlren: function() {
              if (this.childrenNo >= 1) {
                this.childrenNo--;
                this.state.traveller.childrenNo--;
                //$scope.traveller.decreaseChidlren();
                this.children.pop();
              }
            }
          });
          this.adult++;
          this.roomsCount++;
        },
        removeRoom: function(index) {
          if (this.rooms.length > 1) {
            this.adult -= this.rooms[index].adult;
            this.roomsCount--;
            this.childrenNo -= this.rooms[index].childrenNo;
            this.rooms.splice(index, 1);
          }
        }
      }
    };

    //this.addAdult=this.addAdult.bind(this)
    this.getPassengerDetail = this.getPassengerDetail.bind(this);

    // this.props.passengersCallBack(this.state.traveller);
  }

  componentDidMount() {
    this.props.update(this.state.traveller);
  }

  onUpdateTravellers = () => {
    this.props.update(this.state.traveller);
  };

  ////////////////
  getPassengerDetail() {
    alert("success......");
  }

  addAdult(room, e) {
    // e.preventDefault();
    room.addAdult();
    this.state.traveller.adult++;
    console.log(this.state.traveller);
    this.setState({ ["traveller"]: this.state.traveller });

    this.onUpdateTravellers();
  }
  decreaseAdult(room, e) {
    // event.preventDefault();
    if (room.adult > 1) {
      room.decreaseAdult();
      this.state.traveller.adult--;
    }

    console.log(this.state.traveller);
    this.setState({ ["traveller"]: this.state.traveller });
    this.onUpdateTravellers();
  }
  addChildren(room, e) {
    // event.preventDefault();
    if (room.childrenNo < 4) {
      room.addchildren();
      this.state.traveller.childrenNo++;
    }

    console.log(this.state.traveller);
    this.setState({ ["traveller"]: this.state.traveller });
    this.onUpdateTravellers();
  }
  decreaseChidlren(room, e) {
    // event.preventDefault();

    if (room.childrenNo > 0) {
      room.decreaseChidlren();
      this.state.traveller.childrenNo--;
    }
    console.log(this.state.traveller);
    this.setState({ ["traveller"]: this.state.traveller });
    this.onUpdateTravellers();
  }

  addRoom(event) {
    // event.preventDefault();

    let blankRoom = {
      adult: 2,
      childrenNo: 0,
      children: [],
      addAdult() {
        this.adult++;
        // context.state.traveller.adult++;
        // this.state.traveller.adult++;
      },
      decreaseAdult: function() {
        if (this.adult > 1) {
          this.adult--;
          //this.state.traveller.adult--;
          // $scope.traveller.decreaseAdult();
        }
      },
      addchildren: function() {
        if (this.childrenNo < 4) {
          this.childrenNo++;
          //this.state.traveller.childrenNo++;
          // $scope.traveller.addchildren();
          //   var newChild = new ChildrenAge();
          this.children.push({ age: 0 });
        }
      },
      decreaseChidlren: function() {
        if (this.childrenNo >= 1) {
          this.childrenNo--;
          //this.state.traveller.childrenNo--;
          //$scope.traveller.decreaseChidlren();
          this.children.pop();
        }
      }
    };

    this.state.traveller.rooms.push(blankRoom);
    this.state.traveller.roomsCount++;
    //this.state.traveller.rooms[0].addchildren();
    this.state.traveller.adult = this.state.traveller.adult + 2;
    this.setState({ ["traveller"]: this.state.traveller });
    console.log(this.state.traveller);
    this.onUpdateTravellers();
  }

  changeAge(child, value) {
    child.age = value;
    this.setState({ ["traveller"]: this.state.traveller });
    console.log(3333, child);
  }
  removeRoom(room, i, v) {
    this.state.traveller.removeRoom(i);
    this.setState({ ["traveller"]: this.state.traveller });
    this.onUpdateTravellers();
  }

  render() {
    return (
      <div>
        <Col
          id="passengersCom"
          span={6}
          offset={6}
          className="bg-white c-black mainPassenger "
        >
          {/*   
        total   adults: {this.state.traveller.adult} */}

          {/* total children: {this.state.traveller.childrenNo} */}

          {this.state.traveller.rooms.map((room, index) => (
            <div className="  passengerContainer">
              {/* {index!== 0 ?<Row className='seperatorDiv'></Row> :null} */}
              <Row className="passengerRow">
                <Col span={24} className="passengerTitles roomHeader">
                  <span className="flr">اتاق {index + 1}</span>
                  {index + 1 !== 1 ? (
                    //   <img  onClick={this.removeRoom.bind(this,room,index)} className='pointerCursor' width='18px' height='auto' src="data:image/png;base64,">
                    //  </img>
                    <span
                      onClick={this.removeRoom.bind(this, room, index)}
                      className=" fll pointerCursor removeRoom"
                    >
                      حذف اتاق
                    </span>
                  ) : null}
                </Col>
              </Row>

              <Row className="passengerRow">
                <Col span={11}>
                  <Col className="childCount" span={8}>
                    {" "}
                    <div
                      className="adjustPaasenger"
                      onClick={this.addAdult.bind(this, room)}
                    >
                      <img
                        width="25px"
                        height="auto"
                        src="data:image/png;base64,"
                      />
                    </div>
                  </Col>
                  <Col className="childCount" span={8}>
                    {" "}
                    <span className="childCount"> {room.adult}</span>
                  </Col>
                  <Col className="childCount" span={8}>
                    {" "}
                    <div
                      className="adjustPaasenger"
                      onClick={this.decreaseAdult.bind(this, room)}
                    >
                      <img
                        width="25px"
                        height="auto"
                        src="data:image/png;base64,"
                      />
                    </div>{" "}
                  </Col>
                </Col>

                <Col span={13} className="passengerTitles">
                  <span dir="rtl" className="rightFloat">
                    بزرگسال :
                  </span>
                </Col>
              </Row>

              <Row className="passengerRow">
                <Col span={11}>
                  <Col className="childCount" span={8}>
                    {" "}
                    <div
                      className=" adjustPaasenger"
                      onClick={this.addChildren.bind(this, room)}
                    >
                      <img
                        width="25px"
                        height="auto"
                        src="data:image/png;base64,"
                      />
                    </div>
                  </Col>
                  <Col className="childCount" span={8}>
                    {" "}
                    <span className="childCount"> {room.childrenNo}</span>{" "}
                  </Col>
                  <Col className="childCount" span={8}>
                    {" "}
                    <div
                      className=" adjustPaasenger"
                      onClick={this.decreaseChidlren.bind(this, room)}
                    >
                      <img
                        width="25px"
                        height="auto"
                        src="data:image/png;base64,"
                      />
                    </div>
                  </Col>
                </Col>

                <Col className="passengerTitles" span={13}>
                  <span dir="rtl" className="rightFloat">
                    کودک :
                  </span>
                </Col>
              </Row>

              {room.children.map((child, index) => (
                <Col className="childAge" span={24}>
                  <Col className="" span={10}>
                    <Select
                      labelInValue
                      defaultValue={{ key: "1" }}
                      onChange={this.changeAge.bind(this, child)}
                    >
                      <Option selected value="0">
                        {" "}
                        0 - 2
                      </Option>
                      <Option value="2"> 2 - 6 </Option>
                      <Option value="6"> 6 - 12</Option>
                    </Select>
                  </Col>

                  <Col span={14}>
                    <span
                      dir="rtl"
                      className="ageTitle passengerTitles rightFloat"
                    >
                      سن کودک {index + 1} :
                    </span>
                  </Col>
                </Col>
              ))}
            </div>
          ))}

          <Row gutter={24} className="passengerButton">
            <Col span={24}>
              <div className="gutter-box">
                <Button
                  onClick={this.addRoom.bind(this)}
                  className="addroombutton"
                >
                  افزودن اتاق
                </Button>
              </div>
            </Col>
          </Row>
        </Col>
      </div>
    );
  }
}
