import React, { Component } from 'react';
import ReactCodeInput from 'react-verification-code-input';

export default class PartTwo extends Component {
	constructor(props) {
		super(props);
		this.state = {
			buttonSent: 'ارسال کد',
			backgroundCol: '#cdcdcd',
			send: {
				j_phoneNumber: '',
				j_otpToken: '',
				_spring_security_remember_me: 'on'
			},
			timer: 59
		};
	}
	componentDidMount = () => {
		let temp = this.state.send;
		temp.j_phoneNumber = this.props.phoneN;
		this.setState({
			send: temp
		});
		this.counter();
	};
	VerificationInput = (code) => {
		let temp = this.state.send;
		temp.j_otpToken = code;
		this.setState({
			send: temp,
			backgroundCol: '#00a680'
		});
	};
	send = () => {
	};
	counter = () => {
		this.setState({
			start: setInterval(() => {
				this.setState({ timer: this.state.timer - 1 });
			}, 1000)
		});
	};
	componentDidUpdate = () => {
		if (this.state.timer === 0) clearInterval(this.state.start);
		return true;
	};
	render() {
		return (
			<div>
				<div className="tac fSize16  margin-top8 shabnam">
					<span className="color616161">کد به شماره </span>
					<span className="colorff8a65">{this.props.phoneN} </span>
					<span className="color616161">ارسال خواهد شد</span>
				</div>
				<div className="tac fSize16  margin-top6 shabnam">
					<span className="color-jusrtu">تغییر شماره تلفن</span>
				</div>
				<div className="tac fSize16  margin-top17 shabnam">
					<span className="color616161">کد دریافتی را وارد کنید</span>
				</div>
				<div className="partTwoLogin">
					<ReactCodeInput
						className="partOneLoginMobileView"
						fields={5}
						type="number"
						value={12345}
						onComplete={this.VerificationInput}
					/>
				</div>
				<div className="tac margin-top7">00:{this.state.timer}</div>
				<div className="juCenter">
					<button
						className="borNone colorWhite fSize19 shabnam sendButtonPartOneLoginMobileView"
						onClick={this.send}
						style={{ backgroundColor: this.state.backgroundCol }}
					>
						{this.state.buttonSent}
					</button>
				</div>
			</div>
		);
	}
}
