import React, { Component } from 'react';
import { Row, Col } from 'antd';
import Rating from 'react-rating';
import AddToFavorite from '../AddToFavorite';
import Default from '../../assets/defultResturant.jpeg';

export default class CardNearby extends Component {
	render() {
		return (
			<a href={`./${this.props.data.type}?uid=${this.props.data.uid}&city=${this.props.data.city}`}>
				<Col span={8} className="gutter-row ResultMain-front-view flr">
					<Col span={15}>
						<div 
						style = {{paddingRight : "7px"}}
						className="main-front-text  nearbyName"> {this.props.data.title}</div>
						<Row gutter={16} className="ratingSection">
							<Col className="gutter-row flr resultPaddingStarRating" span={24}>
								<Rating
						
									className="resultRating rtl rating"
									type="custom"
									initialRating={this.props.data.rate / 2}
									readonly
								/>
								{/* <span className=" main-front-comment flr rtl bazkhord">7650 بازخورد</span> */}
							</Col>
						</Row>

						<Row gutter={16} className="">
							<Col className=" gutter-row main-front-comment rtl tar" span={24}>
								<span className="nearbyName categoryTitle">
									{'tags' in this.props.data ? (
										<span>
											{this.props.data.tags.length > 0 ? (
												<span className="distanceText">{this.props.data.tags[0]}</span>
											) : null}
										</span>
									) : null}
								</span>
							</Col>
						</Row>
					</Col>

					<Col span={9}>
						{'images' in this.props.data ? (
							<div>
								{this.props.data.images.length > 0 ? (
									<div>
										{/* <AddToFavorite className="addToFavMargin restaurantNearby" /> */}
										<div
											className="mask  flr CardNearby"
											style={{
												background: `URL(${this.props.data.images[0]})`
											}}
										/>
									</div>
								) : (
									<div
										className="mask  flr CardNearby"
										style={{
											background: `URL(${Default})`
										}}
									/>
								)}
							</div>
						) : (
							<div
								className="mask  flr CardNearby"
								style={{
									background: `URL(${Default})`
								}}
							/>
						)}
					</Col>
				</Col>
			</a>
		);
	}
}
