import { TOP_CITY } from './types';
import axios from 'axios';

export const topCity = () => (dispatch) => {
	axios
		.get(`http://justro.me/api/city/topCities`)
		.then((res) => {
			dispatch({
				type: TOP_CITY,
				payload: res.data
			});
		})
		.catch(function(error) {
			console.log(error);
		})
		.then(function() {});
};
