import React, { Component } from 'react';
import Search from '../../assets/search.png';
import Location from '../../assets/markerIcon.png';
import { Row } from 'antd';

// redux code
import { connect } from 'react-redux';
import { searchCity } from '../../actions/search/searchCity';

class InputSearch extends Component {
	constructor(props) {
		super(props);
		this.state = {
			city: '',
			displayDropDown: 'none'
		};
	}
	onChange = (e) => {
		if (e.target.value.length > 0) {
			this.setState({
				city: e.target.value,
				displayDropDown: 'block'
			});
		} else {
			this.setState({
				city: e.target.value,
				displayDropDown: 'none'
			});
		}
	};

	goToCity = (city) => {
		window.location.href = `./ThingsTodoList?uid=${city.layer_uid}&city=${city.name}`;
	};

	render() {
		return (
			<div className="inputSearchCityPlaceMobileView">
				<img src={Search} style={{ width: '6.5%', marginLeft: "4%" }} />
				<input
					value={this.state.city}
					onChange={this.onChange}
					placeholder="نام مکان، شهر یا جاذبه مورد نظر شما ... "
					className="realInputSearchCityPlaceMobileView"
					style={{ width: '78%', border: 'none', paddingRight: '3%' }}
				/>
				<img src={Location} style={{ width: '5.5%' }} />
				<div style={{ display: this.state.displayDropDown, zIndex: '1', backgroundColor: "white", marginTop: "6%" }}>
					{this.props.city.slice(0, 6).map((data) => (
						<Row style={{ margin: '3% 0px', paddingRight: '5%' }} onClick={this.goToCity.bind(null, data)}>
							<Row data-tag={data} className="ta-r nameModalSearchCity">
								{data.name}
							</Row>
							<Row className="ta-r parentModalSearchCity">{data.parent} , ایران</Row>
						</Row>
					))}
				</div>
			</div>
		);
	}
}
// redux code
const mapStateToProps = (state) => ({
	city: state.posts.city
});
// redux code

export default connect(mapStateToProps, { searchCity })(InputSearch); // redux code

