import React, { Component } from "react";

import Suggestion from "../suggestions";
import "antd/dist/antd.css";
  
import CitySuggestion from "../citySuggestion";
// import Passengers from "../passengers";
import HeaderFlight from "./HeaderFlight";
import FeaturesSection from "../FeaturesSection";



export default class Flight extends Component {
  constructor() {
    super();
    this.searchAllEnabled = false;
  }


  

  handleOutsideClick(e) {
    try {
      if(document.getElementsByClassName("flightPassenger")[0] !== 'undefined' ){

        if ( document.getElementsByClassName("flightPassenger")[0].contains(e.target)
        ||
        document.getElementsByClassName("flightSearchBarContainer")[0].contains(e.target))   {
         
        }
        else{
          if(  e.target.nodeName!=='LI'){
    document.getElementsByClassName("flightPassenger")[0].style.display='none'
          }
       
        }
      }
  }
  catch(err) {
   
  }
 
   
     
    
  }





  render() {
 
    return (
      <div onClick={this.handleOutsideClick.bind(this)} >
       
       <HeaderFlight/>
       <FeaturesSection/>
        {[].map(suggestion => (
          <Suggestion data={suggestion} />
        ))}
          <CitySuggestion />
        <div class="g-signin2" data-onsuccess="onSignIn" />
      </div>
    );
  }
}
