import React, { Component } from "react";
import { Select,Row, Col, Button } from "antd";




import "antd/dist/antd.css";

import { func } from "prop-types";
const Option=Select.Option;
export default class FlightPassengers extends Component {
  constructor(props) {
    super(props);
    
        this.state={
            traveller : {
                flightMode: {key:"economy",label:"اقتصادی"},
                adult: 2,
                senior:0,
                childrenNo: 0,
                children: [],
                senior:0,
                addSenior:function(){this.senior++;}, 

                decreaseSenior: function() {
                    if (this.senior > 0) {
                      this.senior--;
                      
                    }
                  },
                addAdult:function() {
                    this.adult++;
                
                  },
                  decreaseAdult: function() {
                      
                    if (this.adult > 1) {
                      this.adult--;
                    }
                  },
                  addchildren: function() {
                    if (this.childrenNo < 4) {
                      this.childrenNo++;;
                      this.children.push({age:0});
                    }
                  },
                  decreaseChidlren: function() {
                    if (this.childrenNo >= 1) {
                      this.childrenNo--;
                      this.children.pop();
                    }
                  }
              }
        };

    
  };

onUpdateTravellers=()=>{
  this.props.update(this.state.traveller);

}

  ////////////////

          addAdult(e){
     
            this.state.traveller.addAdult();
           // this.state.traveller.adult++;
            console.log(this.state.traveller)
            this.setState({['traveller']:this.state.traveller});
            
            this.onUpdateTravellers()

          }
          decreaseAdult(e){
             
            // event.preventDefault();
            if(this.state.traveller.adult>1){
              
                this.state.traveller.decreaseAdult();
             // this.state.traveller.adult--;
            console.log(this.state.traveller)
            this.setState({['traveller']:this.state.traveller})
            this.onUpdateTravellers()
          
            }}


          addSenior(e){
     
            this.state.traveller.addSenior();
           // this.state.traveller.senior++;
    
            this.setState({['traveller']:this.state.traveller});
            this.onUpdateTravellers()

          }
          decreaseSenior(e){
            // event.preventDefault();
            if(this.state.traveller.senior>0){
                this.state.traveller.decreaseSenior();
            //  this.state.traveller.senior--;
          
            }

            
            
            console.log(this.state.traveller)
            this.setState({['traveller']:this.state.traveller})
            this.onUpdateTravellers()

        }
          addChildren(e){
            // event.preventDefault();
            if(this.state.traveller.childrenNo<4){
                this.state.traveller.addchildren();
           // this.state.traveller.childrenNo++;
            }

            console.log(this.state.traveller)
            this.setState({['traveller']:this.state.traveller})
            this.onUpdateTravellers()
        }
        decreaseChidlren(e){
     

          if(this.state.traveller.childrenNo>0){
            this.state.traveller.decreaseChidlren();
             // this.state.traveller.childrenNo--;
          }
          console.log(this.state.traveller)
          this.setState({['traveller']:this.state.traveller})
          this.onUpdateTravellers()
        }

        changeAge(child,value){

        child.age=value;
        this.setState({['traveller']:this.state.traveller});
        this.onUpdateTravellers()
        console.log(3333,child);

        }
        changeFlightMode(v){
            let tmp=this.state;
            tmp.traveller.flightMode.key=v.key;
            tmp.traveller.flightMode.label=v.label;

            console.log(" this ev  ::::::  ",v.key)
            this.setState({tmp});
            console.log(" this state  ::::::  ",this.state.traveller);
            this.onUpdateTravellers()
        }
    
     
  render() {
    return (<div>
    
      <Col id='passengersCom' span={6} offset={6} className='bg-white c-black flightPassenger ' >
             
       
       
         <div className='  flightPassengerContainer'>
  <Row>
  <Select className='flightModeSelect' labelInValue defaultValue={{ key: 'economy' }}  onChange={this.changeFlightMode.bind(this)} >
                   <Option selected value='economy'> اکونومی کلاس</Option>
                   <Option value='firstClass'> فرست کلاس</Option>
                   <Option value='businessClass'> بیزینس کلاس</Option>
                 </Select>
  </Row>

                 <Row className='flightPassengerRow' >

                            <Col  span={11}>

                                <Col className='childCount'  span={8}>   <div className="adjustPaasenger" onClick={this.addAdult.bind(this)}><img width='25px' height='auto' src="data:image/png;base64,"></img></div></Col>
                                <Col className='childCount' span={8}>  <span className='childCount' > {this.state.traveller.adult}</span></Col>
                                <Col className='childCount' span={8}>  <div className="adjustPaasenger" onClick={this.decreaseAdult.bind(this)}><img width='25px' height='auto' src="data:image/png;base64,"></img></div> </Col>
                
                            </Col> 
                            <Col  span={13} className='passengerTitles'>
                                <span dir='rtl' className='rightFloat'>
                                بزرگسال :
                                </span> 
                            </Col>    
                
          </Row>

                 <Row className='flightPassengerRow' >

                    <Col  span={11}>

                        <Col className='childCount'  span={8}>   <div className="adjustPaasenger" onClick={this.addSenior.bind(this)}><img width='25px' height='auto' src="data:image/png;base64,"></img></div></Col>
                        <Col className='childCount' span={8}>  <span className='childCount' > {this.state.traveller.senior}</span></Col>
                        <Col className='childCount' span={8}>  <div className="adjustPaasenger" onClick={this.decreaseSenior.bind(this)}><img width='25px' height='auto' src="data:image/png;base64,"></img></div> </Col>

                    </Col> 
                    <Col  span={13} className='passengerTitles'>
                        <span dir='rtl' className='rightFloat'>
                         بزرگسال(+65):
                        </span> 
                    </Col>    

                    </Row>

                 <Row className='flightPassengerRow' >

                  <Col  span={11}>

<Col className='childCount' span={8}>  <div className=" adjustPaasenger" onClick={this.addChildren.bind(this)}><img width='25px' height='auto' src="data:image/png;base64,"></img></div></Col>
<Col className='childCount'  span={8}>     <span className='childCount'> {this.state.traveller.childrenNo}</span> </Col>
<Col className='childCount'  span={8}>      <div className=" adjustPaasenger"  onClick={this.decreaseChidlren.bind(this)}><img width='25px' height='auto' src="data:image/png;base64,"></img></div></Col>
   


    
  </Col>


                      <Col className='passengerTitles'  span={13}>
                         


                                 <span dir='rtl' className='rightFloat'>
                                 کودک :  
                      </span> 

                      </Col>
                      
                </Row>


      

              { this.state.traveller.children.map((child,index)=>
                 <Col className='flightChildAge'  span={24}>

                  <Col  className=''  span={10}>
                
                <Select labelInValue defaultValue={{ key: '0' }}  onChange={this.changeAge.bind(this,child)} >
                   <Option selected value='0'> 0 - 2</Option>
                   <Option value='2'> 2 - 6 </Option>
                   <Option value='6'> 6 - 12</Option>
                 </Select>
                 
                </Col>

             <Col  span={14}>
             


                                 <span dir='rtl' className='ageTitle passengerTitles rightFloat'>
                                سن کودک {index +1} :
                      </span> 


             </Col>

            
           
              
            </Col>)} 



          </div>

     
      </Col>{this.state.traveller.children}
      </div>
    );
  }
}
